<style>
.el-form-search .el-form-item {
  padding-right: 10px;
}
</style>

<script>
import { looseEqual } from '@/src/utils/util';
import { formatters } from '../../../src/utils/formatter.js';
import YuXdialog from '../../xdialog/src/component';
import YuButton from '../../button/src/button';
import Locale from '@/src/mixins/locale';
import { t } from '@/src/locale';

/**
* 业务表单
*/
export default {
  name: 'YuXform',
  xtype: 'YuXform',

  componentName: 'YuXform',

  model: {
    prop: 'model',
    event: 'input'
  },
  mixins: [Locale],
  component: {
    YuXdialog,
    YuButton
  },
  props: {
    hiddenDelVal: Boolean,
    //  高级查询时配置此属性
    moreFields: {
      type: Array,
      default: function() {
        return [];
      }
    },
    relatedTableName: String,
    disabled: Boolean,
    dynamic: Boolean,
    mode: String,
    formType: String,
    model: Object,
    rules: Object,
    labelPosition: String,
    labelWidth: {
      type: String,
      default: '100px'
    },
    hiddenRule: {
      type: Boolean,
      default: false
    },
    labelSuffix: {
      type: String,
      default: '：'
    },
    inline: Boolean,
    beforeQuery: Function,
    showMessage: {
      type: Boolean,
      default: true
    },
    utrace: {
      type: [Boolean, Object],
      default: false
    },
    utraceUsrId: String, // usrId为用户ID，为与后端接口一致，不修改成userId
    utraceOrgId: String,
    utraceMenuId: String,
    utraceTitle: {
      type: String,
      default: '修改痕迹信息'
    },
    uPkValue: String,
    showByMonth: { // 是否开启按月份查询U历史痕迹
      type: Boolean,
      default: false
    },
    showUtitleMessage: { // 是否鼠标滑过小U标记显示title信息
      type: Boolean,
      default: true
    },
    traceServerName: {
      type: String,
      default: ''
    },
    traceGetInterface: {
      type: String,
      default: '/utrace/selectSModifyTrace'
    },
    traceSaveInterface: {
      type: String,
      default: '/utrace/addSModifyTrace'
    },
    traceGetListInterface: {
      type: String,
      default: '/utrace/selectSModifyTraceWithPage'
    },
    ukeyField: String,
    searchIcon: {
      type: String,
      default: 'search'
    },
    resetIcon: {
      type: String,
      default: 'edit'
    },
    removeEmpty: Boolean,
    customSearchFn: Function,
    // 高级查询功能
    expandText: {
      type: String,
      default: function() {
        return t('el.form.expandText');
      }
    },
    collapseText: {
      type: String,
      default: function() {
        return t('el.form.collapseText');
      }
    },
    searchText: {
      type: String,
      default: function() {
        return t('el.form.search');
      }
    },
    resetText: {
      type: String,
      default: function() {
        return t('el.form.reset');
      }
    },
    // 是否默认展开
    expand: {
      type: Boolean,
      default: false
    },
    // 是否开启布局自适应（根据屏幕尺寸改变xform的列数）
    responsive: Boolean
  },
  watch: {
    rules() {
      this.validate();
    },
    utrace(val, oldval) {
      if (val !== oldval) {
        val ? this.loadUTrace() : this.clearUTrace();
      }
    }
  },
  data() {
    return {
      ifInit: true,
      fields: [],
      formdata: this.model,
      notice: false,
      utraceDialogVisible: false,
      utraceBaseParam: {'mPkV': ''}
    };
  },
  created() {
    this.$on('el.form.addField', (field) => {
      if (field) {
        this.fields.push(field);
        if (!this.ifInit) {
          this.$nextTick(function() {
            let name = field.name;
            let type = field.ctype;
            let multiple = field.$parent.$attrs.multiple;
            if (name && name !== 'custom') {
              var aval = type === 'checkbox' || (type === 'select' && multiple || multiple === '') ? [] : '';
              var value = field.$options.parent.$options.propsData.value;
              var val = value ? value : aval;
              val = type === 'num' && val !== '' ? Number(val) : val;
              this.$set(this.formdata, name, val);
            }
          });
        }
      }
    });
    this.$on('el.form.checkbox.change', (field, val) => {
      if (field) {
        this.formdata[field] = val;
      }
    });
    /* istanbul ignore next */
    this.$on('el.form.removeField', (field) => {
      if (field.name) {
        this.fields.splice(this.fields.indexOf(field), 1);
        delete this.formdata[field.name];
      }
    });
    this.$on('el.form.item.blur', (field, value) => {
      if (field) {
        this.formdata[field] = value;
      }
    });
    this.$on('utrace-click', (data, event) => {
      this.utraceDialogVisible = !this.utraceDialogVisible;
      if (this.utraceDialogVisible) {
        this.utraceBaseParam['mFieldId'] = data.name;
        this.$nextTick(function() {
          this.showByMonth && this.$refs.refUTraceSearchForm && this.$refs.refUTraceSearchForm.resetFields(); // 清空小U月份查询
          let refTable = this.$refs.refUTraceListTable;
          var parames = {condition: JSON.stringify(this.utraceBaseParam)};
          if (refTable) {
            refTable.remoteData(parames);
          } else if (this.$slots.utrace) { // 对于自定义表格slot插槽对象处理
            refTable = this.$slots.utrace[0].children[0].componentInstance;
            refTable && refTable.remoteData && refTable.remoteData(parames);
          }
        });
      }
    });
  },
  methods: {
    // 因数组类型数据用克隆赋值的方式监听不到，统一采用extend方法赋值
    setFormData(data) {
      yufp.extend(this.formdata, data);
    },
    utraceClickFn(data, event) {
      this.$emit('utrace-click', data, event);
    },
    traceDialogClose: function() {
      this.utraceDialogVisible = !this.utraceDialogVisible;
    },
    cancel(event) {
      this.$emit('cancel', event);
    },
    save(event) {
      this.$emit('cancel', event);
    },
    resetFields() {
      if (!this.formdata) {
        process.env.NODE_ENV !== 'production' &&
        console.warn('[Element Warn][Form]model is required for resetFields to work.');
        return;
      }
      this.fields.forEach(field => {
        field.resetField();
      });
    },
    validate(callback) {
      if (!this.formdata) {
        console.warn('[Element Warn][Form]model is required for validate to work!');
        return;
      };
      let valid = true;
      let count = 0;
      // let i = 0;
      // 如果需要验证的fields为空，调用验证时立刻返回callback
      if (this.fields.length === 0 && callback) {
        callback(true);
      }
      this.fields.forEach((field, index) => {
        if (!this.hiddenRule && field.$parent && field.$parent.hidden) {
          count++;
          // 当时最后一个的时候也调用回调
          if (typeof callback === 'function' && count === this.fields.length) {
            callback(valid);
          }
        } else {
          field.validate('', errors => {
            if (errors) {
              valid = false;
            }
            if (typeof callback === 'function' && ++count === this.fields.length) {
              callback(valid);
            }
          });
        }
      });
    },
    validateField(prop, cb) {
      let field = this.fields.filter(field => field.name === prop)[0];
      if (!field) { throw new Error('must call validateField with valid name string!'); }

      field.validate('', cb);
    },
    setNoticeClass: function(group) {
      var noticeArr = group.filter(function(item) {
        return !!item.notice;
      });
      this.notice = noticeArr.length > 0 ? 1 : 0;
    },
    // 动态表单含折叠时调用
    // getFormGroup: function() {
    //   var formgroup = [];
    //   if (this.$children.length > 0) {
    //     let children = this.$children[0].$children;
    //     for (var i = 0, l = children.length; i < l; i++) {
    //       var childrenName = children[i].$options.componentName;
    //       while (childrenName !== 'YuXformGroup') {
    //         var child = children[i].$children[0];
    //         childrenName = child.$options.componentName;
    //       }
    //       formgroup.push(child);
    //     };
    //   }
    //   this.setNoticeClass(formgroup);
    //   return formgroup;
    // },
    // 双向绑定表单数据
    setModel: function() {
      var fields = this.fields;
      this.setNoticeClass(fields);
      let data = {};
      for (let i = 0; i < fields.length; i++) {
        let name = fields[i].name;
        let type = fields[i].ctype;
        let multiple = fields[i].$parent.$attrs.multiple;
        let valueType = fields[i].$parent.valueType;
        if (name && name !== 'custom') {
          var aval = (type === 'checkbox' || (type === 'select' && multiple || multiple === '')) && valueType === 'array' ? [] : '';
          var value = fields[i].$options.parent.$options.propsData.value;
          var val = value ? value : aval;
          val = type === 'num' && val !== '' ? Number(val) : val;
          data[name] = val;
        }
      }
      // }
      this.formdata = data;
      this.$emit('input', this.formdata);
      // };
    },
    // 设置小U留痕信息
    _setUTrace: function(h) {
      var _this = this;
      var dialogProps = {
        props: {
          ref: 'refUTraceXFormDialog',
          visible: _this.utraceDialogVisible,
          width: '70%',
          title: _this.utraceTitle || this.t('el.form.uTraceDialogTitle'),
          'before-close': _this.traceDialogClose
        }
      };
      var buttonProps = {
        props: {
          type: 'primary'
        },
        on: {
          click: _this.traceDialogClose
        }
      };
      var elButton = <YuButton {...buttonProps}>{this.t('el.form.uTraceTableDialogBtn')}</YuButton>;
      var buttonList = (<div slot="footer" align="center">{elButton}</div>);
      var yuTable = _this.$slots.utrace || _this._getDefaultUTraceTableColumns(h) ;
      return <YuXdialog {...dialogProps}>{yuTable}{buttonList}</YuXdialog>;
    },
    // 小U按月搜索查询
    uFormSearch: function() {
      let refTable = this.$refs.refUTraceListTable;
      let params = {};
      const queryParam = JSON.parse(refTable.queryParam[refTable.conditionKey]);
      queryParam.month = this.$refs.refUTraceSearchForm.formdata.month || '';
      queryParam.mFieldId = this.utraceBaseParam['mFieldId'];
      const key = refTable.conditionKey;
      params[key] = JSON.stringify(queryParam);
      refTable.remoteData(params);
    },
    // 小U按月搜索form表单重置
    uSearchFormReset: function() {
      this.$refs.refUTraceSearchForm.resetFields(); // 清空小U月份查询
    },
    // 加载小U数据
    loadUTrace: function() {
      var _this = this;
      if (_this.utrace) {
        if (!_this.uPkValue) {
          console.error(this.t('el.form.uTraceNoParam'));
          return ;
        }
        var ufield = [];
        var mPkV = [];
        var option = _this.uPkValue + _this.formdata[_this.ukeyField];
        mPkV.push(option || '');
        // 查询修改痕迹历史
        var params = null;
        if (mPkV.length > 0) {
          params = { condition: JSON.stringify({mPkV: mPkV}) };
          _this.utraceBaseParam['mPkV'] = mPkV;
        };
        params && yufp.service && yufp.service.request({
          url: _this.traceServerName + _this.traceGetInterface,
          data: params,
          async: false,
          method: 'GET',
          callback: function(code, message, response) {
            if (response.data != null) {
              for (var i = 0; i < _this.fields.length; i++) {
                var field = _this.fields[i];
                for (var j = 0, reData = response.data; j < reData.length; j++) {
                  var mFieldId = reData[j].mFieldId;
                  if (field.name === mFieldId) {
                    if (ufield.indexOf(mFieldId) < 0) {
                      ufield.push(mFieldId);
                      field.message = reData[j].mDatetime + ':被用户' + reData[j].usrId + '从[' +
                        reData[j].mOldDispV + ']修改为[' + reData[j].mNewDispV + ']';
                    }
                  }
                }
              }
            }
            _this.$emit('load-utrace', [code, message, response]);
          }
        });
      }
    },
    // 清除界面上的小U标记
    clearUTrace() {
      // 清空小U标记提示
      for (var i = 0; i < this.fields.length; i++) {
        this.fields[i].message = '';
      }
    },
    /**
     *  保存小U数据
     *  @param {Array} oldFormModel 旧表单数据
     *  @param forceSave 强制保存（场景：不展示小U，但是要保存小U数据）
     */
    saveUTrace: function(oldFormModel, forceSave) {
      if (!this.utraceUsrId && !this.utraceOrgId && !this.utraceMenuId) {
        console.error(this.t('el.form.saveUTraceFailure'));
        return;
      }
      if (this.utrace || forceSave) {
        var _this = this;
        var newForm = _this.formdata;
        var temp = [];
        for (var k = 0, fields = _this.fields; k < fields.length; k++) {
          var field = fields[k].name;
          var label = fields[k].label;
          if (!looseEqual(oldFormModel[field], newForm[field]) && fields[k].utrace !== false) {
            var option = {};
            option['usrId'] = _this.utraceUsrId;
            option['mMenuId'] = _this.utraceMenuId;
            option['mPkV'] = _this.uPkValue + newForm[_this.ukeyField];
            option['orgId'] = _this.utraceOrgId;
            option['mFieldId'] = field;
            option['mFieldNm'] = label;
            if (fields[k] && fields[k].$children[0]) {
              if (fields[k].$children[0].dataCode) { // 字段翻译
                if (newForm[field] instanceof Array) { // 新表单数据为数组
                  option['mNewV'] = newForm[field].join(',');
                  option['mNewDispV'] = yufp.lookup.convertMultiKey(fields[k].$children[0].dataCode, newForm[field]);
                  if (oldFormModel[field] instanceof Array) { // 旧表单数据为数组
                    option['mOldV'] = oldFormModel[field].join(',');
                    option['mOldDispV'] = yufp.lookup.convertMultiKey(fields[k].$children[0].dataCode, oldFormModel[field]);
                  } else {
                    option['mOldV'] = oldFormModel[field];
                    var arrayFields = oldFormModel[field].split(',');
                    option['mOldDispV'] = yufp.lookup.convertMultiKey(fields[k].$children[0].dataCode, arrayFields);
                  }
                } else {
                  option['mNewV'] = newForm[field];
                  option['mOldV'] = oldFormModel[field];
                  option['mNewDispV'] = yufp.lookup.convertKey(fields[k].$children[0].dataCode, newForm[field]);
                  option['mOldDispV'] = yufp.lookup.convertKey(fields[k].$children[0].dataCode, oldFormModel[field]);
                }
              } else if (fields[k].$children[0].options) { // 下拉选项翻译
                let ops = fields[k].$children[0].options;
                let formatter = formatters['keytoValue'];
                if (newForm[field] instanceof Array) { // 新表单数据为数组
                  option['mNewV'] = newForm[field].join(',');
                  option['mNewDispV'] = formatter(null, {options: ops}, newForm[field]);
                  if (oldFormModel[field] instanceof Array) { // 旧表单数据为数组
                    option['mOldV'] = oldFormModel[field].join(',');
                    option['mOldDispV'] = formatter(null, {options: ops}, oldFormModel[field]);
                  } else {
                    option['mOldV'] = oldFormModel[field];
                    let arrayFields = oldFormModel[field].split(',');
                    option['mOldDispV'] = formatter(null, {options: ops}, arrayFields);
                  }
                } else {
                  option['mNewV'] = newForm[field];
                  option['mOldV'] = oldFormModel[field];
                  option['mNewDispV'] = formatter(null, {options: ops}, newForm[field]);
                  option['mOldDispV'] = formatter(null, {options: ops}, oldFormModel[field]);
                }
              } else {
                option['mNewDispV'] = newForm[field];
                option['mOldDispV'] = oldFormModel[field];
                option['mNewV'] = newForm[field];
                option['mOldV'] = oldFormModel[field];
              }
            } else {
              option['mNewDispV'] = newForm[field];
              option['mOldDispV'] = oldFormModel[field];
              option['mNewV'] = newForm[field];
              option['mOldV'] = oldFormModel[field];
            }
            temp.push(option);
          }
        }
        temp.length > 0 && yufp.service && yufp.service.request({
          url: _this.traceServerName + _this.traceSaveInterface,
          data: JSON.stringify(temp),
          method: 'POST',
          callback: function(code, message, response) {
            _this.$emit('after-save-utrace', code, message, response);
            // 重新拉取小U数据,强制保存不重新加载小U
            if (forceSave !== true) {
              _this.loadUTrace();
            }
          }
        });
        temp.length === 0 && _this.$emit('after-save-utrace', -1, '没有变更数据', null);
      } else {
        console.error(this.t('el.form.noUTrace'));
      }
    },
    // 生成默认小U跟踪字段
    _getDefaultUTraceTableColumns(h) {
      return (
        <div>
          {
            this.showByMonth ? (<yu-xform ref="refUTraceSearchForm" class="utrace-search-form" related-table-name="refUTraceListTable" remove-empty={true}>
              <yu-xform-group column={3}>
                <yu-xform-item placeholder="月份" ctype="datepicker" type="month" name="month"></yu-xform-item>
                <div slot="custom" class="search-btn-group">
                  <yu-button style="margin-left:10px;" type="primary" icon="search" on-click={this.uFormSearch}>查询</yu-button>
                  <yu-button type="primary" icon="edit" on-click={this.uSearchFormReset}>重置</yu-button>
                </div>
              </yu-xform-group>
            </yu-xform>) : ''
          }
          <yu-xtable ref="refUTraceListTable" max-height="300" stripe default-sort={{prop: 'mDatetime', order: 'descending'}} default-load={false} data-url={this.traceServerName + this.traceGetListInterface}>
            <yu-xtable-column label={this.t('el.form.uTraceTableCol1')} type="index" width="55" />
            <yu-xtable-column label={this.t('el.form.uTraceTableCol2')} prop="mFieldNm" resizable={true} />
            <yu-xtable-column label={this.t('el.form.uTraceTableCol3')} prop="mOldDispV" resizable={true} />
            <yu-xtable-column label={this.t('el.form.uTraceTableCol4')} prop="mNewDispV" resizable={true} />
            <yu-xtable-column label={this.t('el.form.uTraceTableCol5')} prop="usrId" resizable={true} />
            <yu-xtable-column label={this.t('el.form.uTraceTableCol6')} prop="mDatetime" resizable={true} sortable/>
          </yu-xtable>
        </div>
      );
    }
  },
  mounted: function() {
    var _this = this;
    this.setModel();
    this.$watch('$parent.templateData', function(oldval, newval) {
      _this.setModel();
    });
    if (this.utrace) {
      this.loadUTrace();
    }
  },
  render: function(h) {
    var _this = this;
    return (
      <div class={[
        'yu-xform',
        'el-form',
        _this.labelPosition ? 'el-form--label-' + _this.labelPosition : '',
        _this.inline ? 'el-form--inline' : '',
        _this.notice ? 'yu-form-notice' : '',
        _this.utrace ? 'yu-form-utrace' : '',
        { 'el-form-search': _this.formType === 'search'},
        { 'el-form-details': _this.formType === 'details'}
      ]}>
        {_this.$slots.default}
        {_this.$slots.button}
        {_this.utrace ? _this._setUTrace(h) : ''}
      </div>
    );
  }
};
</script>