<script>
import { getValueByPath } from '@/src/utils/util';
import ElTable from '../../table/src/table.vue';
import ElPagination from '../../pagination/src/pagination.js';
import YuXtableColumn from './xtable-column.js';
import YuXdialog from '../../xdialog/src/component';
import YuXcheckbox from '../../xcheckbox/src/xcheckbox';
import YuButton from '../../button/src/button';
import { formatters } from '../../../src/utils/formatter.js';
import { looseEqual } from '@/src/utils/util';
import deepmerge from 'deepmerge';
import Locale from '@/src/mixins/locale';

function noop() {}
export default {
  name: 'YuXtable',
  xtype: 'YuXtable',

  componentName: 'YuXtable',
  mixins: [Locale],
  component: {
    ElTable,
    ElPagination,
    YuXtableColumn,
    YuXdialog,
    YuButton,
    YuXcheckbox
  },
  props: {
    rowNumber: Boolean,
    selectionType: String,
    dataUrl: String,
    defaultLoad: {
      type: Boolean,
      default: true
    },
    /** 请求类型 */
    requestType: {
      type: String,
      default: 'GET'
    },
    jsonData: {
      type: String,
      default: 'data'
    },
    jsonTotal: {
      type: String,
      default: 'total'
    },
    pageKey: {
      type: String,
      default: 'page'
    },
    sizeKey: {
      type: String,
      default: 'size'
    },
    conditionKey: {
      type: String,
      default: 'condition'
    },
    // 默认参数
    baseParams: Object,
    data: {
      type: Array,
      default: function() {
        return [];
      }
    },
    // 表格相关配置属性
    height: [String, Number],
    maxHeight: [String, Number],
    fit: {
      type: Boolean,
      default: true
    },
    stripe: Boolean,
    border: {
      type: Boolean,
      default: true
    },
    rowKey: [String, Function],
    reserveSelection: Boolean,
    context: {},
    showHeader: {
      type: Boolean,
      default: true
    },
    showSummary: Boolean,
    sumText: String,
    summaryMethod: Function,
    spanMethod: Function,
    rowClassName: [String, Function],
    rowStyle: [Object, Function],
    highlightCurrentRow: {
      type: Boolean,
      default: true
    },
    currentRowKey: [String, Number],
    emptyText: String,
    expandRowKeys: Array,
    defaultExpandAll: Boolean,
    defaultSort: Object,
    tooltipEffect: String,
    // 分页相关属性配置
    pageable: {
      type: Boolean,
      default: true
    },
    pageSize: {
      type: Number,
      default: 10
    },
    pageSizes: {
      type: Array,
      default() {
        return [10, 20, 30, 40, 50, 100];
      }
    },
    rules: [Object, Array, String],
    selectable: Function,
    utrace: {
      type: Boolean,
      default: false
    },
    utraceUsrId: String, // usrId为用户ID，为与后端接口一致，不修改成userId
    utraceOrgId: String,
    utraceMenuId: String,
    utraceTitle: {
      type: String,
      default: '修改痕迹信息'
    },
    uPkValue: String,
    traceServerName: {
      type: String,
      default: ''
    },
    traceGetInterface: {
      type: String,
      default: '/utrace/selectSModifyTrace'
    },
    traceGetListInterface: {
      type: String,
      default: '/utrace/selectSModifyTraceWithPage'
    },
    traceSaveInterface: {
      type: String,
      default: '/utrace/addSModifyTrace'
    },
    ukeyField: String,
    showByMonth: { // 是否开启按月份查询U历史痕迹
      type: Boolean,
      default: false
    },
    showUtitleMessage: { // 是否鼠标滑过小U标记显示title信息
      type: Boolean,
      default: true
    },
    beforePageChange: Function,
    beforeSizeChange: Function,
    parseResponse: {
      type: Function,
      default: noop
    },
    autoSortable: Boolean,
    renderCustomContent: Function,
    // 是否开启隐藏列
    showHiddenMenu: Boolean, // 表格的唯一id
    tableSequence: String
  },
  data: function() {
    let total = this.dataTotal;
    if (this.data && this.data.length > 0) {
      total = this.data.length;
    }
    return {
      // 默认分页属性
      total: total,
      page: 1,
      size: this.pageSize || this.pageSizes[0] || 10,
      tableColumns: [],
      tabledata: this.data,
      queryParam: {},
      // loading: false,
      selections: [],
      olddata: [],
      repeatTrigger: false,
      layout: 'total, sizes, prev, pager, next, jumper',
      utraceDialogVisible: false,
      utraceBaseParam: {'mPkV': ''},
      contextMenuId: 'c_menu_id_' + new Date().getTime(),
      sequenceId: new Date().getTime() + '_' + parseInt(Math.random() * 1000, 10),
      // 是否已查询过，解决未查询直接翻页情况
      queryFlag: false
    };
  },
  computed: {
    pageInfo() {
      return {
        page: this.page,
        size: this.size,
        total: this.total
      };
    }
  },
  created() {
    if (!this.dataUrl) {
      this.olddata = this._props.data;
      this.total = this.olddata.length;
      this.privateLocalData();
    }
  },
  methods: {
    pageChangeFn(val) {
      var _this = this;
      _this.page = val;
      if (_this.repeatTrigger) {
        _this.repeatTrigger = false;
      } else {
        if (_this.dataUrl) {
          // 未发请求是，不能直接翻页发请求
          if (this.dataUrl && this.queryFlag === false) {
            return;
          }
          _this.privateRemoteData();
        } else {
          _this.privateLocalData();
        }
      }
      _this.$emit('page-change', _this.selections, val, _this.pageInfo);
    },
    sizeChangeFn(val) {
      var _this = this;
      _this.size = val;
      if (_this.repeatTrigger) {
        _this.repeatTrigger = false;
      } else {
        if (_this.page !== 1) {
          _this.page = 1;
          _this.repeatTrigger = true;
        }
        if (_this.dataUrl) {
          // 未发请求是，不能直接翻页发请求
          if (this.dataUrl && this.queryFlag === false) {
            return;
          }
          _this.privateRemoteData();
        } else {
          _this.privateLocalData();
        }
      }
      _this.$emit('size-change', _this.selections, val, _this.pageInfo);
    },
    /**
     * 外部调用，请使用remoteData方法
     * privateRemoteDate仅供组件内部使用
     */
    privateRemoteData(queryParam) {
      this.$refs.table.store.states.check = null;
      this.$refs.table.store.states.currentEditRow = null;
      var _this = this;
      this.queryFlag = true;
      _this.tabledata = [];
      _this.selections = [];
      if (!_this.dataUrl) {
        throw new Error(this.t('el.table.noDataUrl'));
      }
      var ld = this.$loading({
        target: _this.$el,
        body: false
      });
      // _this.loading = true;
      _this.queryParam = queryParam ? queryParam : _this.queryParam;

      queryParam = yufp.extend(true, {}, _this.queryParam);
      var baseParams = yufp.extend(true, {}, _this.baseParams);

      var bCondition = baseParams[_this.conditionKey];
      var qCondition = queryParam[_this.conditionKey];
      if (bCondition) {
        if (qCondition) {
          bCondition =
            typeof bCondition === 'object'
              ? bCondition
              : JSON.parse(bCondition);
          qCondition =
            typeof qCondition === 'object'
              ? qCondition
              : JSON.parse(qCondition);
          yufp.extend(true, bCondition, qCondition);
        }
        queryParam[_this.conditionKey] =
          typeof bCondition === 'object'
            ? JSON.stringify(bCondition)
            : bCondition;
      } else if (qCondition) {
        queryParam[_this.conditionKey] =
          typeof qCondition === 'object'
            ? JSON.stringify(qCondition)
            : qCondition;
      }
      delete baseParams[_this.conditionKey];
      queryParam = yufp.extend(baseParams, queryParam);

      if (_this.pageable) {
        var pageObj = {};
        pageObj[_this.pageKey] = _this.page;
        pageObj[_this.sizeKey] = _this.size;
        yufp.extend(queryParam, pageObj);
      }
      yufp.service.request({
        url: _this.dataUrl,
        data: queryParam,
        method: _this.requestType,
        callback(code, message, response) {
          if (_this.parseResponse && typeof _this.parseResponse === 'function') {
            let rest = _this.parseResponse(code, message, response);
            if (rest === false) {
              _this.$nextTick(() => {
                ld.close();
              });
              return;
            }
          }
          _this.tabledata = getValueByPath(response, _this.jsonData) || [];
          _this.total = getValueByPath(response, _this.jsonTotal) || 0;
          // _this.loading = false;
          if (_this.utrace) {
            _this.loadUTrace();
          }
          _this.$nextTick(() => {
            ld.close();
            _this.$nextTick(() => {
              _this.$emit('loaded', _this.tabledata, _this.total, response, ld);
            });
          });
        }
      });
    },
    remoteData(queryParam) {
      var _this = this;
      if (_this.page !== 1) {
        _this.page = 1;
        _this.repeatTrigger = true;
      }
      _this.privateRemoteData(queryParam);
    },
    privateLocalData() {
      let startIndex = (this.page - 1) * this.size;
      this.tabledata = this.olddata && this.olddata.length > 0
        ? this.pageable ? this.olddata.slice(startIndex, this.page * this.size) : this.olddata
        : [];
      if (this.utrace) {
        this.loadUTrace();
      }
    },
    // 清空表格数据
    clearData() {
      this.tabledata.splice(0);
    },
    saveEditRow(store, row) {
      store.states._editRows.indexOf(row) === -1 && store.states._editRows.push(row);
    },
    saveEditUTraceData(store, row, column, oldv, newv) {
      var uk = this.ukeyField;
      if (this.utrace && column.utrace && uk && row[uk] && !looseEqual(oldv, newv)) {
        let k = 'ut-$' + row[uk] + '$-' + column.property;
        let utData = store.states._uTraceData[k];
        store.states._uTraceData[k] = {
          mFieldNm: column.label,
          mNewV: newv,
          mOldV: utData ? utData['mOldV'] : oldv
        };
      }
      this.saveEditRow(store, row);
    },
    getEditRows() {
      return this.$refs.table.store.states._editRows;
    },
    clearSelection(selection) {
      this.selections = [];
      return this.$refs.table.clearSelection(selection);
    },
    toggleRowSelection(row, selected) {
      return this.$refs.table.toggleRowSelection(row, selected);
    },
    doLayout() {
      this.$refs.table.doLayout();
    },
    setCurrentRow(row) {
      this.selections = row ? [row] : [];
      return this.$refs.table.setCurrentRow(row);
    },
    // 触发event类型方法
    select(selection, row) {
      this.$emit('select', selection, row);
    },
    selectAll() {
      var table = this.$refs.table;
      table.store.states.data.forEach(function(item) {
        table.toggleRowSelection(item, true);
      });
    },
    selectAllEmit(selection) {
      this.$emit('select-all', selection);
    },
    selectionChange(selection) {
      this.selections = selection;
      this.$emit('selection-change', selection);
    },
    cellMouseEnter(row, column, cell, event) {
      this.$emit('cell-mouse-enter', row, column, cell, event);
    },
    cellMouseLeave(row, column, cell, event) {
      this.$emit('cell-mouse-leave', row, column, cell, event);
    },
    cellClick(row, column, cell, event) {
      this.$emit('cell-click', row, column, cell, event);
    },
    cellDblclick(row, column, cell, event) {
      this.$emit('cell-dblclick', row, column, cell, event);
    },
    rowClick(row, event, column, index) {
      if (this.selectionType !== 'checkbox') {
        this.setCurrentRow(row);
        this.selections = [ row ];
      } else if (!this.selectable || this.selectable.call(null, row, index)) {
        // this.$refs.table.toggleRowSelection(row);
        this.selections = this.$refs.table.selection;
      }
      this.$emit('row-click', row, event, column, index);
    },
    rowDblclick(row, event) {
      if (this.selectionType !== 'checkbox') {
        this.selections = [row];
      }
      this.$emit('row-dblclick', row, event);
    },
    rowContextmenu(row, event) {
      this.$emit('row-contextmenu', row, event);
    },
    headerClick(column, event) {
      this.$emit('header-click', column, event);
    },
    sortChange({ column, prop, order }) {
      this.$emit('sort-change', { column, prop, order });
    },
    filterChange(filters) {
      this.$emit('filter-change', filters);
    },
    currentChange(currentRow, oldCurrentRow) {
      this.$emit('current-change', currentRow, oldCurrentRow);
    },
    headerDragend(newWidth, oldWidth, column, event) {
      this.$emit('header-dragend', newWidth, oldWidth, column, event);
    },
    expand(row, expanded) {
      this.$emit('expand', row, expanded);
    },
    validate(callback, flag) {
      this.$refs.table.validate(callback, flag);
    },
    clearValidateMessage() {
      this.$refs.table.clearValidateMessage();
    },
    changUTraceListDialog() {
      this.utraceDialogVisible = !this.utraceDialogVisible;
    },
    showUTraceListDialog(name, kv) {
      var _this = this;
      _this.changUTraceListDialog();
      if (name && _this.utraceDialogVisible) { // 避免关闭后再次请求小U数据 20191209 lijh11
        this.utraceBaseParam['mFieldId'] = name;
        this.$nextTick(function() {
          _this.showByMonth && _this.$refs.refUTraceSearchForm && _this.$refs.refUTraceSearchForm.resetFields(); // 清空小U月份查询
          let refTable = _this.$refs.refUTraceListTable;
          let parames = {condition: JSON.stringify(kv ? {'mPkV': [_this.uPkValue + kv], 'mFieldId': name} : _this.utraceBaseParam)}; // 点某个字段小U展示所属行内所有修改过字段的历史记录，故增加mFieldId属性 20191128 lijh11
          if (refTable) {
            refTable.remoteData(parames);
          } else if (_this.$slots.utrace) { // 对于自定义表格slot插槽对象处理
            refTable = _this.$slots.utrace[0].children[0].componentInstance;
            refTable && refTable.remoteData && refTable.remoteData(parames);
          }
        });
      }
    },
    // 小U按月搜索查询
    uFormSearch: function() {
      let refTable = this.$refs.refUTraceListTable;
      let params = {};
      const queryParam = JSON.parse(refTable.queryParam[refTable.conditionKey]);
      queryParam.month = this.$refs.refUTraceSearchForm.formdata.month || '';
      queryParam.mFieldId = this.utraceBaseParam['mFieldId'];
      const key = refTable.conditionKey;
      params[key] = JSON.stringify(queryParam);
      refTable.remoteData(params);
    },
    // 小U按月搜索form表单重置
    uSearchFormReset: function() {
      this.$refs.refUTraceSearchForm.resetFields(); // 清空小U月份查询
    },
    // 加载小U数据
    loadUTrace: function() {
      var _this = this;
      if (_this.utrace && _this.tabledata.length > 0) {
        if (!_this.uPkValue) {
          console.error(this.t('el.table.uTraceNoParam'));
          return;
        }
        var mPkV = [];
        for (var i = 0; i < _this.tabledata.length; i++) {
          var option = _this.uPkValue + _this.tabledata[i][_this.ukeyField];
          mPkV.push(option || '');
        }
        // 查询修改痕迹历史
        var params = null;
        if (mPkV.length > 0) {
          params = { condition: JSON.stringify({ mPkV: mPkV }) };
          _this.utraceBaseParam['mPkV'] = mPkV;
        }
        params && yufp.service && yufp.service.request({
          url: _this.traceServerName + _this.traceGetInterface,
          data: params,
          async: false,
          method: 'GET',
          callback: function(code, message, response) {
            if (response && response.data != null) {
              for (let i = 0; i < _this.tabledata.length; i++) {
                let tData = _this.tabledata[i];
                let ufield = [];
                let messages = [];
                for (let j = 0, reData = response.data; j < reData.length; j++) {
                  let mFieldId = reData[j].mFieldId;
                  let pk = tData[_this.ukeyField];
                  let mpk = reData[j].mPkV;
                  mpk = mpk.replace(_this.uPkValue, '');
                  if (pk === mpk) {
                    if (ufield.indexOf(mFieldId) < 0) {
                      ufield.push(mFieldId);
                      messages.push(_this.t('el.table.uTraceMessage', {time: reData[j].mDatetime, usrId: reData[j].usrId, mOldDispV: reData[j].mOldDispV, mNewDispV: reData[j].mNewDispV}));
                    }
                  }
                }
                ufield.length > 0 && (tData.uTraceFidlds = ufield);
                messages.length > 0 && (tData.uTraceMessages = messages);
              }
            }
          }
        });
      }
    },
    /**
     * 设置小U留痕信息
     */
    _setUTrace: function(h) {
      var _this = this;
      var dialogProps = {
        props: {
          ref: 'refUTraceXTableDialog',
          visible: _this.utraceDialogVisible,
          width: '850px',
          title: _this.utraceTitle,
          'before-close': _this.changUTraceListDialog
        }
      };
      var buttonProps = {
        props: {
          type: 'primary'
        },
        on: {
          click: _this.showUTraceListDialog
        }
      };
      var buttonList = (<div slot="footer" align="center">{<YuButton {...buttonProps}>{this.t('el.table.uTraceTableDialogBtn')}</YuButton>}</div>);
      var yuTableTemp = _this.$slots.utrace || (_this._getDefaultUTraceTableColumns(h));
      return (<YuXdialog {...dialogProps}>{yuTableTemp}{buttonList}</YuXdialog>);
    },
    /**
     *  保存小U数据
     *  @param {Array} editData 编辑数据
     *  @param forceSave 强制保存（场景：不展示小U，但是要保存小U数据）
     */
    saveUTrace: function(editData, forceSave) {
      if (!this.utraceUsrId && !this.utraceOrgId && !this.utraceMenuId) {
        console.error(this.t('el.table.saveUTraceFailure'));
        return;
      }
      if (this.utrace || forceSave) {
        var _this = this;
        let _utraceData = this.$refs.table.store.states._uTraceData;
        let ukf = this.ukeyField;
        var temp = [];
        if (Array.isArray(editData)) {
          Object.keys(_utraceData).map(key => {
            let v = key.split('-')[1].replace(/\$/g, '');
            let has = false;
            editData.map(item => {
              if (item[ukf] === v) {
                has = true;
                return;
              }
            });
            !has && delete _utraceData[key] && (has = !has);
          });
        }
        for (let k in _utraceData) {
          let params = k.split('-');
          if (params.length > 0) {
            let id = params[1].substr(1, params[1].length - 2);
            let col = params[2];
            let op = _utraceData[k];
            let option = {};
            option['usrId'] = _this.utraceUsrId;
            option['mMenuId'] = _this.utraceMenuId;
            option['mPkV'] = _this.uPkValue + id;
            option['orgId'] = _this.utraceOrgId;
            option['mFieldId'] = col;
            option['mFieldNm'] = op.mFieldNm;
            if (op.mNewV instanceof Array) {
              option['mNewV'] = op.mNewV.join(',');
            } else {
              option['mNewV'] = op.mNewV;
            }
            if (op.mOldV instanceof Array) {
              option['mOldV'] = op.mOldV.join(',');
            } else {
              option['mOldV'] = op.mOldV;
            }
            let { odv, ndv } = _this._getDisplayText(col, op.mOldV, op.mNewV);
            option['mNewDispV'] = ndv;
            option['mOldDispV'] = odv;

            temp.push(option);
          }
        }
        temp.length > 0 && yufp.service && yufp.service.request({
          url: _this.traceServerName + _this.traceSaveInterface,
          data: JSON.stringify(temp),
          method: 'POST',
          callback: function(code, message, response) {
            _this.$emit('after-save-utrace', code, message, yufp.clone(response, {}), yufp.clone(temp, {}));
            // 保存成功后 删除处理数据
            _this.$refs.table.store.states._uTraceData = {};
            // 重新拉取小U数据,强制保存不重新加载小U
            if (forceSave !== true) {
              _this.loadUTrace();
            }
          }
        });
        temp.length === 0 && _this.$emit('after-save-utrace', -1, this.t('el.table.saveUTraceNoUpdate'), null, null);
      } else {
        console.error(this.t('el.table.noUTrace'));
      }
    },
    /**
     *  获取显示文本
     *  @param {Object} col 列对象
     *  @param {String|Array} oldv 原始数据
     *  @param {String|Array} newv 新数据
     *  @return {Object} {odv,ndv} 返回数据对象，值对应的显示文本
     */
    _getDisplayText(col, oldv, newv) {
      let c = this.getColByName(col);
      if (c && c.dataCode) {
        let ov = oldv;
        if (ov instanceof Array) {
          ov = yufp.lookup.convertMultiKey(c.dataCode, ov.join(','));
        } else {
          ov = yufp.lookup.convertKey(c.dataCode, ov);
        }
        let nv = newv;
        if (nv instanceof Array) {
          nv = yufp.lookup.convertMultiKey(c.dataCode, nv.join(','));
        } else {
          nv = yufp.lookup.convertKey(c.dataCode, nv);
        }
        return { odv: ov, ndv: nv};
      } else if (c && c.options) {
        let formatter = formatters['keytoValue'];
        return { odv: formatter(null, {options: c.options}, oldv), ndv: formatter(null, {options: c.options}, newv)};
      } else {
        return { odv: oldv, ndv: newv };
      }
    },
    /**
     *  根据列名获取列对象
     *  @param {String} col 列名称
     *  @return {Object} 列对象
     */
    getColByName(col) {
      let c = this.tableColumns.filter((item) => {
        return item.prop === col;
      });
      return c && c.length > 0 ? c[0] : null;
    },
    // 生成默认小U跟踪字段
    _getDefaultUTraceTableColumns(h) {
      return (
        <div>
          {
            this.showByMonth ? (<yu-xform ref="refUTraceSearchForm" class="utrace-search-form" related-table-name="refUTraceListTable" remove-empty={true}>
              <yu-xform-group column={3}>
                <yu-xform-item placeholder="月份" ctype="datepicker" type="month" name="month"></yu-xform-item>
                <div slot="custom" class="search-btn-group">
                  <yu-button style="margin-left:10px;" type="primary" icon="search" on-click={this.uFormSearch}>查询</yu-button>
                  <yu-button type="primary" icon="edit" on-click={this.uSearchFormReset}>重置</yu-button>
                </div>
              </yu-xform-group>
            </yu-xform>) : ''
          }
          <yu-xtable ref="refUTraceListTable" max-height="300" stripe default-load={false} default-sort={{prop: 'mDatetime', order: 'descending'}} data-url={this.traceServerName + this.traceGetListInterface}>
            <yu-xtable-column label={this.t('el.table.uTraceTableCol1')} type="index" width="55" />
            <yu-xtable-column label={this.t('el.table.uTraceTableCol2')} prop="mFieldNm" resizable={true}/>
            <yu-xtable-column label={this.t('el.table.uTraceTableCol3')} prop="mOldDispV" resizable={true} />
            <yu-xtable-column label={this.t('el.table.uTraceTableCol4')} prop="mNewDispV" resizable={true} />
            <yu-xtable-column label={this.t('el.table.uTraceTableCol5')} prop="usrId" resizable={true} />
            <yu-xtable-column label={this.t('el.table.uTraceTableCol6')} prop="mDatetime" resizable={true} sortable/>
          </yu-xtable>
        </div>
      );
    },
    checkValidate() {
      let validate = false;
      let _this = this;
      this.validate(function(valid) {
        validate = valid;
      });
      if (validate) {
        _this.$message({message: this.t('el.table.failure'), type: 'error'});
        return false;
      } else {
        return true;
      }
    },
    /**
    * 根据插槽获取子元素，包括嵌套,并转换为数组，
    数组中每一项对象值为{label，prop，hidden}
     */
    setHiddenColumn: function(h) {
      if (this.$refs.table && this.$refs.table.store && this.$refs.table.store.states.vnodeColumn) {
        var vnodeColumn = this.$refs.table.store.states.vnodeColumn;
        var hiddenColumnsChecked = [];
        var hiddenColumns = [];
        vnodeColumn.filter(function(item) {
          if (item.prop && (!item.$children || item.$children.length === 0)) {
            hiddenColumns.push({key: item.prop, value: item.label, hidden: item.varHideColumn});
            if (item.varHideColumn !== true) {
              hiddenColumnsChecked.push(item.prop);
            }
            return true;
          } else {
            return false;
          }
        }, this);
        var checkboxProps = {
          props: {
            options: hiddenColumns,
            value: hiddenColumnsChecked,
            min: 1// 必须选一项数据
          },
          on: {
            change: this.headerContextChange
          }
        };
        // 不能使用elcheckbox 会涉及到渲染值错误（当添加key后会出现死循环渲染）
        return (<div id={this.contextMenuId} class="yu-xtable__contextMenu">
          {
            <YuXcheckbox {...checkboxProps}></YuXcheckbox>
          }
        </div>
        );
      }
      return '';
    },
    /**
      * 表头右键监听
      * @param e:鼠标事件
      * @param v:vue
      */
    contextMenuFun(e, v) {
      var contextmenuTab = v.$parent.$el.querySelector('#' + v.contextMenuId);
      v.openMenu(contextmenuTab, e);
      contextmenuTab.removeEventListener('mouseleave', (e) => {
        v.closeMenu(contextmenuTab);
      });
      contextmenuTab.addEventListener('mouseleave', (e) => {
        v.closeMenu(contextmenuTab);
      });
    },
    // 跟随右键位置显示菜单
    openMenu(contextmenuTab, e) {
      contextmenuTab.style.left = e.clientX + 'px';
      contextmenuTab.style.top = e.clientY + 'px';
      contextmenuTab.style.display = 'block';
      contextmenuTab.style.position = 'fixed';
    },
    // 关闭右键菜单
    closeMenu(contextmenuTab) {
      contextmenuTab.style.display = 'none';
    },
    /**
    *表头右键中checkbox的事件
     */
    headerContextChange: function(e) {
      var vnodeColumn = this.$refs.table.store.states.vnodeColumn;
      vnodeColumn.map(function(item) {
        // 去掉多余的column无用列（没有prop的和存在子节点的）
        if (item.prop && (!item.$children || item.$children.length === 0)) {
          if (e.indexOf(item.prop) > -1) {
            item.varHideColumn = false;
          } else {
            item.varHideColumn = true;
          }
        }
      });
    }
  },
  watch: {
    data(val) {
      if (val.length === 0) {
        this.$refs.table.store.states.check = null;
        this.$refs.table.store.states.currentEditRow = null;
        this.$refs.table.store.states.selection = [];
      }
      this.olddata = val;
      this.total = val.length;
      this.privateLocalData();
    },
    dataUrl(val) {
      var _this = this;
      if (_this.page !== 1) {
        _this.page = 1;
        _this.repeatTrigger = true;
      }
      _this.privateRemoteData();
    },
    baseParams(val) {
      var _this = this;
      if (_this.page !== 1) {
        _this.page = 1;
        _this.repeatTrigger = true;
      }
      _this.privateRemoteData();
    },
    dataTotal(val) {
      this.total = val;
    }
  },
  mounted: function() {
    var defaultSlot = this.$slots.default;
    var getTableColumn = function(xtableColumn) {
      var tableColumn = [];
      var columns = xtableColumn.filter(function(item) {
        return item.tag && item.tag.indexOf('YuXtableColumn') > -1;
      });
      for (var i = 0, l = columns.length; i < l; i++) {
        if (columns[i].tag && columns[i].tag.indexOf('YuXtableColumn') > -1) {
          if (columns[i].componentOptions.children) {
            tableColumn.push(columns[i].componentOptions.propsData);
            tableColumn[i].children = getTableColumn(columns[i].componentOptions.children);
          } else {
            tableColumn.push(columns[i].componentOptions.propsData);
          }
        }
      };
      return tableColumn;
    };
    // 过滤空的文本节点，20191120（getTableColumn 已添加 YuXtableColumn 的过滤，顾去掉下面方法中的YuXtableColumn 判断）
    if (defaultSlot && defaultSlot.length > 0) {
      this.tableColumns = getTableColumn(defaultSlot);
    }
    if (this.defaultLoad && this.dataUrl) {
      this.privateRemoteData();
    } else {
      this.total = this.total || 0;
    }
    this.$on('el.form.change', this.validate);
  },
  render: function(h) {
    // 表格属性，及事件传递
    var cloneProp = deepmerge(this.$props, this.$options.propsData, { clone: true });
    delete cloneProp.data;
    if (cloneProp.selectionType === 'checkbox') {
      cloneProp.highlightCurrentRow = false;
    }
    cloneProp.tableSequence = this.sequenceId;
    var propsData = {
      props: cloneProp
    };
    propsData.on = {
      'select': this.select,
      'select-all': this.selectAllEmit,
      'selection-change': this.selectionChange,
      'cell-mouse-enter': this.cellMouseEnter,
      'cell-mouse-leave': this.cellMouseLeave,
      'cell-click': this.cellClick,
      'cell-dblclick': this.cellDblclick,
      'row-click': this.rowClick,
      'row-contextmenu': this.rowContextmenu,
      'row-dblclick': this.rowDblclick,
      'header-click': this.headerClick,
      'sort-change': this.sortChange,
      'filter-change': this.filterChange,
      'current-change': this.currentChange,
      'header-dragend': this.headerDragend,
      'expand': this.expand
    };
    // 分页组件属性，及事件传递
    var pageData = {
      props: {
        total: this.total,
        currentPage: this.page,
        pageSize: this.size,
        pageSizes: this.pageSizes,
        layout: this.layout,
        beforePageChange: this.beforePageChange,
        beforeSizeChange: this.beforeSizeChange
      }
    };
    pageData.on = {
      'current-change': this.pageChangeFn,
      'size-change': this.sizeChangeFn
    };
    var table = (
      <div class="yu-xtable">
        <ElTable ref="table" {...propsData} data={this.tabledata}>
          { this.selectionType === 'radio' ? <YuXtableColumn type="single" width="35"></YuXtableColumn> : '' }
          { this.selectionType === 'checkbox' ? <YuXtableColumn reserveSelection={this.reserveSelection} type="selection" selectable={this.selectable} width="55"></YuXtableColumn> : '' }
          { this.rowNumber ? <YuXtableColumn type="index" width="55" label={ this.t('el.table.order') }></YuXtableColumn> : '' }
          { this.$slots.default }
          <template slot="empty" v-if="$slots.empty">{ this.$slots.empty }</template>
          <template slot="append" v-if="$slots.append">{ this.$slots.append }</template>
        </ElTable>
        {this.pageable ? <ElPagination {...pageData}/> : ''}
        {this.utrace ? this._setUTrace(h) : ''}
        {this.showHiddenMenu ? this.setHiddenColumn(h) : ''}
      </div>
    );
    return table;
  },
  updated() {
    var _this = this;
    if (_this.showHiddenMenu) {
      var thead = this.$el.querySelector('.el-table__header-wrapper');
      thead.removeEventListener('contextmenu', function(e) {
        e.preventDefault();
        _this.contextMenuFun(e, _this);
      });
      thead.addEventListener('contextmenu', function(e) {
        e.preventDefault();
        _this.contextMenuFun(e, _this);
      });
    }
  }
};
</script>

