<template>
  <button :disabled="varDisabled" class="el-button"
    @click="handleClick"
    :autofocus="autofocus"
    :type="nativeType"
    :class="[
      type ? 'el-button--' + type : '',
      size ? 'el-button--' + size : '',
      {
        'is-disabled': varDisabled,
        'is-loading': varLoading,
        'is-plain': plain
      }
    ]"
  >
    <i class="el-icon-loading" v-if="varLoading" @click="handleInnerClick"></i>
    <i :class="'el-icon-' + icon" v-if="icon && !varLoading" @click="handleInnerClick"></i>
    <span v-if="$slots.default" @click="handleInnerClick"><slot></slot></span>
  </button>
</template>
<script>
  export default {
    name: 'ElButton',
    xtype: 'YuButton',

    props: {
      type: {
        type: String,
        default: 'default'
      },
      size: String,
      icon: {
        type: String,
        default: ''
      },
      nativeType: {
        type: String,
        default: 'button'
      },
      loading: Boolean,
      disabled: Boolean,
      plain: Boolean,
      autofocus: Boolean,
      logShow: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        varDisabled: this.disabled,
        varLoading: this.loading
      };
    },
    watch: {
      disabled: function(val) {
        this.varDisabled = val;
      },
      loading: function(val) {
        this.varLoading = val;
      }
    },
    methods: {
      handleClick(evt) {
        if (this.logShow === true && this.buttonLogs) {
          this.buttonLogs(this);
        }
        this.$emit('click', evt);
      },
      handleInnerClick(evt) {
        if (this.varDisabled) {
          evt.stopPropagation();
        }
      }
    }
  };
</script>
