<template>
  <div class="input-textarea-out">
    <div v-show="!details" :class="[
      type === 'textarea' ? 'el-textarea' : 'el-input',
      size ? 'el-input--' + size : '',
      {
        'is-disabled': disabled,
      }
    ]" :style="{'line-height': divStyle}">
    <div :class="[
      {
        'el-input-group': $slots.prepend || $slots.append || unit,
        'el-input-group--append': $slots.append || unit,
        'el-input-group--prepend': $slots.prepend
      }
    ]">
      <template v-if="type !== 'textarea'">
        <!-- 前置元素 -->
        <div class="el-input-group__prepend" v-if="$slots.prepend">
          <slot name="prepend"></slot>
          <span v-if="!this.disabled&&needPlaceholder&&placeholder!=''&&(value==''||value==undefined)"
            @click="placeholderClick"
            class="el-placeholder el-prependPlaceholder">
            {{placeholder}}
          </span>
        </div>
        <!-- input 图标 -->
        <slot name="icon">
          <i class="el-input__icon"
            :class="[
              'el-icon-' + icon,
              onIconClick ? 'is-clickable' : ''
            ]"
            v-if="icon"
            @click="handleIconClick">
          </i>
        </slot>
        <input
          v-if="type !== 'textarea'"
          class="el-input__inner"
          v-bind="$props"
          :autocomplete="autoComplete"
          :value="currentValue"
          ref="input"
          @input="handleInput"
          @focus="handleFocus"
          @blur="handleBlur"
          @keyup.delete="inputFix"
          @keyup.ctrl.88="inputFix"
        >
        <div class="el-input_account" v-if="account">
          <span>{{currentValue.length}}</span>
          <i v-if="maxlength">/</i>
          <span>{{maxlength}}</span>
        </div>
        <div class="el-input_notice" v-if="notice">
          <span>{{prompt}}</span>
        </div>
        <i class="el-input__icon el-icon-loading" v-if="validating"></i>
        <!-- 后置元素 -->
        <div class="el-input-group__append" v-if="unit || $slots.append">
          <slot name="append"></slot>
          <span v-if="unit">{{unit}}</span>
        </div>
      </template>
      <textarea
        v-else
        class="el-textarea__inner"
        :value="currentValue"
        @input="handleInput"
        ref="textarea"
        v-bind="$props"
        :style="textareaStyle"
        @focus="handleFocus"
        @blur="handleBlur"
        @keyup.delete="inputFix"
        @keyup.ctrl.88="inputFix">
      </textarea>
      <span v-if="!this.disabled&&needPlaceholder&&!$slots.prepend&&placeholder!=''&&(value==''||value==undefined)"
        @click="placeholderClick"
        :class="[type === 'textarea' ? 'el-placeholder el-textareaPlaceholder' : 'el-placeholder']">
        {{placeholder}}
      </span>
    </div>
  </div>
  <span v-show="details">{{value}}</span>
  </div>
</template>

<style>
.el-input_account{position: absolute;right: 0;line-height: normal;font-size: 12px;}
.el-input_notice{position: absolute;left: 0;line-height: normal;font-size: 12px;}
</style>

<script>
  import emitter from '@/src/mixins/emitter';
  import calcTextareaHeight from './calcTextareaHeight';
  import merge from '@/src/utils/merge';

  export default {
    name: 'ElInput',
    xtype: 'YuInput',

    componentName: 'ElInput',

    mixins: [emitter],

    data() {
      return {
        hasInput: false,
        hasInit: false,
        prompt: this.notice,
        currentValue: this.value,
        textareaCalcStyle: {},
        needPlaceholder: !('placeholder' in document.createElement('input'))
      };
    },

    props: {
      // 输入框单位
      unit: String,
      value: [String, Number],
      placeholder: String,
      notice: String,
      limitChar: {
        type: RegExp,
        default: function() {
          // 中文：\u4e00-\u9fa5
          // 字母大小写、数字：A-Za-z0-9
          // \x21-\x2f：! " # $ % & ` ( ) * + , - . /、
          // \x3a-\x40：: ; < = > ? @
          // \x5b-\x60：[ \ ] ^ _ '
          // \x7B-\x7F：{ | } ~ del
          // 中文顿号: 、
          // 中文常用标点符号 ￥‘’
          // 国外/新疆人名中间的分割点: \·
          // 中文常用标点符号
          // 空格
          return /[^\u4e00-\u9fa5A-Za-z0-9\s\x21-\x2f\x3a-\x40\x5b-\x60\x7B-\x7F\（\）\《\》\、\——\；\，\。\、\￥\‘\’\·\“\”\<\>\！\？\：\【\】]{1,}/g;
        }
      },
      size: String,
      resize: String,
      readonly: Boolean,
      autofocus: Boolean,
      icon: String,
      account: Boolean,
      disabled: Boolean,
      type: {
        type: String,
        default: 'text'
      },
      name: String,
      autosize: {
        type: [Boolean, Object],
        default: false
      },
      rows: {
        type: Number,
        default: 2
      },
      autoComplete: {
        type: String,
        default: 'off'
      },
      form: String,
      maxlength: [String, Number],
      minlength: [String, Number],
      max: {},
      min: {},
      step: {},
      validateEvent: {
        type: Boolean,
        default: true
      },
      onIconClick: Function,
      formatter: Function,
      digit: Number,
      details: Boolean
    },

    computed: {
      validating() {
        return this.$parent.validateState === 'validating';
      },
      textareaStyle() {
        return merge({}, this.textareaCalcStyle, { resize: this.resize });
      },
      // created by taoting1
      // 解决ie9中，label与 输入框append模式 不对齐的bug
      divStyle() {
        return (this.$slots.prepend || this.$slots.append || this.unit) ? 'normal' : 'inherit';
      }
    },

    watch: {
      'value'(val, oldValue) {
        if (val && this.limitChar && typeof (val) === 'string') {
          val = val.replace(this.limitChar, '');
        }
        // if (this.type === 'num' && val !== '' && /^[-\d\.\,]*$/.test(val)) {
        //   val = Number(val);
        // }
        this.setCurrentValue(val);
        if (this.type === 'num' && !this.hasInput) {
          this.formatterFn();
        }
        val === '' ? this.prompt = this.notice : this.prompt = '';
        var nVal = this.type === 'num' && val !== '' ? Number(val) : val;
        this.$emit('change', nVal);
      }
    },

    methods: {
      formatterFn() {
        var val = this.value;
        if (this.type === 'num' && this.value !== '' && this.value) {
          if (this.formatter) {
            if (this.digit) {
              val = this.formatter(this.value, this.digit);
            } else {
              val = this.formatter(this.value);
            }
          }
        }
        this.setCurrentValue(val);
      },
      inputFix(event) {
        if (!('placeholder' in document.createElement('input'))) {
          if (event.keyCode === 8 || event.keyCode === 46 || (event.ctrlKey && event.keyCode === 88)) {
            this.handleInput(event);
          }
        }
      },
      placeholderClick(event) {
        if ((this.$parent.$el.className.indexOf('el-select') > -1 && !this.disabled) || !(this.disabled || this.readonly)) {
          this.handleFocus(event);
          this.$refs.input ? this.$refs.input.focus() : this.$refs.textarea.focus();
        }
      },
      handleBlur(event) {
        this.hasInput = false;
        // 去除前后空格
        var value = event.target.value === '' ? '' : event.target.value.trim();
        // 金额格式化
        if (this.type === 'num' && value !== '' && /^[-\d\.\,]*$/.test(value)) {
          if (this.formatter) {
            if (this.digit) {
              this.$emit('input', Number(parseFloat(value + '').toFixed(this.digit)));
              value = this.formatter(value, this.digit);
            } else {
              value = this.formatter(value);
            }
          }
        } else {
          this.$emit('input', value);
        }
        value === '' ? this.prompt = this.notice : this.prompt = '';
        this.setCurrentValue(value);
        this.$emit('blur', event);
        if (this.validateEvent) {
          this.prompt = '';
          this.dispatch('ElFormItem', 'el.form.blur', [this.currentValue]);
          this.dispatch('YuXformAbstractItem', 'el.form.blur', [this.currentValue]);
        }
      },
      inputSelect() {
        this.$refs.input.select();
      },
      resizeTextarea() {
        if (this.$isServer) return;
        var { autosize, type } = this;
        if (type !== 'textarea') return;
        if (!autosize) {
          this.textareaCalcStyle = {
            minHeight: calcTextareaHeight(this.$refs.textarea).minHeight
          };
          return;
        }
        const minRows = autosize.minRows;
        const maxRows = autosize.maxRows;

        this.textareaCalcStyle = calcTextareaHeight(this.$refs.textarea, minRows, maxRows);
      },
      handleFocus(event) {
        this.hasInput = false;
        var value = event.target.value;
        if (this.type === 'num' && value !== '' && /^[-\d\.\,]*$/.test(value) && this.formatter) {
          var val = this.value;
          this.setCurrentValue(val);
        }
        this.prompt = '';
        this.$emit('focus', event);
      },
      handleInput(event) {
        this.hasInput = true;
        var value = event.target.value;
        // if (this.type === 'num' && value !== '' && /^[-\d\.\,]*$/.test(value)) {
        //   value = Number(value);
        // }
        this.$emit('input', value);
        this.setCurrentValue(value);
        // 会触发watch，watch中已处理change，顾删除改处（目前情况是会触发两次change）
        // this.$emit('change', value);
      },
      handleIconClick(event) {
        if (this.onIconClick) {
          this.onIconClick(event);
        }
        this.$emit('click', event);
      },
      setCurrentValue(value) {
        if (value === this.currentValue) return;
        if (this.validateEvent && this.currentValue !== undefined) {
          this.prompt = '';
          this.dispatch('YuXtable', 'el.form.change');
          this.dispatch('ElFormItem', 'el.form.change', [value]);
          this.dispatch('YuXformAbstractItem', 'el.form.change', [value]);
        }
        this.$nextTick(() => {
          this.resizeTextarea();
        });
        this.currentValue = value;
      }
    },

    created() {
      this.$on('inputSelect', this.inputSelect);
    },

    mounted() {
      if (this.value || this.value === 0) {
        this.$nextTick(function() {
          this.$emit('change', this.value);
        });
      }
      this.resizeTextarea();
      this.formatterFn();
    }
  };
</script>