<template>
  <div>
    <el-radio-group
      v-show="!details"
      v-model="radio" 
      :disabled="disabled" 
      :name="name" 
      @change="change">
      <el-radio 
        v-if="!optionButton" 
        v-for="item in radioData" 
        :label="item.key" 
        :key="item.key" 
        :disabled="item.disabled === undefined ? disabled : item.disabled">
        {{item.value}}
      </el-radio>
      <el-radio-button 
        v-if="optionButton" 
        v-for="item in radioData" 
        :label="item.key" 
        :key="item.key" 
        :disabled="item.disabled === undefined ? disabled : item.disabled">
        {{item.value}}
      </el-radio-button>
    </el-radio-group>
    <span v-show="details">{{getSelectdText()}}</span>
  </div>
</template>

<script>
export default {
  name: 'ElRadioX',
  xtype: 'YuXradio',

  props: {
    disabled: Boolean,
    name: String,
    dataUrl: String,
    dataCode: String,
    /** 字典查询参数 */
    dataParams: {
      type: Object,
      default: function() {
        return {};
      }
    },
    /** 请求类型 */
    requestType: {
      type: String,
      default: 'GET'
    },
    jsonData: {
      type: String,
      default: 'data'
    },
    options: {
      type: Array,
      default: function() {
        return [];
      }
    },
    value: [String, Number],
    // 选项是否为button，默认false
    optionButton: {
      type: Boolean,
      default: function() {
        return false;
      }
    },
    datacodeFilter: Function,
    /** 数据配置选项*/
    props: {
      type: Object,
      default: function() {
        return { key: 'key', value: 'value' };
      }
    },
    excludeKey: String,
    details: Boolean
  },
  data: function() {
    return {
      radio: this.value ? this.value : '',
      radioData: this.options
    };
  },
  mounted: function() {
    // 动态表单赋初始值联动使用
    if (this.value) {
      this.$nextTick(function() {
        this.$emit('change', this.value);
      });
    }
    if (this.dataUrl) {
      this.query();
    } else if (!this.dataUrl && this.dataCode) {
      this.setDataCode(this.dataCode);
    } else {
      this.setRadioData(this.radioData);
    }
  },
  methods: {
    setRadioData: function(options) {
      if (this.props.key === 'key' && this.props.value === 'value') {
        this.radioData = options;
      } else {
        var listData = [];
        for (var i = 0, len = options.length; i < len; i++) {
          var obj = options[i];
          listData.push({
            key: obj[this.props.key],
            value: obj[this.props.value]
          });
        }
        this.radioData = listData;
      }
    },
    query: function() {
      var me = this;
      yufp.service.request({
        method: me.requestType,
        url: me.dataUrl,
        data: me.dataParams,
        callback: function(code, message, response) {
          var data = me.getObjectKey(response, me.jsonData) || [];
          data = data && data.length > 0 ? data : [];
          me.setRadioData(data);
        }
      });
    },
    setDataCode: function(dataCode) {
      var _this = this;
      var listData = [];
      yufp.lookup.bind(dataCode, function(options) {
        if (_this.datacodeFilter) {
          // 此处获取字段名称是不合理的，暂时先从父元素中获取
          listData = _this.datacodeFilter(options, _this.dataCode, _this.$parent && _this.$parent.name);
          _this.setRadioData(listData);
        } else {
          if (_this.excludeKey) {
            let array = _this.excludeKey.split(',');
            options = options.filter(function(item) {
              if (array.indexOf(item[_this.props.key]) > -1) {
                return false;
              }
              return true;
            });
          }
          _this.setRadioData(options);
        }
      });
    },
    change: function(val) {
      this.$emit('change', val);
    },
    getObjectKey: function(obj, ns) {
      if (!ns) {
        return obj;
      }
      var keys = ns.split('.');
      for (var i = 0, len = keys.length; i < len; i++) {
        if (!obj) {
          break;
        }
        obj = obj[keys[i]];
      }
      return obj;
    },
    getSelectdText: function() {
      var text = '';
      for (var i = 0; i < this.radioData.length; i++) {
        var obj = this.radioData[i];
        if (obj.key === this.radio) {
          text = obj.value;
          break;
        }
      }
      return text;
    }
  },
  watch: {
    dataCode: function(val) {
      if (val) {
        this.setDataCode(val);
      }
    },
    dataUrl: function(url) {
      if (url) {
        this.query();
      }
    },
    // radioData: function(data) {
    //   if (data) {
    //     this.setRadioData(data);
    //   }
    // },
    radio: function(val) {
      this.$emit('input', val);
    },
    value: function(val) {
      if (this.radio !== val) {
        this.radio = val;
      }
    },
    options: function(val) {
      this.radioData = val;
    }
  }
};
</script>

