<template>
    <div :id="chartId" :style="{width: width, height: height}"></div>
</template>

<script>
export default {
  name: 'YuEcharts',
  xtype: 'YuEcharts',

  props: {
    option: {
      type: Object,
      required: true
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '100%'
    }
  },
  data: function() {
    return {
      echartsInstance: null,
      chartId: 'echarts_' + (new Date().getTime() + Math.random() * 10)// 防止同一页面多个图表产生的id相同
    };
  },
  mounted: function() {
    this.$nextTick(function() {
      this.echartsInstance = window.echarts.init(
        document.getElementById(this.chartId)
      );
      this.echartsInstance.setOption(this.option);
    });
  },
  watch: {
    option: {
      handler: function(val, oldVal) {
        this.echartsInstance ||
          (this.echartsInstance = window.echarts.init(
            document.getElementById(this.chartId)
          ));
        this.echartsInstance.clear();
        this.echartsInstance.setOption(val);
      },
      deep: true
    }
  },
  beforeDestroy: function() {
    // 销毁echarts实例，为了让单页面应用中echarts占用的内存释放
    if (this.echartsInstance) {
      this.echartsInstance.dispose();
      this.echartsInstance = null;
    }
  }
};
</script>
