<script>
import YuXformItem from '@/packages/xform/src/form-item-x';
import YuXform from '@/packages/xform/src/form';
import YuXformGroup from '@/packages/xform/src/form-group';
import ElCollapse from '@/packages/collapse/src/collapse.vue';
import ElCollapseItem from '@/packages/collapse/src/collapse-item.vue';
import { getValueByPath } from '@/src/utils/util';
import { formatters} from '../../../src/utils/formatter';
import { str2obj } from '../../../src/utils/util';
import deepmerge from 'deepmerge';

export default {
  name: 'YuXdynamicForm',
  xtype: 'YuXdynamicForm',

  componentName: 'YuXdynamicForm',

  component: {
    YuXform,
    YuXformItem,
    YuXformGroup,
    ElCollapse,
    ElCollapseItem
  },
  props: {
    sort: {
      type: Boolean,
      default: true
    },
    // 静态模版数据
    templateData: Array,
    // 查询模版数据url
    templateDataUrl: String,
    // 请求方法
    requestType: {
      type: String,
      default: 'GET'
    },
    // 默认查询参数
    baseParam: Object,
    jsonData: {
      type: String,
      default: 'data'
    },
    // 分组是否为折叠面板
    mode: {
      type: String,
      default: 'collapse'
    },
    labelWidth: {
      type: String,
      default: '100px'
    }
  },
  data: function() {
    return {
      template: [],
      activeArr: [],
      formdata: {},
      validate: null
    };
  },

  watch: {
    templateData: function(data) {
      var cloneTemplate = {};
      cloneTemplate = deepmerge(cloneTemplate, data, { clone: true });
      var template = this.orderTemplateData(cloneTemplate);
      this.template = this.transferTemplate(template);
    }
  },

  methods: {
    // 因数组类型数据用克隆赋值的方式监听不到，统一采用extend方法赋值
    setFormData(data) {
      yufp.extend(this.formdata, data);
    },
    // 动态表单清空
    resetFields: function() {
      this.$refs.dynamicForm.resetFields();
    },
    // 对模版数据中的linkage，以及校验做转换
    transferTemplate: function(templateArr) {
      for (var i = 0, l = templateArr.length; i < l; i++) {
        var fields = templateArr[i].fields;
        for (var j = 0, lh = fields.length; j < lh; j++) {
          var field = fields[j];
          if (field.linkage && typeof (field.linkage) === 'string') {
            field.linkage = str2obj(field.linkage);
          }
          if (field.rules instanceof Array) {
            for (var k = 0, _l = field.rules.length; k < _l; k++) {
              var rule = field.rules[k];
              if (rule.validator) {
                rule.validator = str2obj(rule.validator);
              }
            }
          }
          if (field.rules instanceof Object) {
            if (field.rules.validator) {
              field.rules.validator = str2obj(field.rules.validator);
            }
          }
        }
      }
      return templateArr;
    },
    // 对模版数据进行排序
    orderTemplateData: function(template) {
      this.activeArr = [];
      template = this.sort ? template.sort(this.compare('order')) : template;
      for (var i = 0, l = template.length; i < l; i++) {
        template[i].fields = this.sort ? template[i].fields.sort(this.compare('order')) : template[i].fields;
        if (template[i].collapse) {
          this.activeArr.push(template[i].title);
        }
      }
      return template;
    },
    // 查询模版数据
    getTemplateData: function() {
      var _this = this;
      yufp.service.request({
        url: _this.templateDataUrl,
        data: _this.baseParam,
        method: _this.requestType,
        async: false,
        callback(code, message, response) {
          var templateArr = getValueByPath(response, _this.jsonData) || [];
          _this.template = _this.orderTemplateData(_this.transferTemplate(templateArr));
        }
      });
    },
    // 用于数组排序的方法
    compare: function(prop) {
      return function(obj1, obj2) {
        var val1 = obj1[prop];
        var val2 = obj2[prop];
        if (!isNaN(Number(val1)) && !isNaN(Number(val2))) {
          val1 = Number(val1);
          val2 = Number(val2);
        }
        if (val1 < val2) {
          return -1;
        } else if (val1 > val2) {
          return 1;
        } else {
          return 0;
        }
      };
    },
    renderFormItem: function(h, field, formitem, formindex) {
      var cloneFormItem = {};
      cloneFormItem = deepmerge(cloneFormItem, formitem, { clone: true });
      var value = cloneFormItem.value;
      if (cloneFormItem.formatter) {
        var type = cloneFormItem.formatter;
        cloneFormItem.formatter = typeof (type) === 'function' ? type : formatters[type];
      }
      delete cloneFormItem.value;
      if (cloneFormItem.linkage) {
        return <yu-xform-item {...{ props: cloneFormItem }} value={value} on-change={() => cloneFormItem.linkage(this, this.formdata, field, this.templateData)} name={cloneFormItem.field} colspan={cloneFormItem.colspan} ctype={cloneFormItem.ctype}></yu-xform-item>;
      } else {
        return <yu-xform-item {...{ props: cloneFormItem }} value={value} name={cloneFormItem.field} colspan={cloneFormItem.colspan} ctype={cloneFormItem.ctype}></yu-xform-item>;
      }
    }
  },

  created: function() {
    if (this.templateDataUrl) {
      this.getTemplateData();
    } else {
      this.template = this.orderTemplateData(this.transferTemplate(this.templateData));
    }
  },

  mounted: function() {
    this.$nextTick(function() {
      this.validate = this.$refs.dynamicForm.validate;
    });
  },

  render: function(h) {
    var _this = this;
    if (this.mode === 'collapse') {
      return (
        <YuXform ref="dynamicForm" label-width={_this.labelWidth} dynamic mode="collapse" v-model={_this.formdata}>
          <ElCollapse v-model={_this.activeArr}>
            {_this._l(_this.template, (item, $index) => (
              <ElCollapseItem title={item.title} name={item.title}>
                <YuXformGroup column={item.column}>
                  {_this._l(item.fields, (formitem, formindex) =>
                    _this.renderFormItem.call(this._renderProxy, h, item.fields, formitem, formindex)
                  )}
                </YuXformGroup>
              </ElCollapseItem>
            ))}
          </ElCollapse>
        </YuXform>
      );
    } else if (this.mode === 'normal') {
      return (
        <YuXform ref="dynamicForm" label-width={_this.labelWidth} dynamic mode="normal" v-model={_this.formdata}>
          {_this._l(_this.template, (item, $index) => (
            <YuXformGroup column={item.column}>
              {_this._l(item.fields, (formitem, formindex) =>
                _this.renderFormItem.call(this._renderProxy, h, item.fields, formitem, formindex)
              )}
            </YuXformGroup>
          ))}
        </YuXform>
      );
    }
  }
};
</script>

