<template>
  <div class="el-timelineItem" :style="[style]" :class="['is-' + $parent.direction]">
      <div class="el-timelineItem_head"  :class="['is-' + $parent.direction]">
          <div class="el-timelineItem_line" :class="['is-' + $parent.direction]"></div>
           <div class="el-timelineItem_icon">   
              <span  class="el-timelineItem_defaultIcon"></span>  
          </div>
       </div>
         <div class="el-timelineItem_date" :class="['is-' + $parent.direction]" >
         <slot name="date">{{ date }}</slot>
         </div>
         <div class="el-timelineItem_main" :class="['is-' + $parent.direction]">
            <div class="el-timelineItem_title">
            <slot name="title">{{ title }}</slot>
            </div>
             <div class="el-timelineItem_description">
                 <slot name="description">{{ description }}</slot>
             </div>
         </div>
  </div>
</template>

<script>
export default {
  name: 'ElTimelineItem',
  xtype: 'YuTimelineItem',

  props: {
    title: String,
    date: String,
    description: String
  },
  computed: {
    style: function() {
      const parent = this.$parent;
      const space =
        typeof parent.space === 'number'
          ? parent.space + 'px'
          : parent.space ? parent.space : (100 + '%');
      if (parent.direction === 'horizontal') {
        return { width: space };
      } else {
        return { height: space };
      }
    }
  }
};
</script>

