import Vue from 'vue';
import Loading from '@/packages/loading/src/loading.vue';
import LoadingDct from '@/packages/loading/src/directive.js';
import { addClass, removeClass} from '@/src/utils/dom';
import { t } from '@/src/locale';
const Mask = Vue.extend(Loading);
const noRepeatDirective = {};

noRepeatDirective.install = Vue => {
  if (Vue.prototype.$isServer) return;
  const disabledDom = function(el, binding, flag) {
    if (binding.modifiers.disabled) {
      if (el.__vue__ && el.__vue__._data && el.__vue__._data.varDisabled !== undefined) {
        el.__vue__.varDisabled = flag;
      } else {
        if (flag === true) {
          addClass(el, 'el-norepeat-disabled');
        } else {
          removeClass(el, 'el-norepeat-disabled');
        }
      }
    }
  };

  const setLoading = function(el, binding, flag) {
    if (binding.modifiers.loading && el.__vue__ && el.__vue__._data && el.__vue__._data.varLoading !== undefined) {
      el.__vue__.varLoading = flag;
    }
  };
  const initTime = function(el, binding) {
    let time = 3000;
    if (binding.value && binding.value.timeout) {
      let caltime;
      try {
        caltime = parseInt(binding.value.timeout, 10);
      } catch (err) {
        throw new Error(t('el.norepeat.dateTypeError1'));
      }
      if (caltime > 0) {
        time = caltime;
      } else {
        throw new Error(t('el.norepeat.dateTypeError2'));
      }
    }
    el.controlTime = time;
  };
  const defaultModel = function(el, binding) {
    if (!el.disabled) {
      el.disabled = true;
      setLoading(el, binding, true);
      disabledDom(el, binding, true);
      let timer = setTimeout(() => {
        setLoading(el, binding, false);
        disabledDom(el, binding, false);
        el.disabled = false;
        el.timer = null;
      }, el.controlTime);
      el.timer = timer;
    }
  };
  // 非组件自己的loading 属性
  const loadingModel = function(el, binding) {
    if (!el.instance.visible) {
      el.disabled = true;
      LoadingDct.toggleLoading(el, binding, true);
      let timer = setTimeout(() => {
        el.disabled = false;
        el.instance.visible = false;
        el.domVisible = false;
        el.timer = null;
      }, el.controlTime);
      el.timer = timer;
    }
  };
  const noTimer = function(el, binding) {
    if (binding.modifiers.fullscreen || binding.modifiers.eleloading) {
      loadingModel(el, binding);
    } else {
      defaultModel(el, binding);
    }
  };
  const closeStatus = function(el, binding) {
    if (el.timer) {
      clearTimeout(el.timer);
      el.timer = null;
    }
    if (binding.modifiers.fullscreen || binding.modifiers.eleloading) {
      el.disabled = false;
      el.instance.visible = false;
      el.domVisible = false;
      disabledDom(el, binding, false);
    } else {
      el.disabled = false;
      disabledDom(el, binding, false);
      setLoading(el, binding, false);
    }
  };
  Vue.directive('norepeat', {
    bind: function(el, binding, vnode) {
      if (binding.modifiers.fullscreen || binding.modifiers.eleloading) {
        let msg = '';
        if (binding.value && binding.value.message) {
          msg = binding.value.message;
        }
        const mask = new Mask({
          el: document.createElement('div'),
          data: {
            text: msg,
            fullscreen: !!binding.modifiers.fullscreen
          }
        });
        el.instance = mask;
        el.mask = mask.$el;
        el.maskStyle = {};
        LoadingDct.toggleLoading(el, binding, false);
      }
      initTime(el, binding);
    },
    inserted(el, binding) {
      el.addEventListener('click', e => {
        if (el.className.indexOf('el-norepeat-disabled') > -1) {
          e.preventDefault();
          return false;
        }
        if (e.stopPropagation) {
          e.stopPropagation();
        }
        if (!binding.modifiers.fullscreen && !binding.modifiers.eleloading) {
          noTimer(el, binding);
        }
      });
    },
    unbind(el, binding) {
      if (el.domInserted) {
        // console.log(el.domInserted, el.mask);
        if (binding.modifiers.fullscreen || binding.modifiers.eleloading) {
          if (document.body.contains(el.mask)) {
            document.body.removeChild(el.mask);
          }
        } else {
          el.mask &&
          el.mask.parentNode &&
          el.mask.parentNode.removeChild(el.mask);
        }
      }
    },
    update: function(el, binding, vNode, oldVNode) {
      if (!binding.value || (binding.value && binding.value.show === undefined)) {
        return;
      }
      if (el.timer && binding.value && binding.value.show === false) {
        closeStatus(el, binding);
      } else {
        if (!el.timer && binding.value.show === true) {
          noTimer(el, binding);
        }
      }
    }
  });

};

export default noRepeatDirective;
