<template>
  <el-select 
    v-model="selectedVal" 
    :separator="separator"
    :multiple="multiple" 
    :disabled="disabled"
    :multiple-limit="multipleLimit"
    :size="size" 
    :name="name"
    :clearable="clearable" 
    :placeholder="placeholder"
    :filterable="filterable"
    :allow-create="allowCreate"
    :filter-method="filterMethod"
    :lock-height="lockHeight"
    :value-type="valueType"
    :details="details"
    @change="change" 
    @visible-change="visibleChange" 
    @remove-tag="removeTag" 
    @clear="clear">
    <el-option v-for="item in typeOptions" 
      :key="item.key" 
      :value="item.key"
      :disabled="item.disabled"
      :label="item.value">
      <slot name="option" :item="item"></slot>
    </el-option>
  </el-select>
</template>

<script>
import Locale from '@/src/mixins/locale';

export default {
  name: 'ElSelectX',
  xtype: 'YuXselect',
  mixins: [Locale],
  props: {
    /** 字典类型 */
    props: {
      type: Object,
      default: function() {
        return { key: 'key', value: 'value' };
      }
    },
    /** 字典查询参数 */
    dataParams: {
      type: Object,
      default: function() {
        return {};
      }
    },
    /** 字典类型查询URL */
    dataUrl: String,
    /** 字典code */
    dataCode: String,
    /** 请求类型 */
    requestType: {
      type: String,
      default: 'GET'
    },
    jsonData: {
      type: String,
      default: 'data'
    },
    options: {
      type: Array,
      default: function() {
        return [];
      }
    },
    // 下述字段为el-select组件中部分属性，配置文档参见yuwp
    name: {
      type: String,
      default: 'el-select-x'
    },
    size: String,
    disabled: {
      type: Boolean,
      default: false
    },
    clearable: {
      type: Boolean,
      default: true
    },
    multiple: Boolean,
    multipleLimit: {
      type: Number,
      default: 0
    },
    placeholder: {
      type: String,
      default: ''
    },
    allowCreate: {
      type: Boolean,
      default: false
    },
    filterable: {
      type: Boolean,
      default: false
    },
    filterMethod: Function,
    datacodeFilter: Function,
    value: {
      required: true
    },
    excludeKey: String,
    lockHeight: Boolean,
    // 多选时，数据类型为字符串时的连接符
    separator: {
      type: String,
      default: ','
    },
    valueType: {
      type: String,
      default: 'array'
    },
    // 是否为详情表单模式
    details: Boolean
  },
  data: function() {
    // var selValue = this.multiple ? [] : '';
    var selValue = this.value;
    return {
      selectedVal: selValue,
      typeOptions: this.options
    };
  },
  watch: {
    selectedVal: function(val) {
      this.$emit('input', val);
    },
    value: function(val) {
      if (this.selectedVal !== val) {
        this.selectedVal = val;
      }
    },
    dataUrl: function(url) {
      if (url) {
        this.query();
      }
    },
    dataParams: function(params) {
      if (params) {
        this.query();
      }
    },
    options: function(data) {
      if (data) {
        var listData = [];
        for (var i = 0, len = data.length; i < len; i++) {
          var item = {};
          var obj = data[i];
          item['key'] = obj[this.props.key];
          item['value'] = obj[this.props.value];
          if (obj.disabled) {
            item['disabled'] = obj.disabled;
          }
          listData.push(item);
        }
        this.typeOptions = listData;
      }
    },
    typeOptions: function(data) {
      if (data) {
        this.typeOptions = data;
      }
    },
    dataCode: function(code) {
      if (code) {
        var _this = this;
        yufp.lookup.bind(code, function(options) {
          if (_this.datacodeFilter) {
            _this.typeOptions = _this.datacodeFilter(options, code, _this.$parent && _this.$parent.name);
          } else {
            if (_this.excludeKey) {
              let array = _this.excludeKey.split(',');
              options = options.filter(function(item) {
                if (array.indexOf(item[_this.props.key]) > -1) {
                  return false;
                }
                return true;
              });
            }
            _this.typeOptions = options;
          }
        });
      }
    }
  },
  created: function() {
    if (!this.dataUrl && !this.dataCode) {
      var listData = [];
      for (var i = 0, len = this.typeOptions.length; i < len; i++) {
        var item = {};
        var obj = this.typeOptions[i];
        item['key'] = obj[this.props.key];
        item['value'] = obj[this.props.value];
        if (obj.disabled) {
          item['disabled'] = obj.disabled;
        }
        listData.push(item);
      }
      this.typeOptions = listData;
    } else if (!this.dataUrl && this.dataCode) {
      var _this = this;
      var optionsArr = [];
      yufp.lookup.bind(this.dataCode, function(options) {
        if (_this.datacodeFilter) {
          optionsArr = _this.datacodeFilter(options, _this.dataCode, _this.$parent && _this.$parent.name);
          _this.typeOptions = _this.formatter(optionsArr);
        } else {
          if (_this.excludeKey) {
            let array = _this.excludeKey.split(',');
            options = options.filter(function(item) {
              if (array.indexOf(item[_this.props.key]) > -1) {
                return false;
              }
              return true;
            });
          }
          _this.typeOptions = _this.formatter(options);
        }
      });
    } else {
      this.query();
    }
  },
  methods: {
    formatter(option) {
      var listData = [];
      for (var i = 0, len = option.length; i < len; i++) {
        var item = {};
        var obj = option[i];
        item['key'] = obj[this.props.key];
        item['value'] = obj[this.props.value];
        if (obj.disabled) {
          item['disabled'] = obj.disabled;
        }
        listData.push(item);
      }
      return listData;
    },
    change: function(val) {
      this.$emit('change', val);
    },
    visibleChange: function(val) {
      this.$emit('visible-change', val);
    },
    removeTag: function(tag) {
      this.$emit('remove-tag', tag);
    },
    getSelectdText: function() {
      var text = '';
      for (var i = 0; i < this.typeOptions.length; i++) {
        var obj = this.typeOptions[i];
        if (obj.key === this.selectedVal) {
          text = obj.value;
          break;
        }
      }
      return text;
    },
    getSelectdValue: function(value) {
      return this.selectedVal;
    },
    setSelectdByValue: function(value) {
      var isok = false;
      for (var i = 0; i < this.typeOptions.length; i++) {
        var obj = this.typeOptions[i];
        if (obj.value === value) {
          this.selectedVal = obj.key;
          isok = true;
          break;
        }
      }
      if (!isok) {
        this.$message.error(this.t('el.xselect.noValue'));
      }
    },
    setSelectdByItem: function(item) {
      if (item && !isNaN(item)) {
        if (this.typeOptions.length < item) {
          this.$message.error(this.t('el.xselect.maximumLength'));
        } else {
          this.selectedVal = this.typeOptions[item].key;
        }
      } else {
        this.$message.error(this.t('el.xselect.noValueOrNotNumber'));
      }
    },
    clear: function() {
      this.selectedVal = '';
      this.$emit('clear');
    },
    query: function() {
      var _this = this;
      yufp.service.request({
        method: _this.requestType,
        url: _this.dataUrl,
        data: _this.dataParams,
        callback: function(code, message, response) {
          _this.typeOptions = [];
          var data1 = _this.getObjectKey(response, _this.jsonData);
          data1 = data1 && data1.length > 0 ? data1 : [];
          for (var i = 0, len = data1.length; i < len; i++) {
            var obj = data1[i];
            if (obj[_this.props.key]) {
              _this.typeOptions.push({
                key: obj[_this.props.key],
                value: obj[_this.props.value]
              });
            }
          }
        }
      });
    },
    getObjectKey: function(obj, ns) {
      if (!ns) {
        return obj;
      }
      var keys = ns.split('.');
      for (var i = 0, len = keys.length; i < len; i++) {
        if (!obj) {
          break;
        }
        obj = obj[keys[i]];
      }
      return obj;
    }
  }
};
</script>