import Vue from 'vue';
import Loading from './loading.vue';
import { addClass, removeClass, getStyle } from '@/src/utils/dom';
const Mask = Vue.extend(Loading);

const loadingDirective = {};
loadingDirective.install = Vue => {
  if (Vue.prototype.$isServer) return;
  Vue.directive('loading', {
    bind: function(el, binding) {
      const mask = new Mask({
        el: document.createElement('div'),
        data: {
          text: el.getAttribute('element-loading-text'),
          fullscreen: !!binding.modifiers.fullscreen
        }
      });
      el.instance = mask;
      el.mask = mask.$el;
      el.maskStyle = {};
      loadingDirective.toggleLoading(el, binding, true);
    },

    update: function(el, binding) {
      el.instance.setText(el.getAttribute('element-loading-text'));
      if (binding.oldValue !== binding.value) {
        loadingDirective.toggleLoading(el, binding, true);
      }
    },

    unbind: function(el, binding) {
      if (el.domInserted) {
        if (binding.modifiers.fullscreen || binding.modifiers.body) {
          document.body.removeChild(el.mask);
        } else {
          el.mask &&
          el.mask.parentNode &&
          el.mask.parentNode.removeChild(el.mask);
        }
      }
    }
  });
};
loadingDirective.toggleLoading = (el, binding, flag) => {
  if (binding.value) {
    Vue.nextTick(() => {
      if (binding.modifiers.fullscreen) {
        el.originalPosition = getStyle(document.body, 'position');
        el.originalOverflow = getStyle(document.body, 'overflow');

        addClass(el.mask, 'is-fullscreen');
        insertDom(document.body, el, binding, flag);
      } else {
        removeClass(el.mask, 'is-fullscreen');

        if (binding.modifiers.body) {
          el.originalPosition = getStyle(document.body, 'position');

          ['top', 'left'].forEach(property => {
            const scroll = property === 'top' ? 'scrollTop' : 'scrollLeft';
            el.maskStyle[property] = el.getBoundingClientRect()[property] + document.body[scroll] + document.documentElement[scroll] + 'px';
          });
          ['height', 'width'].forEach(property => {
            el.maskStyle[property] = el.getBoundingClientRect()[property] + 'px';
          });

          insertDom(document.body, el, binding, flag);
        } else {
          el.originalPosition = getStyle(el, 'position');
          insertDom(el, el, binding, flag);
        }
      }
    });
  } else {
    if (el.domVisible) {
      el.instance.$on('after-leave', () => {
        el.domVisible = false;
        if (binding.modifiers.fullscreen && el.originalOverflow !== 'hidden') {
          document.body.style.overflow = el.originalOverflow;
        }
        if (binding.modifiers.fullscreen || binding.modifiers.body) {
          document.body.style.position = el.originalPosition;
        } else {
          el.style.position = el.originalPosition;
        }
      });
      el.instance.visible = false;
    }
  }
};
const insertDom = (parent, el, binding, flag) => {
  if (!el.domVisible && getStyle(el, 'display') !== 'none' && getStyle(el, 'visibility') !== 'hidden') {
    Object.keys(el.maskStyle).forEach(property => {
      el.mask.style[property] = el.maskStyle[property];
    });

    if (el.originalPosition !== 'absolute' && el.originalPosition !== 'fixed') {
      parent.style.position = 'relative';
    }
    if (binding.modifiers.fullscreen && binding.modifiers.lock) {
      parent.style.overflow = 'hidden';
    }
    el.domVisible = flag;

    parent.appendChild(el.mask);
    Vue.nextTick(() => {
      el.instance.visible = flag;
    });
    el.domInserted = true;
  }
};

export default loadingDirective;
