<template>
  <div class="el-linkage-select">
    <div v-for="(item,index) in realLevel" :key="index" class="el-linkage-select-item" :style="itemStyle" >
      <label v-if="labels[index]" class="el-linkage-select-item__label" :ref="'label_'+index">{{labels[index]}}</label>
      <el-xselect class="el-linkage-select-item__select" :details="details" v-model="realValues[index]" :placeholder="placeholder[index]" :options="currentOption[index]" @change="changeHandle($event, index)" :props="selectProps" @visible-change="visibleChange" :style="selectStyle[index]" @clear="clearHandle($event, index)" :clearable="clearable" :disabled = "disabled" :filterable="filterable"></el-xselect>
    </div>
    <div style=" clear:both"></div>
  </div>
</template>
<script>
import ElXselect from '@/packages/xselect';
import Locale from '@/src/mixins/locale';
export default {
  name: 'ElLinkageSelect',
  xtype: 'YuLinkageSelect',
  mixins: [Locale],
  componentName: 'ElLinkageSelect',
  components: {
    ElXselect
  },
  props: {
    props: {
      type: Object,
      default: function() {
        return {key: 'key', value: 'value', children: 'children', parentId: 'parentId'};
      }
    },
    dataUrl: String,
    /** 请求类型 */
    requestType: {
      type: String,
      default: 'GET'
    },
    jsonData: {
      type: String,
      default: 'data'
    },
    options: {
      type: Array,
      default: function() {
        return [];
      }
    },
    level: {
      type: Number,
      required: true
    },
    placeholders: {
      type: Array,
      default: function() {
        return [];
      }
    },
    labels: {
      type: Array,
      default: function() {
        return [];
      }
    },
    value: {
      type: [Array, String],
      default: function() {
        return [];
      }
    },
    remoteParamName: {
      type: String,
      default: 'code'
    },
    required: Boolean,
    disabled: Boolean,
    dataStructure: {
      type: String,
      default: 'tile'
    },
    // 平铺模式时,根节点的父节点默认值
    rootValue: {
      type: [String, Number],
      default: ''
    },
    clearable: {
      type: Boolean,
      default: true
    },
    filterable: {
      type: Boolean,
      default: false
    },
    details: Boolean
  },
  data: function() {
    return {
      realOptions: [],
      placeholder: this.placeholders,
      currentOption: [],
      selectProps: {},
      realValues: Object.prototype.toString.call(this.value) === '[object String]' ? this.value.split(',') : this.value,
      selectFlag: false,
      selectStyle: [],
      // 强制调用change
      force: false,
      // 区分change事件触发的value watch 还是外部数据改变触发的value watch
      changeFlag: false,
      realLevel: this.level
    };
  },
  computed: {
    labelKey() {
      return this.props.key || 'key';
    },
    valueKey() {
      return this.props.value || 'value';
    },
    childrenKey() {
      return this.props.children || 'children';
    },
    parentKey() {
      return this.props.parentId || 'parentId';
    },
    // 去掉换行时的, 'margin-top': '1px' 。原因，在xform中会挤占下面的位置
    itemStyle() {
      return {'width': ((1 / this.level) * 100) + '%'};
    }
  },
  created: function() {
    if (this.level <= 0) {
      throw new Error(this.t('el.linkageselect.levelError'));
    }
    this.initData();
    this.selectProps = {'key': this.labelKey, 'value': this.valueKey};

  },
  mounted: function() {
    let refs = this.$refs;
    for (let i = 0; i < this.realLevel; i++) {
      let obj = refs['label_' + i];
      if (obj && obj[0]) {
        this.selectStyle.push({'width': 'calc(100% - ' + (obj[0].scrollWidth + 3) + 'px' + ')'});
      } else {
        this.selectStyle.push({'width': 'calc(100% - ' + '3px' + ')'});
      }
    }
  },
  methods: {
    initData: function() {
      if (this.options.length > 0) {
        // 如果是平铺数据
        if (this.dataStructure === 'tile') {
          this.realOptions = this.getJsonTree(this.options, this.rootValue);
        } else {
          this.realOptions = this.options;
        }
        this.currentOption.push(this.realOptions);
        if (this.realValues.length !== 0) {
          this.initOptions(this.realOptions, this.realValues[0], 0);
        }
      } else if (this.dataUrl) {
        // 第一个下拉框默认加载
        this.realOptions = this.query(this.rootValue);
        if (this.realOptions && this.realOptions.length > 0) {
          this.currentOption.push(this.realOptions);
        }
        for (let i = 1;i < this.realValues.length; i++) {
          this.realOptions = this.query(this.realValues[i - 1]);
          if (this.realOptions && this.realOptions.length > 0) {
            this.currentOption.push(this.realOptions);
          }
        }
      }
    },
    changeHandle: function(val, index) {
      this.changeFlag = true;
      // this.selectFlag 为false 表示不是点击产生的change事件，所以就返回
      if (!this.selectFlag && !this.force) {
        return ;
      }
      // 移除数组之后的所有项
      let afterIndex = this.currentOption.length - index;
      this.currentOption.splice(index + 1, afterIndex);
      this.realValues.splice(index + 1, afterIndex);
      let children = [];
      if (this.options && this.options.length > 0) {
        children = this.findChildren(this.currentOption[index], val);
      } else if (this.dataUrl) {
        children = this.query(val);
      }
      if (children && children.length > 0) {
        this.currentOption.push(children);
      }
      this.$emit('input', this.realValues);
      this.$emit('change', val, index);
    },
    clearHandle: function(val, index) {
      this.force = true;
      this.changeHandle(val, index);
      this.$emit('clear', val, index);
    },
    // 根据key 查询对应key中的children值
    findChildren: function(paraVal, key) {
      if (!key || !paraVal) {
        return ;
      }
      for (let i = 0; i < paraVal.length;i++) {
        if (paraVal[i][this.labelKey] === key) {
          return paraVal[i][this.childrenKey];
        }
      }
      return null;
    },
    // 后台查询数据
    query: function(key) {
      let _this = this;
      let keyV = _this.remoteParamName;
      let param = {};
      param[keyV] = key;
      let data = [];
      yufp.service.request({
        method: _this.requestType,
        name: _this.dataUrl,
        async: false,
        data: param,
        callback: function(code, message, response) {
          if (code === 0 && response) {
            data = _this.getObjectKey(response, _this.jsonData);
            _this.$emit('loaded', data, response);
          }
        }
      });
      return data;
    },
    getObjectKey: function(obj, ns) {
      if (!ns) {
        return obj;
      }
      var keys = ns.split('.');
      for (var i = 0, len = keys.length; i < len; i++) {
        if (!obj) {
          break;
        }
        obj = obj[keys[i]];
      }
      return obj;
    },
    initOptions: function(levelVal, key, i) {
      if (this.realValues.length !== 0) {
        let children = this.findChildren(levelVal, key);
        if (children) {
          this.currentOption.push(children);
          i++;
          this.initOptions(children, this.realValues[i], i);
        }
      }
    },
    visibleChange: function() {
      this.selectFlag = !this.selectFlag;
    },
    getJsonTree: function(data, parentId) {
      let itemArr = [];
      for (let i = 0;i < data.length; i++) {
        let node = data[i];
        if (node[this.parentKey] === parentId) {
          let newNode = {};
          newNode[this.labelKey] = node[this.labelKey];
          newNode[this.valueKey] = node[this.valueKey];
          let children = this.getJsonTree(data, node[this.labelKey]);
          // 没有子节点就无需添加
          if (children && children.length !== 0) {
            newNode[this.childrenKey] = children;
          }
          itemArr.push(newNode);
        }
      }
      return itemArr;
    }
  },
  watch: {
    value: function(val, oldVal) {
      this.realValues = val;
      this.force = false;
      // 只有赋值的时候val !== oldVal ，级联选择的时候都是相等的
      if (val !== oldVal) {
        this.currentOption = [];
        this.initData();
      }
    },
    options: function(val) {
      if (val.length > 0) {
        this.currentOption = [];
        // 如果是平铺数据
        if (this.dataStructure === 'tile') {
          this.realOptions = this.getJsonTree(val, this.rootValue);
        } else {
          this.realOptions = val;
        }
        this.currentOption.push(this.realOptions);
      }
    }
  }
};
</script>
