<template>
  <el-cascader
     v-model="selectedVal"
    :show-all-levels="showAllLevels"
    :options="optionsData"
    :disabled="disabled"
    :placeholder="placeholder"
    :filterable="filterable"
    :clearable="clearable"
    :size="size"
    @active-item-change="activeItemChange"
    @change="change"
    :change-on-select="changeOnSelect">
  </el-cascader>
</template>

<script>
import { t } from '@/src/locale';
export default {
  name: 'ElCascaderX',
  xtype: 'YuXcascader',

  props: {
    /** 数据配置选项 */
    props: {
      type: Object,
      default: function() {
        return { value: 'value', label: 'label', children: 'children' };
      }
    },
    /** 字典查询参数 */
    dataParams: {
      type: Object,
      default: function() {
        return {};
      }
    },
    /** 字典类型查询URL */
    dataUrl: String,
    /** 字典code */
    dataCode: String,
    /** 请求类型 */
    requestType: {
      type: String,
      default: 'GET'
    },
    jsonData: {
      type: String,
      default: 'data'
    },
    // 下述字段为el-cascader组件中部分属性，配置文档参见yuwp
    name: {
      type: String,
      default: 'el-cascader-x'
    },
    size: String,
    disabled: Boolean,
    clearable: {
      type: Boolean,
      default: true
    },
    filterable: {
      type: Boolean,
      default: true
    },
    placeholder: {
      type: String,
      default() {
        return t('el.xcascader.placeholder');
      }
    },
    options: {
      type: Array,
      default: function() {
        return [];
      }
    },
    showAllLevels: {
      type: Boolean,
      default: false
    },
    hangeOnSelect: {
      type: Boolean,
      default: true
    },
    value: {
      required: true
    },
    changeOnSelect: {
      type: Boolean,
      default: false
    }
  },

  data: function() {
    return {
      selectedVal: [],
      active: '',
      item: '',
      optionsData: this.options
    };
  },
  watch: {
    selectedVal: function(val) {
      this.$emit('input', val);
    },
    value: function(val) {
      if (this.selectedVal !== val) {
        this.selectedVal = val;
      }
    },
    dataUrl: function(url) {
      if (url) {
        this.query();
      }
    },
    options: function(data) {
      if (data) {
        this.optionsData = data;
      }
    },
    dataCode: function(code) {
      if (code) {
        this.setDateCode(code);
      }
    }
  },
  created: function() {
    if (this.dataUrl) {
      this.query();
    } else if (!this.dataUrl && this.dataCode) {
      this.setDateCode(this.dataCode);
    }
  },
  methods: {
    setDateCode: function(dataCode) {
      var _this = this;
      yufp.lookup.bind(dataCode, function(options) {
        var listData = [];
        for (var i = 0, len = options.length; i < len; i++) {
          var obj = options[i];
          listData.push({
            value: obj[_this.props.value],
            label: obj[_this.props.label]
          });
        }
        _this.optionsData = listData;
      });
    },
    change: function(val) {
      this.$emit('change', val);
    },
    activeItemChange: function(val) {
      this.$emit('active-item-change', val);
    },
    getSelectdText: function() {
      var lis = this.selectedVal;
      var res = [];
      for (var i = 0; i < this.optionsData.length; i++) {
        var value1 = this.optionsData[i][this.props.value];
        if (lis[0] === value1) {
          res[0] = this.optionsData[i][this.props.label];
          var child1 = this.optionsData[i][this.props.children];
          for (var j = 0; j < child1.length; j++) {
            var value2 = child1[j][this.props.value];
            if (lis[1] === value2) {
              res[1] = child1[j][this.props.label];
              var child2 = child1[j][this.props.children];
              for (var x = 0; x < child2.length; x++) {
                var value3 = child2[x][this.props.value];
                if (lis[2] === value3) {
                  res[2] = child2[x][this.props.label];
                }
              }
            }
          }
        }
      }
      return res;
    },
    getSelectdValue: function(value) {
      return this.selectedVal;
    },
    setSelectdByValue: function(list) {
      this.selectedVal = list;
    },
    clear: function() {
      this.selectedVal = [];
    },
    query: function(val, para) {
      var _this = this;
      if (!val) {
        _this.optionsData = [];
      }
      var params;
      if (para) {
        params = para;
      } else {
        params = _this.dataParams;
      }
      yufp.service.request({
        method: _this.requestType,
        name: _this.dataUrl,
        data: params,
        callback: function(code, message, response) {
          var data1 = _this.getObjectKey(response, _this.jsonData);
          data1 = data1 && data1.length > 0 ? data1 : [];
          if (data1.length > 0 && !val) {
            _this.optionsData = data1;
          } else if (data1.length > 0 && val) {
            if (val.length === 1) {
              for (var i = 0; i < _this.optionsData.length; i++) {
                var value1 = _this.optionsData[i][_this.props.value];
                if (val[0] === value1) {
                  _this.optionsData[i][_this.props.children] = data1;
                  break;
                }
              }
            } else if (val.length === 2) {
              for (var k = 0; k < _this.optionsData.length; k++) {
                var value2 = _this.optionsData[k][_this.props.value];
                if (val[0] === value2) {
                  var child_ = _this.optionsData[k][_this.props.children];
                  for (var j = 0; j < child_.length; j++) {
                    var value3 = child_[j][_this.props.value];
                    if (val[1] === value3) {
                      child_[j][_this.props.children] = data1;
                      break;
                    }
                  }
                }
              }
            }
          }
        }
      });
    },
    loadCascaderData: function(para) {
      if (para.dataUrl) {
        this.dataUrl = para.dataUrl;
      }
      this.query(para.value, para.para);
    },
    getObjectKey: function(obj, ns) {
      if (!ns) {
        return obj;
      }
      var keys = ns.split('.');
      for (var i = 0, len = keys.length; i < len; i++) {
        if (!obj) {
          break;
        }
        obj = obj[keys[i]];
      }
      return obj;
    }
  }
};
</script>

