<template>
  <transition name="el-message-fade">
    <div
      class="el-message"
      :class="customClass"
      v-show="visible"
      @mouseenter="clearTimer"
      @mouseleave="startTimer">
      <img class="el-message__img" :src="typeImg" alt="" v-if="!iconClass">
      <div class="el-message__group" :class="{ 'is-with-icon': iconClass }">
        <!-- <slot><p><i class="el-message__icon" :class="iconClass" v-if="iconClass"></i>{{ message }}</p></slot>
        <div v-if="showClose" class="el-message__closeBtn el-icon-close" @click="close"></div> -->
        <i class="el-message__icon" :class="iconClass" v-if="iconClass"></i>
        <slot>
          <p v-if="!dangerouslyUseHTMLString" class="el-message__content">{{ message }}</p>
          <p v-else v-html="message" class="el-message__content"></p>
        </slot>
        <i v-if="showClose" class="el-message__closeBtn el-icon-close" @click="close"></i>
      </div>
    </div>
  </transition>
</template>

<script type="text/babel">
  import info from '../assets/info.svg';
  import success from '../assets/success.svg';
  import warning from '../assets/warning.svg';
  import error from '../assets/error.svg';

  export default {
    data() {
      return {
        visible: false,
        message: '',
        duration: 3000,
        type: 'info',
        iconClass: '',
        customClass: '',
        onClose: null,
        showClose: true,
        closed: false,
        lineBreak: false,
        dangerouslyUseHTMLString: false,
        timer: null
      };
    },

    computed: {
      typeImg() {
        let img;
        switch (this.type) {
          case 'info':
            img = info;
            break;
          case 'success':
            img = success;
            break;
          case 'warning':
            img = warning;
            break;
          case 'error':
            img = error;
            break;
        }
        return img;
      }
    },

    watch: {
      closed(newVal) {
        if (newVal) {
          this.visible = false;
          this.$el.addEventListener('transitionend', this.destroyElement);
        }
      }
    },

    methods: {
      destroyElement() {
        this.$el.removeEventListener('transitionend', this.destroyElement);
        this.$destroy(true);
        this.$el.parentNode.removeChild(this.$el);
      },

      close() {
        this.closed = true;
        if (typeof this.onClose === 'function') {
          this.onClose(this);
        }
      },

      clearTimer() {
        clearTimeout(this.timer);
      },

      startTimer() {
        if (this.duration > 0) {
          this.timer = setTimeout(() => {
            if (!this.closed) {
              this.close();
            }
          }, this.duration);
        }
      }
    },
    created() {
      if (this.lineBreak) {
        const h = this.$createElement;
        const msg = this.message.split('\n');
        const tmpMsg = [];
        for (let i in msg) {
          tmpMsg.push(h('p', null, msg[i]));
        }
        this.message = h('div', null, tmpMsg);
      }
    },
    mounted() {
      this.startTimer();
    }
  };
</script>
