<template>
  <div class="el-combo-tree el-select">
            <el-popover
                ref="selectPopover"
                placement="bottom-start"
                trigger="click"
                v-model="visible"
                :visible-arrow="false">
                <el-tree-x
                    ref="selectTreeX"
                    @check-change="checkChangeFn"
                    @node-click="nodeClickFn"
                    @load-all-data="loaded"
                    :empty-text="emptyText"
                    :current-node-key="currentNodeKey"
                    :show-checkbox="multiple"
                    :node-key="dataId"
                    :indent="indent"
                    :async="async"
                    :root-visible="rootVisible"
                    :data-url="dataUrl"
                    :data-params="dataParams"
                    :json-data="jsonData"
                    :expand-on-click-node="false"
                    :request-type="requestType"
                    :data-id="dataId"
                    :data-label="dataLabel"
                    :data-pid="dataPid"
                    :data-root="dataRoot"
                    :lazy="lazy"
                    :local-data="localData"
                    :highlight-current="highlightCurrent"
                    :remote-param-name="remoteParamName"
                    :force-node-type = "forceNodeType"
                    >
                </el-tree-x>
        </el-popover>
        <el-input v-if="!disabled"
            :placeholder="placeholder"
            v-popover:selectPopover
            v-model="selectedLabel"
            :readonly="true"
            :details="details"
            :icon="iconClass"
            :disabled="disabled || details"
            :on-icon-click="handleIconClick"
            @mouseenter.native="inputHovering = true"
            @mouseleave.native="inputHovering = false">
        </el-input>
        <el-input v-else
            :placeholder="placeholder"
            v-model="selectedLabel"
            :readonly="true"
            :details="details"
            :icon="iconClass"
            :disabled="disabled || details"
            :on-icon-click="handleIconClick"
            @mouseenter.native="inputHovering = true"
            @mouseleave.native="inputHovering = false">
        </el-input>
    </div>
</template>

<script>
import { addClass, removeClass, hasClass } from '@/src/utils/dom';
import { getValueByPath } from '@/src/utils/util';
import { t } from '@/src/locale';
export default {
  name: 'ElComboTree',
  xtype: 'YuComboTree',

  props: {
    value: {
      required: true
    },
    placeholder: {
      type: String,
      default() {
        return t('el.combotree.placeholder');
      }
    },
    clearable: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    multiple: {
      type: Boolean,
      default: false
    },
    emptyText: String,
    requestType: String,
    lazy: Boolean,
    dataUrl: String,
    dataId: String,
    dataLabel: String,
    dataPid: String,
    dataRoot: [String, Object],
    async: Boolean,
    rootVisible: {
      type: Boolean,
      default: true
    },
    highlightCurrent: {
      type: Boolean,
      default: true
    },
    localData: Array,
    indent: Number,
    dataParams: Object,
    jsonData: {
      type: String,
      default: 'data'
    },
    remoteParamName: String,
    contentRemoteParamName: String,
    allNodeValue: Boolean,
    details: Boolean,
    forceNodeType: Function
  },
  data: function() {
    var selValue = this.multiple ? [] : '';
    selValue = this.value ? this.value : selValue;
    return {
      inputHovering: false,
      tempSelVal: selValue,
      selectedLabel: '',
      currentNodeKey: '',
      visible: false
    };
  },
  watch: {
    value: function(val) {
      if (val === undefined) {
        this.clear();
      }
      if (this.tempSelVal !== val) {
        this.tempSelVal = val;
        if (this.lazy === true && this.dataUrl) {
          this.remoteData(val);
        }
      }
    },
    tempSelVal: function(val) {
      this.setSelected();
      this.$emit('input', val);
      this.$emit('change', val);
    },
    visible: function(val) {
      if (!val) {
        this.handleIconHide();
      } else {
        this.handleIconShow();
      }
      this.$emit('visible-change', val);
    }
  },
  computed: {
    iconClass: function() {
      var criteria =
        this.clearable &&
        !this.disabled &&
        this.inputHovering &&
        this.value !== undefined &&
        (this.multiple ? this.value.length > 0 : this.value !== '');
      return criteria ? 'circle-close is-show-close' : 'caret-top';
    }
  },
  methods: {
    loaded: function() {
      this.setSelected();
    },
    setSelected: function() {
      if (this.multiple) {
        this.$refs.selectTreeX.setCheckedKeys(this.tempSelVal);
      } else {
        this.currentNodeKey = this.tempSelVal;
      }
      this.setSelectedLabel();
    },
    checkChangeFn: function(nodeData, checked, indeterminate) {
      // 底层控件选择事件触发
      if (this.multiple) {
        this.tempSelVal = this.$refs.selectTreeX.getCheckedKeys(true);
        this.setSelectedLabel();
      }
    },
    nodeClickFn: function(nodeData, node, self) {
      if (!this.multiple) {
        if (this.allNodeValue) {
          this.tempSelVal = nodeData[this.dataId];
          this.selectedLabel = nodeData[this.dataLabel];
          this.visible = false;
        } else if (node.isLeaf) {
          this.tempSelVal = nodeData[this.dataId];
          this.selectedLabel = nodeData[this.dataLabel];
          this.visible = false;
        }
      }
      this.$emit('node-click', nodeData, node, self);
    },
    handleIconClick: function(event) {
      event.stopPropagation();
      if (this.iconClass.indexOf('circle-close') > -1) {
        this.clear();
      } else {
        this.toggleMenu(event);
      }
    },
    toggleMenu: function() {
      if (!this.disabled) {
        this.visible = !this.visible;
      }
    },
    getSelectedObjs: function() {
      var _this = this;
      var resultObj;
      if (_this.multiple) {
        // 多选
        resultObj = _this.$refs.selectTreeX.getCheckedNodes(true);
      } else {
        // 单选
        resultObj = {};
        var treeData = _this.$refs.selectTreeX.orginalData;
        for (var i = 0; i < treeData.length; i++) {
          if (treeData[i][_this.dataId] === _this.tempSelVal) {
            resultObj = treeData[i];
            break;
          }
        }
      }
      return resultObj;
    },
    clear: function() {
      if (this.multiple) {
        this.tempSelVal = [];
      } else {
        this.tempSelVal = '';
      }
      this.selectedLabel = '';
      this.visible = false;
      this.$emit('clear');
    },
    setSelectedLabel: function() {
      this.$nextTick(function() {
        // 填充输入框内容
        if (this.multiple) {
          // 多选
          var checkedNodes = this.$refs.selectTreeX.getCheckedNodes(true);
          if (checkedNodes.length > 0) {
            var _this = this;
            this.selectedLabel = checkedNodes
              .reduce(function(arr, cur) {
                arr.push(cur[_this.dataLabel]);
                return arr;
              }, [])
              .join(',');
          } else {
            this.selectedLabel = '';
          }
        } else {
          if (this.tempSelVal === '') {
            this.selectedLabel = '';
            return;
          }
          // 单选：填充值前一定要保证树结构已经加载完成，即orginalData有值
          var allNode = this.$refs.selectTreeX.orginalData;
          for (var i = 0; i < allNode.length; i++) {
            if (allNode[i][this.dataId] === this.tempSelVal) {
              this.selectedLabel = allNode[i][this.dataLabel];
              break;
            }
          }
        }
      });
    },
    handleIconHide: function() {
      var icon = this.$el.querySelector('.el-input__icon');
      if (icon) {
        removeClass(icon, 'is-reverse');
      }
    },
    handleIconShow: function() {
      var icon = this.$el.querySelector('.el-input__icon');
      if (icon && !hasClass(icon, 'el-icon-circle-close')) {
        addClass(icon, 'is-reverse');
      }
    },
    /**
    *lazy模式时查询对应的文本框内容
    */
    remoteData: function(id) {
      var _this = this;
      var params = {};
      yufp.extend(params, _this.dataParams);
      var dataId = _this.contentRemoteParamName ? _this.contentRemoteParamName : _this.dataId;
      params[dataId] = id;
      yufp.service.request({
        url: _this.dataUrl,
        method: _this.requestType,
        data: params,
        async: _this.async,
        callback: function(code, message, response) {
          var data = getValueByPath(response, _this.jsonData) || [];
          // 返回结果是否为数组，数组就取第一项
          if (data instanceof Array) {
            _this.selectedLabel = data[0] && data[0][_this.dataLabel];
          } else {
            _this.selectedLabel = data[_this.dataLabel];
          }
        }
      });
    }
  },
  mounted: function() {
    this.setSelected();
  }
};
</script>

