<script>
import ElCarousel from '@/packages/carousel/src/main.vue';
import ElCarouselItem from '@/packages/carousel/src/item.vue';
import YuXsplitScreenItem from '@/packages/xsplit-screen/src/split-screen-item.vue';
import { getValueByPath } from '@/src/utils/util';
import deepmerge from 'deepmerge';

export default {
  name: 'YuXsplitScreen',
  xtype: 'YuXsplitScreen',

  componentName: 'YuXsplitScreen',

  component: {
    ElCarousel,
    ElCarouselItem,
    YuXsplitScreenItem
  },
  props: {
    dialogHeight: {
      type: String,
      default: '500px'
    },
    // 每屏面板布局方式（默认三列布局）
    column: {
      type: Number,
      default: 3
    },
    // 面板之间的间隔
    span: {
      type: Number,
      default: 20
    },
    // 分屏相关设置属性，是否轮播，轮播时间登，具体参考Carousel 组件。
    initialIndex: {
      type: Number,
      default: 0
    },
    height: String,
    trigger: {
      type: String,
      default: 'hover'
    },
    autoplay: {
      type: Boolean,
      default: true
    },
    interval: {
      type: Number,
      default: 3000
    },
    indicatorPosition: String,
    indicator: {
      type: Boolean,
      default: true
    },
    arrow: {
      type: String,
      default: 'hover'
    },
    type: String,
    // 业务相关属性

    // 静态分屏数据
    screenData: Array,
    // 查询分屏数据url
    screenDataUrl: String,
    // 请求方法
    requestType: {
      type: String,
      default: 'GET'
    },
    // 默认查询参数
    baseParam: Object,
    jsonData: {
      type: String,
      default: 'data'
    }
  },
  data: function() {
    return {
      template: [],
      dialogVisible: false,
      curItem: {}
    };
  },

  watch: {
    screenData: function(data) {
      var cloneTemplate = {};
      cloneTemplate = deepmerge(cloneTemplate, data, { clone: true });
      this.template = this.orderTemplateData(cloneTemplate);
    }
  },

  computed: {
    layout: function() {
      return {
        width: 'calc(100% / ' + this.column + ')',
        padding: '0' + this.span / 2 + 'px'
      };
    }
  },
  methods: {
    // 对模版数据进行排序
    orderTemplateData: function(template) {
      template.sort(this.compare('pageNo'));
      for (var i = 0, l = template.length; i < l; i++) {
        template[i].data.sort(this.compare('orders'));
      }
      return template;
    },
    // 查询模版数据
    getTemplateData: function() {
      var _this = this;
      var cloneTemplate = [];
      yufp.service.request({
        url: _this.screenDataUrl,
        data: _this.baseParam,
        method: _this.requestType,
        async: false,
        callback: function(code, message, response) {
          var templateArr = getValueByPath(response, _this.jsonData) || [];
          var map = {};
          var dest = [];
          for (var i = 0; i < templateArr.length; i++) {
            var ai = templateArr[i];
            if (!map[ai.pageNo]) {
              dest.push({
                pageNo: ai.pageNo,
                data: [ai]
              });
              map[ai.pageNo] = ai;
            } else {
              for (var j = 0; j < dest.length; j++) {
                var dj = dest[j];
                if (dj.pageNo === ai.pageNo) {
                  dj.data.push(ai);
                  break;
                }
              }
            }
          }
          if (!dest.length > 0) dest.push({data: [], pageNo: 0});
          _this.initGraphList = deepmerge(cloneTemplate, dest, { clone: true });
          _this.template = _this.orderTemplateData(dest);
          _this.$emit('load-end', _this.template);
        }
      });
    },
    // 用于数组排序的方法
    compare: function(prop) {
      return function(obj1, obj2) {
        var val1 = obj1[prop];
        var val2 = obj2[prop];
        if (!isNaN(Number(val1)) && !isNaN(Number(val2))) {
          val1 = Number(val1);
          val2 = Number(val2);
        }
        if (val1 < val2) {
          return -1;
        } else if (val1 > val2) {
          return 1;
        } else {
          return 0;
        }
      };
    },
    handleDelete: function(event, item, index, $index) {
      event.stopPropagation();
      this.template[$index].data.splice(index, 1);
      this.$emit('delete-end', item, index, $index);
    },
    handleMore: function(event, item, index, $index) {
      event.stopPropagation();
      this.$emit('handle-more', item, index, $index);
    },
    handleEnlarge: function(event, item, index, $index) {
      event.stopPropagation();
      this.curItem = item;
      this.$nextTick(function() {
        this.dialogVisible = true;
      });
    },
    handDropEnd: function(template) {
      this.$emit('drop-end', template);
    },
    change: function(index) {
      this.$emit('change', index);
    }
  },
  mounted: function() {
    for (var i = 0, l = this.template.length; i < l; i++) {
      var item = this.template[i];
      for (var j = 0, lh = item.data.length; j < lh; j++) {
        var subitem = item.data[j];
        if (subitem.type === 'router') {
          yufp.router.to(subitem.routerId, '', subitem.routerId);
        }
      }
    }
  },
  created: function() {
    if (this.screenDataUrl) {
      this.getTemplateData();
    } else {
      this.template = this.orderTemplateData(this.screenData);
    }
  },

  render: function(h) {
    var _this = this;
    var props = this.$options.propsData;
    return (
      <div>
        <ElCarousel {...{ props: props }} on-change={_this.change}>
          {
            _this.$slots.custom ? <ElCarouselItem>
              {_this.$slots.custom}
            </ElCarouselItem> : ''
          }
          {_this._l(_this.template, (items, $index) => (
            <ElCarouselItem class="drag-container">
              {_this._l(items.data, (item, index) => (
                <div class="drag-item" style={_this.layout}>
                  <div class="drag-box" style={{height: parseFloat(_this.height) / 2 - 40 + 'px'}}>
                    <div class="title">
                      <span>{item.graphName}</span>
                      <i
                        class="el-icon-d-arrow-right"
                        on-click={event =>
                          _this.handleMore(event, item, index, $index)
                        }
                      />
                      <i
                        class="el-icon-delete"
                        on-click={event =>
                          _this.handleDelete(event, item, index, $index)
                        }
                      />
                      <i
                        class="el-icon-yx-enlarge"
                        on-click={event =>
                          _this.handleEnlarge(event, item, index, $index)
                        }
                      />
                    </div>
                    {item.type === 'router' ? (
                      <YuXsplitScreenItem
                        {...{ props: item }}
                        id={item.routerId}
                      />
                    ) : (
                      <YuXsplitScreenItem {...{ props: item }} />
                    )}
                  </div>
                </div>
              ))}
            </ElCarouselItem>
          ))}
        </ElCarousel>
        <el-dialog-x
          height={_this.dialogHeight}
          title={_this.curItem.graphName}
          visible={_this.dialogVisible}
          {...{ on: { 'update:visible': val => (_this.dialogVisible = val) } }}
        >
          {_this.curItem.type === 'router' ? (
            <YuXsplitScreenItem
              {...{ props: _this.curItem }}
              id={_this.curItem.routerId}
            />
          ) : (
            <YuXsplitScreenItem {...{ props: _this.curItem }} />
          )}
        </el-dialog-x>
      </div>
    );
  }
};
</script>

