<script>
// YuXformAbstractItem 名字关联input.vue中dispatch名字，若要修改，请同步更改
import ElTooltip from '@/packages/tooltip';
import { getStyle } from '@/src/utils/dom';
import YuXformAbstractItem from './abstract-item.vue';
import YuXformItemPart from './form-item-part.vue';
// import { formatters } from '../../../src/utils/formatter';

/**
 * 业务表单-表单项（表单组子项）
 */
export default {
  name: 'YuXformItem',
  xtype: 'YuXformItem',

  componentName: 'YuXformItem',

  component: {
    YuXformAbstractItem,
    YuXformItemPart,
    ElTooltip
  },
  props: {
    valueType: {
      type: String,
      default: 'array'
    },
    options: Array,
    dataUrl: String,
    dataCode: String,
    // 表单字段初始值
    value: [String, Array, Boolean, Number],
    // 表单字段名称
    name: {
      type: String,
      required: true
    },
    hidden: Boolean,
    disabled: {
      type: Boolean,
      default: false
    },
    notice: [String, Boolean],
    noticeTitle: {
      type: String,
      default: ''
    },
    label: String,
    labelWidth: String,
    prop: String,
    required: Boolean,
    // 自定义校验
    rules: [Object, Array, String],
    error: String,
    validateStatus: String,
    showMessage: {
      type: Boolean,
      default: true
    },
    utrace: {
      type: Boolean,
      default: true
    },
    utraceMessage: {
      type: String,
      default: ''
    },
    // 添加模糊匹配模式
    fuzzyQuery: {
      type: String,
      default: ''
    },
    showOverflowTooltip: {
      type: Boolean
    },
    hiddenDelVal: Boolean
  },
  data: function() {
    return {
      formdata: {},
      // disabled: false,
      parent: null,
      formType: '',
      // tooltip是否禁用
      tooltipDisabled: true
    };
  },
  mounted: function() {
    this.$nextTick(function() {
      let parent = this.$parent;
      let parentName = parent.$options.componentName;
      while (parentName !== 'YuXform') {
        parent = parent.$parent;
        try {
          parentName = parent.$options.componentName;
        } catch (error) {
          parent = this;
          break;
        }
      }
      this.parent = parent;
      // this.disabled = parent.disabled;
      this.formdata = parent.formdata;
      this.$watch('parent.formdata', function(oldval, newval) {
        this.formdata = oldval;
      });
    });
  },
  methods: {
    handleItemMouseEnter(event) {
      // 根据文字是否溢出，控制tooltip是否展示
      // use range width instead of scrollWidth to determine whether the text is overflowing
      // to address a potential FireFox bug: https://bugzilla.mozilla.org/show_bug.cgi?id=1074543#c3
      const formContentEl = this.$refs.itmContentRef;
      const range = document.createRange();
      range.setStart(formContentEl, 0);
      range.setEnd(formContentEl, formContentEl.childNodes.length);
      const rangeWidth = range.getBoundingClientRect().width;
      const padding = (parseInt(getStyle(formContentEl, 'paddingLeft'), 10) || 0) +
        (parseInt(getStyle(formContentEl, 'paddingRight'), 10) || 0);
      if ((rangeWidth + padding > formContentEl.offsetWidth || formContentEl.scrollWidth > formContentEl.offsetWidth)) {
        this.tooltipDisabled = false;
      }
    }
  },
  render: function(h) {
    var _this = this;
    var data = _this.$vnode.data;
    var item = data.attrs.ctype;
    var name = this.$vnode.componentOptions.propsData.name;
    var hidden = _this._props.hidden;
    var propsData = {
      props: _this._props
    };
    if (this.parent) {
      propsData.props['utraced'] = this.parent.utrace === true ? this.utrace : false;
    }
    var disabled = this.parent
      ? this.parent.disabled ? true : _this.disabled
      : _this.disabled;
    var hiddenDelVal = this.parent
      ? this.parent.hiddenDelVal ? true : _this.hiddenDelVal
      : _this.hiddenDelVal;
    var itemData =
      item !== 'custom'
        ? _this.$vnode.data
        : { attrs: _this.$vnode.data.attrs };
    var listener = _this.$listeners || _this._events;
    delete listener.input;
    itemData.on = listener;
    // disabled = this.parent && this.parent.formType === 'details';
    var cur =
      this.parent && this.parent.formType === 'details' && this.$vnode.data.attrs.ctype === 'textarea' ? (
        this.showOverflowTooltip ? (
          <el-tooltip disabled={this.tooltipDisabled} effect="dark" placement="top" ref="tooltip" content={this.formdata[name]}>
            <div ref="itmContentRef" class="yu-xform-details__text-overfolw" on-mouseenter={($event) => this.handleItemMouseEnter($event)}>{this.formdata[name]}</div>
          </el-tooltip>
        ) : (
          <div>{this.formdata[name]}</div>
        )
      ) : (
        <YuXformItemPart
          v-model={this.formdata[name]}
          disabled={disabled}
          hiddenDelVal={hiddenDelVal}
          {...itemData}
          value-type={_this.valueType}
          options={_this.options}
          dataUrl={_this.dataUrl}
          dataCode={_this.dataCode}
          details={this.parent && this.parent.formType === 'details'}
        >
          {_this.$slots.default}
        </YuXformItemPart>
      );
    if (hidden && !this.hiddenDelVal) {
      return (
        <YuXformAbstractItem
          style={{ display: 'none' }}
          ctype={item}
          {...propsData}
        >
          {cur}
        </YuXformAbstractItem>
      );
    } else if (!hidden) {
      return (
        <YuXformAbstractItem
          ctype={item}
          {...propsData}
        >
          {cur}
        </YuXformAbstractItem>
      );
    } else if (hidden && this.hiddenDelVal) {
      return '';
    }
  }
};
</script>

