<template>
  <li class="el-menu-item"
    :draggable="rootMenu.draggable"
    :style="paddingStyle"
    @click="handleClick"
    @contextmenu="handleContextMenu"
    @mouseleave="handleMouseleave"
    @mouseup="handleMouseup"
    @dragend.stop="handleDragEnd"
    @dragover.stop="handleDragOver"
    @drop.stop="handleDrop"
    :class="{
      'is-active': active,
      'is-disabled': disabled
    }">
    <el-tooltip
      v-if="$parent === rootMenu && rootMenu.collapse"
      effect="dark"
      placement="right">
      <div slot="content"><slot name="title"></slot></div>
      <div style="position: absolute;left: 0;top: 0;height: 100%;width: 100%;display: inline-block;box-sizing: border-box;padding: 0 20px;">
        <slot></slot>
      </div>
    </el-tooltip>
    <template v-else>
      <slot></slot>
      <slot name="title"></slot>
      <!-- 右键菜单项，展示的列表 -->
      <template v-if="showMenuRightList">
        <ul v-show="shown" class="menu-right-list" :style="{top: menuRightListPos.top + 'px', left: menuRightListPos.left + 'px'}">
          <slot name="menuRightList">
            <li v-for="(d, index) in menuRightListData" :key="index" @click="listClcik(d.index, $event)">
              <i :class="d.icon"></i>
              <span>{{ d.title }}</span>
            </li>
          </slot>
        </ul>
      </template>
    </template>
  </li>
</template>
<script>
  import Menu from './menu-mixin';
  import ElTooltip from '@/packages/tooltip';
  import Emitter from '@/src/mixins/emitter';

  export default {
    name: 'ElMenuItem',
    xtype: 'YuMenuItem',

    componentName: 'ElMenuItem',

    components: { ElTooltip },

    mixins: [Menu, Emitter],

    data: function() {
      return {
        shown: false,
        menuRightListPos: {
          top: '',
          left: ''
        }
      };
    },

    props: {
      index: {
        type: String,
        required: true
      },
      route: {
        type: Object,
        required: false
      },
      // 菜单项数据对象
      menuItemData: {
        typIe: Object
      },
      disabled: {
        type: Boolean,
        required: false
      },
      // 右键菜单，展示的列表的数据
      menuRightListData: Array,
      // 在菜单项右键，是否允许展示列表
      showMenuRightList: Boolean
    },
    computed: {
      active() {
        return this.index === this.rootMenu.activeIndex;
      }
    },
    methods: {
      handleClick() {
        this.dispatch('ElMenu', 'item-click', this);
      },
      // 阻止浏览右键默认事件
      handleContextMenu(e) {
        if (!e) e = window.event;
        if (this.showMenuRightList) {
          e.preventDefault();
        }
      },
      // 判断是右击事件
      handleMouseup(e) {
        if (!e) e = window.event;
        if (e.button === 2 && this.showMenuRightList) {
          // 控制右键列表的位置
          this.menuRightListPos.left = e.offsetX;
          this.menuRightListPos.top = e.offsetY;
          this.shown = true;
          this.$emit('menu-right-click', this);
        }
      },
      /**
       * 鼠标从菜单项移开时，隐藏right-list
       */
      handleMouseleave() {
        this.shown = false;
      },
      /**
       * 右键列表项点击事件
       * @param index 列表项唯一标识
       * @param e 原生DOM事件对象
       */
      listClcik(index, e) {
        e = e || window.event;
        e.stopPropagation();
        this.$emit('menu-rightlist-click', index, this.menuItemData, e);
      },
      /**
       * 允许将数据/元素放置到其他元素中
       */
      handleDragOver(e) {
        if (!this.rootMenu.draggable) return;
        e = e || window.event;
        e.preventDefault();
      },
      /**
       * 触发el-menu组件实例'menu-item-drag-end'事件
       */
      handleDragEnd(e) {
        if (!this.rootMenu.draggable) return;
        e = e || window.event;
        this.rootMenu.$emit('menu-item-drag-end', this, e);
      },
      /**
       * 拖拽放置事件处理程序，阻止drop 事件的默认行为（默认以链接形式打开）
       */
      handleDrop(e) {
        if (!this.rootMenu.draggable) return;
        e = e || window.event;
        e.preventDefault();
      }

    },
    created() {
      this.parentMenu.addItem(this);
      this.rootMenu.addItem(this);
    },
    beforeDestroy() {
      this.parentMenu.removeItem(this);
      this.rootMenu.removeItem(this);
    }
  };
</script>
