<template>
  <div>
    <el-checkbox-group
      v-show="!details"
    v-model="checklist" 
    :min="min" 
    :max="max" 
    :name="name" 
    :value-type="valueType"
    @change="change">
    <el-checkbox 
      v-if="!optionButton" 
      v-for="item in checkdata" 
      :label="item.key" 
      :key="item.key" 
      :disabled="item.disabled === undefined ? disabled : item.disabled">
      {{item.value}}
    </el-checkbox>
    <el-checkbox-button 
      v-if="optionButton" 
      v-for="item in checkdata" 
      :label="item.key" 
      :key="item.key" 
      :disabled="item.disabled === undefined ? disabled : item.disabled">
      {{item.value}}
    </el-checkbox-button>
  </el-checkbox-group>
    <span v-show="details">{{getSelectdText().join(',')}}</span>
  </div>
</template>

<script>
import { looseEqual } from '@/src/utils/util';
export default {
  name: 'ElCheckboxX',
  xtype: 'YuXcheckbox',
  props: {
    min: Number,
    max: Number,
    disabled: Boolean,
    name: String,
    dataUrl: String,
    dataCode: String,
    /** 字典查询参数 */
    dataParams: {
      type: Object,
      default: function() {
        return {};
      }
    },
    /** 请求类型 */
    requestType: {
      type: String,
      default: 'GET'
    },
    jsonData: {
      type: String,
      default: 'data'
    },
    options: {
      type: Array,
      default: function() {
        return [];
      }
    },
    datacodeFilter: Function,
    value: [Array, String],
    // 选项是否为button，默认flase
    optionButton: Boolean,
    valueType: {
      type: String,
      default: 'array'
    },
    /** 字典类型 */
    props: {
      type: Object,
      default: function() {
        return { key: 'key', value: 'value' };
      }
    },
    separator: {
      type: String,
      default: ','
    },
    excludeKey: String,
    details: Boolean
  },
  data: function() {
    return {
      checklist: this.value ? (Object.prototype.toString.call(this.value) === '[object String]' ? this.value.split(this.separator) : this.value) : [],
      checkdata: this.options,
      parent: null
    };
  },
  created: function() {
    if (!this.dataUrl && !this.dataCode) {
      // 此处代码含义有谁明白？克隆么？
      // var listData = [];
      // for (var i = 0, len = this.checkdata.length; i < len; i++) {
      //   var obj = this.checkdata[i];
      //   listData.push({ value: obj.value, key: obj.key });
      // }
      // this.checkdata = listData;
      // 重新修改一下
      var listData = [];
      this.checkdata.forEach(item => {
        let op = { value: item.value, key: item.key };
        if (item.disabled !== undefined) {
          op['disabled'] = item.disabled;
        }
        listData.push(op);
      });
      this.checkdata = listData;
    } else if (!this.dataUrl && this.dataCode) {
      this.setcheckData(this.dataCode);
    } else {
      this.query();
    }
  },
  methods: {
    getCheckListOfString() {
      return this.checklist.join(this.separator);
    },
    change: function(val) {
      this.$emit('change', val);
    },
    setcheckData: function(dataCode) {
      var _this = this;
      var listData = [];
      yufp.lookup.bind(dataCode, function(options) {
        if (_this.datacodeFilter) {
          // 此处获取字段名称是不合理的，暂时先从父元素中获取
          listData = _this.datacodeFilter(options, _this.dataCode, _this.$parent && _this.$parent.name);
          _this.checkdata = _this.formatter(listData);
        } else {
          if (_this.excludeKey) {
            let array = _this.excludeKey.split(',');
            options = options.filter(function(item) {
              if (array.indexOf(item[_this.props.key]) > -1) {
                return false;
              }
              return true;
            });
          }
          _this.checkdata = _this.formatter(options);
        }
      });
    },
    formatter(option) {
      var listData = [];
      for (var i = 0, len = option.length; i < len; i++) {
        var item = {};
        var obj = option[i];
        item['key'] = obj[this.props.key];
        item['value'] = obj[this.props.value];
        if (obj.disabled) {
          item['disabled'] = obj.disabled;
        }
        listData.push(item);
      }
      return listData;
    },
    query: function() {
      var me = this;
      yufp.service.request({
        method: this.requestType,
        url: this.dataUrl,
        data: this.dataParams,
        callback: function(code, message, response) {
          var data = me.getObjectKey(response, me.jsonData) || [];
          data = data && data.length > 0 ? data : [];
          for (var i = 0; i < data.length; i++) {
            var obj = data[i];
            if (obj[me.props.key]) {
              me.checkdata.push({
                key: obj[me.props.key],
                value: obj[me.props.value]
              });
            }
          }
        }
      });
    },
    getObjectKey: function(obj, ns) {
      if (!ns) {
        return obj;
      }
      var keys = ns.split('.');
      for (var i = 0, len = keys.length; i < len; i++) {
        if (!obj) {
          break;
        }
        obj = obj[keys[i]];
      }
      return obj;
    },
    getSelectdText: function() {
      var text = [];
      for (var j = 0; j < this.checklist.length; j++) {
        for (var i = 0; i < this.checkdata.length; i++) {
          var obj = this.checkdata[i];
          if (obj.key === this.checklist[j]) {
            text.push(obj.value);
            break;
          }
        }
      }
      return text;
    },
    getVNodeParent() {
      let parent = this.$parent;
      let parentName = parent.$options.componentName;
      while (parent.$parent && parentName !== 'YuXformAbstractItem') {
        parent = parent.$parent;
        parentName = parent.$options.componentName;
      }
      this.parent = parent;
    },
    // 获取复选框值处理
    _getCheckList(val) {
      if (Object.prototype.toString.call(val) === '[object Array]') {
        if (!looseEqual(this.checklist, val)) {
          return val;
        } else {
          return this.checklist;
        }
      }
      // 初始化默认值
      if (Object.prototype.toString.call(val) === '[object String]') {
        if (this.checklist.join(this.separator) !== val) {
          return val.split(this.separator);
        } else {
          return this.checklist;
        }
      }
    }
  },
  watch: {
    dataCode: function(val) {
      this.setcheckData(val);
    },
    checklist: function(val) {
      if (val) {
        let returnVal = this.valueType === 'string' ? val.join(this.separator) : val;
        this.$emit('input', returnVal);
      } else {
        // 当为undefined 的时候重置数据
        this.checklist = [];
        let returnVal = this.valueType === 'string' ? [].join(this.separator) : [];
        this.$emit('input', returnVal);
      }
    },
    value: function(val) {
      this.checklist = this._getCheckList(val);
    },
    options: function(val) {
      this.checkdata = val;
    }
  },
  mounted: function() {
    // 动态表单赋初始值联动使用
    if (this.value && this.value !== [] && this.value.length > 0) {
      this.$nextTick(function() {
        this.$emit('change', this.value);
      });
    }
  }
};
</script>

