<script>
import ElInput from '../../input/src/input';
import ElRadioX from '../../xradio/src/xradio';
import ElCheckboxX from '../../xcheckbox/src/xcheckbox';
import ElSelectX from '../../xselect/src/xselect';
import ElSwitch from '../../switch/src/component.vue';
import ElTimePicker from '../../date-picker/src/picker/time-picker';
import ElTimeSelect from '../../date-picker/src/picker/time-select';
import ElDatePicker from '../../date-picker/src/picker/date-picker';

/**
 * 业务表单-表单子项辅助
 */
export default {
  functional: true,
  name: 'YuXformItemPart',
  xtype: 'YuXformItemPart',

  component: {
    ElInput,
    ElRadioX,
    ElCheckboxX,
    ElSelectX,
    ElSwitch,
    ElTimePicker,
    ElTimeSelect,
    ElDatePicker
  },
  render(createElement, context) {
    var renderComponent = function() {
      var type = context.data.attrs.ctype;
      if (!type || type === 'input') {
        return ElInput;
      } else if (type === 'textarea') {
        context.data.attrs.type = 'textarea';
        return ElInput;
      } else if (type === 'num') {
        context.data.attrs.type = 'num';
        return ElInput;
      } else if (type === 'switch') {
        return ElSwitch;
      } else if (type === 'select') {
        return ElSelectX;
      } else if (type === 'timepicker') {
        return ElTimePicker;
      } else if (type === 'timeselect') {
        return ElTimeSelect;
      } else if (type === 'datepicker') {
        return ElDatePicker;
      } else if (type === 'radio') {
        return ElRadioX;
      } else if (type === 'checkbox') {
        return ElCheckboxX;
      } else if (type === 'custom') {
        return 'div';
      } else {
        return type;
      }
    };
    return createElement(renderComponent(), context.data, context.children);
  }
};
</script>
