<template>
  <el-tree class="el-tree-x"
    ref="elTree"
    :style="styleObj"
    :node-key="nodeKey"
    :lazy="lazy"
    :load="load"
    :data="data"
    :draggable="draggable"
    :props="treeProps"
    :disabled="disabled"
    :render-content="renderContent"
    :render-custom-content="renderCustomContent"
    :highlight-current="highlightCurrent"
    :current-node-key="currentNodeKey"
    :default-expand-all="defaultExpandAll"
    :expand-on-click-node="expandOnClickNode"
    :auto-expand-parent="autoExpandParent"
    :default-expanded-keys="defaultExpandedKeys"
    :show-checkbox="showCheckbox"
    :check-strictly="checkStrictly"
    :default-checked-keys="defaultCheckedKeys"
    :filter-node-method="filterNodeMethod"
    :accordion="accordion"
    :allow-drag="allowDrag"
    :allow-drop="allowDrop"
    :empty-text="emptyText"
    :force-node-type = "forceNodeType"
    
    @node-click="nodeClick"
    @node-dbclick="nodeDbClick"
    @check-change="checkChange"
    @current-change="currentChange"
    @node-expand="nodeExpand"
    @node-collapse="nodeCollapse"
    @node-drag-start="nodeDragStart"
    @node-drag-enter="nodeDragEnter"
    @node-drag-leave="nodeDragLeave"
    @node-drag-over="nodeDragOver"
    @node-drag-end="nodeDragEnd"
    @node-drop="nodeDrop"
    @node-contextmenu="nodeContextmenu"
    >
  </el-tree>
</template>

<script>
  import {t} from '@/src/locale';
  import { getValueByPath, array2tree } from '@/src/utils/util';
  import deepmerge from 'deepmerge';

  export default {
    name: 'ElTreeX',
    xtype: 'YuXtree',

    props: {
      draggable: {
        type: Boolean,
        defaul: false
      },
      emptyText: {
        type: String,
        default() {
          return t('el.tree.emptyText');
        }
      },
      nodeKey: {
        type: String,
        default: 'id'
      },
      checkStrictly: Boolean,
      defaultExpandAll: Boolean,
      expandOnClickNode: {
        type: Boolean,
        default: true
      },
      autoExpandParent: {
        type: Boolean,
        default: true
      },
      defaultCheckedKeys: Array,
      defaultExpandedKeys: Array,
      renderContent: Function,
      showCheckbox: Boolean,
      props: {
        default() {
          return {
            children: 'children',
            label: 'label',
            icon: 'icon',
            disabled: 'disabled'
          };
        }
      },
      highlightCurrent: {
        type: Boolean,
        default: true
      },
      currentNodeKey: [String, Number],
      filterNodeMethod: Function,
      accordion: Boolean,
      indent: {
        type: Number,
        default: 16
      },

      disabled: Boolean,
      lazy: Boolean,
      dataUrl: String,
      dataId: {
        type: String,
        default: 'id'
      },
      dataLabel: {
        type: String,
        default: 'label'
      },
      dataPid: {
        type: String,
        default: 'pid'
      },
      dataLeaf: {
        type: String,
        default: 'ISLEAF'
      },
      dataRoot: [String, Object],
      height: {
        type: Number,
        default: 400
      },
      rootVisible: {
        type: Boolean,
        default: true
      },
      dataParams: {
        type: Object,
        default() {
          return {};
        }
      },
      requestType: {
        type: String,
        default: 'GET'
      },
      jsonData: {
        type: String,
        default: 'data'
      },
      localData: Array,
      // 是否异步请求
      async: {
        type: Boolean,
        defaul: true
      },
      allowDrag: Function,
      allowDrop: Function,
      renderCustomContent: Function,
      remoteParamName: String,
      forceNodeType: Function
    },

    data() {
      return {
        orginalData: [],
        data: []
      };
    },
    methods: {
      filter(value) {
        return this.$children[0].filter(value);
      },
      getCheckedNodes(leafOnly) {
        return this.$children[0].getCheckedNodes(leafOnly);
      },
      getCheckedKeys(leafOnly) {
        return this.$children[0].getCheckedKeys(leafOnly);
      },
      setCheckedNodes(nodes, leafOnly) {
        this.$children[0].setCheckedNodes(nodes, leafOnly);
      },
      setCheckedKeys(keys, leafOnly) {
        this.$children[0].setCheckedKeys(keys, leafOnly);
      },
      setChecked(data, checked, deep) {
        this.$children[0].setChecked(data, checked, deep);
      },
      getHalfCheckedNodes() {
        return this.$children[0].getHalfCheckedNodes();
      },
      getHalfCheckedKeys() {
        return this.$children[0].getHalfCheckedKeys();
      },
      getCurrentNode() {
        return this.$children[0].getCurrentNode();
      },
      getCurrentKey() {
        return this.$children[0].getCurrentKey();
      },
      setCurrentKey(key) {
        this.$children[0].setCurrentKey(key);
      },
      setCurrentNode(node) {
        this.$children[0].setCurrentNode(node);
      },
      getNode(data) {
        this.$children[0].getNode(data);
      },
      remove(data) {
        this.$children[0].remove(data);
      },
      append(data, parentNode) {
        this.$children[0].append(data, parentNode);
      },
      insertBefore(data, refNode) {
        this.$children[0].insertBefore(data, refNode);
      },
      insertAfter(data, refNode) {
        this.$children[0].insertAfter(data, refNode);
      },
      // 触发事件类型、私有方法
      nodeClick(nodeData, node, self) {
        this.$emit('node-click', nodeData, node, self);
      },
      nodeDbClick(nodeData, node, self) {
        this.$emit('node-dbclick', nodeData, node, self);
      },
      checkChange(nodeData, checked, indeterminate) {
        this.$emit('check-change', nodeData, checked, indeterminate);
      },
      currentChange(nodeData, node, self) {
        this.$emit('current-change', nodeData, node, self);
      },
      nodeExpand(nodeData, node, instance) {
        this.$emit('node-expand', nodeData, node, instance);
      },
      nodeCollapse(nodeData, node, self) {
        this.$emit('node-collapse', nodeData, node, self);
      },
      nodeDragStart(node, event) {
        this.$emit('node-drag-start', node, event);
      },
      nodeDragEnter(node, enode, event) {
        this.$emit('node-drag-start', node, enode, event);
      },
      nodeDragLeave(node, lnode, event) {
        this.$emit('node-drag-leave', node, lnode, event);
      },
      nodeDragOver(node, cnode, event) {
        this.$emit('node-drag-over', node, cnode, event);
      },
      nodeDragEnd(node, enode, position, event) {
        this.$emit('node-drag-end', node, enode, position, event);
      },
      nodeDrop(node, enode, position, event) {
        this.$emit('node-drop', node, enode, position, event);
      },
      nodeContextmenu(event, nodeData, node, instance) {
        event.stopPropagation();
        this.$emit('node-contextmenu', event, nodeData, node, instance);
      },
      load(node, resolve) {
        var _this = this;
        var params = {};
        yufp.extend(params, _this.dataParams);
        var dataId = _this.remoteParamName ? _this.remoteParamName : _this.dataId;
        var clickData = node.data;
        if (clickData[_this.dataId]) { // 非加载根节点
          params[dataId] = clickData[_this.dataId];
        } else { // 加载根节点,注若是懒加载，则此属性自动表示根节点父id值
          params[dataId] = _this.dataRoot;
        }
        yufp.service.request({
          url: _this.dataUrl,
          method: _this.requestType,
          data: params,
          callback: function(code, message, response) {
            var data = getValueByPath(response, _this.jsonData) || [];
            var nodeArray = [];
            for (var i = 0; i < data.length; i++) {
              data[i].id = data[i][_this.dataId];
              data[i].label = data[i][_this.dataLabel];
              data[i].pid = data[i][_this.dataPid];
              nodeArray.push(data[i]);
            }
            return resolve(nodeArray);
          }
        });
      },
      remoteData: function() {
        var _this = this;
        yufp.service.request({
          url: _this.dataUrl,
          method: _this.requestType,
          data: _this.dataParams,
          async: _this.async,
          callback: function(code, message, response) {
            var data = getValueByPath(response, _this.jsonData) || [];
            _this.orginalData = deepmerge([], data, { clone: true });
            var transData = _this.genTreeData(data);
            _this.data = _this.rootVisible ? transData : transData[0].children;
            _this.expandRootNode();
            _this.$emit('load-all-data', _this.orginalData, response);
            // Deprecated 下述事件名反对使用
            _this.$emit('get-tree-datas', _this.orginalData, response);
          }
        });
      },
      /**
       * 将数组数据转换为树结构数据
       */
      genTreeData(data) {
        var _this = this;
        var cloneData = deepmerge([], data, { clone: true });
        var attr = { data: cloneData, id: _this.dataId, label: _this.dataLabel, pid: _this.dataPid, root: _this.dataRoot };
        return [array2tree(attr)];
      },
      /**
       * 一级节点只有一个时，才默认展开一级节点
       */
      expandRootNode() {
        var _this = this;
        setTimeout(function() {
          var root = _this.$children[0].root;
          if (!_this.defaultExpandAll && root.childNodes.length === 1) {
            root.childNodes[0].expanded = true;
          }
        }, 1);
      }

    },
    mounted() {
      if (!this.lazy && !this.localData) {
        this.remoteData();
      }
      if (this.localData) {
        if (this.localData.length > 0) {
          var data = this.genTreeData(this.localData);
          this.orginalData = this.localData;
          this.data = this.rootVisible ? data : data[0].children;
        } else {
          this.data = [];
        }
        this.expandRootNode();
      }
    },
    computed: {
      styleObj() {
        return {
          height: (this.height - 2) + 'px',
          overflow: 'auto'
        };
      },
      treeProps() {
        return this.dataLeaf ? yufp.extend(this.props, {isLeaf: this.dataLeaf}) : this.props;
      }
    },
    watch: {
      dataUrl(val) {
        this.remoteData();
      },
      dataParams(val) {
        this.remoteData();
      },
      localData(val) {
        this.orginalData = val;
        var data = this.genTreeData(val);
        this.data = this.rootVisible ? data : data[0].children;
        this.expandRootNode();
      }
    }
  };
</script>
