<script>
import UploadDragger from './upload-dragger.vue';

export default {
  components: {
    UploadDragger
  },
  props: {
    type: String,
    data: {},
    action: {
      type: String,
      required: true
    },
    name: {
      type: String,
      default: 'file'
    },
    withCredentials: Boolean,
    accept: String,
    onStart: Function,
    onProgress: Function,
    onSuccess: Function,
    onError: Function,
    beforeUpload: Function,
    onPreview: {
      type: Function,
      default: function() {}
    },
    onRemove: {
      type: Function,
      default: function() {}
    },
    drag: Boolean,
    autoUpload: Boolean,
    listType: String,
    disabled: Boolean
  },

  data() {
    return {
      key: 0,
      mouseover: false,
      domain: '',
      file: null,
      submitting: false
    };
  },

  methods: {
    isImage(str) {
      return str.indexOf('image') !== -1;
    },
    handleClick() {
      // if (!this.disabled) {
      // IE9 不能通过程序触发文件选择框，否则form.submit()时会报拒绝访问
      // this.$refs.input.click();
      // }
    },
    handleChange(ev) {
      var file = ev.target.value;
      if (file) {
        if (typeof file === 'string') {
          var nameIdx = file.lastIndexOf('\\');
          var typeIdx = file.lastIndexOf('.');
          file = {
            name: file.substring(nameIdx !== -1 ? nameIdx + 1 : 0),
            type: typeIdx === -1 ? '' : file.substring(typeIdx + 1).toLowerCase(),
            size: 999,
            raw: file
          };
        }
        this.uploadFiles(file);
      }
    },
    uploadFiles(rawFile) {
      this.onStart(rawFile);
      if (this.autoUpload) this.upload(rawFile);
    },
    upload(rawFile) {
      if (!this.beforeUpload) {
        return this.post(rawFile);
      }
      const before = this.beforeUpload(rawFile);
      if (before !== false) {
        this.post(rawFile);
      } else {
        this.onRemove(null, rawFile);
      }
    },
    post(file) {
      const filepath = this.$refs.input.value;
      if (!filepath) {
        this.onRemove(null, file);
      }
      if (this.submitting) return;
      this.submitting = true;
      this.file = file;

      const formNode = this.getFormNode();
      const dataSpan = this.getFormDataNode();
      let data = this.data;
      if (typeof data === 'function') {
        data = data(file);
      }
      const inputs = [];
      for (const key in data) {
        if (data.hasOwnProperty(key)) {
          inputs.push(`<input name="${key}" value="${data[key]}"/>`);
        }
      }
      dataSpan.innerHTML = inputs.join('');
      formNode.submit();
      dataSpan.innerHTML = '';
    },
    abort(file) {
      // IE 9没有用Ajax FormData上传，因此无XHR取消
    },
    getFormNode() {
      return this.$refs.form;
    },
    getFormDataNode() {
      return this.$refs.data;
    },
    parseUrl(url) {
      var r = {
        protocol: /([^\/]+:)\/\/(.*)/i,
        host: /(^[^\:\/]+)((?:\/|:|$)?.*)/,
        port: /\:?([^\/]*)(\/?.*)/,
        pathname: /([^\?#]+)(\??[^#]*)(#?.*)/
      };
      var tmp = {};
      var res = {};
      res['href'] = url;
      for (var p in r) {
        tmp = r[p].exec(url);
        res[p] = tmp[1];
        url = tmp[2];
        if (url === '') {
          url = '/';
        }
        if (p === 'pathname') {
          res['pathname'] = tmp[1];
          res['search'] = tmp[2];
          res['hash'] = tmp[3];
        }
      }
      res['origin'] = res.protocol + '//' + res.host + (res.port ? ':' + res.port : '');
      return res;
    },
    /**
     * 上传后回调，不论成功失败
     */
    onload(e) {
      if (this.file == null) {
        // 默认就会加载iframe，尽管此时URL为空
        return;
      }
      this.$refs.input.value = null;
      this.key++;
      const win = this.$refs.iframe.contentWindow;
      var response = win.document.body.innerHTML;
      try {
        response = JSON.parse(response);
        this.onSuccess(response, this.file);
      } catch (e) {
        response = {};
        this.onError(response, this.file);
      }
      this.submitting = false;
      this.file = null;
    }
  },

  created() {
    this.frameName = 'frame-' + Date.now();
    this.inputId = 'input-' + Date.now();
  },

  mounted() {
  },

  render(h) {
    const {
      // drag,
      // uploadFiles,
      listType,
      frameName,
      inputId,
      disabled
    } = this;
    const oClass = { 'el-upload': true };
    oClass[`el-upload--${listType}`] = true;

    return (
      <div
        class={oClass}
        on-click={this.handleClick}
        nativeOn-drop={this.onDrop}
        nativeOn-dragover={this.handleDragover}
        nativeOn-dragleave={this.handleDragleave}
      >
        <iframe
          on-load={this.onload}
          ref="iframe"
          name={frameName}
        >
        </iframe>
        <form ref="form" action={this.action} target={frameName} enctype="multipart/form-data" method="POST">
          <label for={inputId}>
            {this.$slots.default}
          </label>
          <input
            key={this.key}
            class="el-upload__input"
            type="file"
            id={inputId}
            ref="input"
            name={this.name}
            on-change={this.handleChange}
            accept={this.accept}
            disabled={disabled}>
          </input>
          <input type="hidden" name="documentDomain" value={ this.$isServer ? '' : document.domain } />
          <span ref="data"></span>
        </form>
      </div>
    );
  }
};
</script>
