<template>
  <div class="el-input-num">
   <el-input v-bind="$attrs" v-on="$listeners" v-model="numValue" @blur="blurFn($event)" @input= "changeFn" @focus="focusFn($event)" :limit-char="varlimitChar"></el-input>
  </div>
</template>
<script>
  import ElInput from '@/packages/input';
  import { numberFormatter } from '@/src/utils/util';
  import * as math from 'mathjs';

  export default {
    name: 'ElNum',
    xtype: 'YuNum',
    components: {
      ElInput
    },
    props: {
      value: [String, Number],
      sign: {
        default: '%'
      },
      multiple: {
        type: Number,
        default: 100
      },
      precision: {
        type: Number,
        default: 2
      },
      numberFormatter: String,
      limitChar: RegExp
    },
    data() {
      return {
        numValue: (this.value === undefined || this.value === null) ? '' : this.value,
        inputFlag: false, // 用于判断value 变化的标志
        varlimitChar: this.limitChar,
        varPrecision: this.precision
      };
    },
    watch: {
      value: function(val) {
        if (!this.numberFormatter) {
          if (this.inputFlag) {
            this.inputFlag = false;
          } else {
            let currentVal ;
            if (val) {
              // 数字转换为字符串
              currentVal = val + '';
            } else {
              currentVal = val;
            }
            let num = '';
            if (this.isNumber(currentVal)) {
              num = Number(currentVal);
              this.numValue = this.getFixedData(this.formatPrecis(math.multiply(num, this.multiple))) + this.sign;
            } else {
              num = this.setValueFormat(currentVal);
              if (num) {
                this.numValue = Number(num) + this.sign;
              } else {
                this.numValue = num;
              }
            }
            // if (num) {
            //   this.$emit('change-num', Number(num));
            //   this.$emit('input', Number(num));
            // } else {
            //   this.$emit('change-num', '', '');
            //   this.$emit('input', '');
            // }
          }
        } else {
          if (this.inputFlag) {
            this.inputFlag = false;
            return;
          }
          if (this.isNumber(val)) {
            this.$emit('input', this.getFixedData(val));
            this.format(this.getFixedData(val));
          }
        }
      }
    },
    methods: {
      blurFn: function(event) {
        this.inputFlag = false;
        if (this.numberFormatter) {
          if (this.isNumber(this.numValue)) {
            this.format(Number(this.numValue));
            this.$emit('num-blur', event);
            this.$emit('input', this.getFixedData(this.value));
          } else {
            if (this.numValue) {
              this.$emit('input', this.numValue);
              this.inputFlag = false;
            } else {
              this.$emit('input', '');
            }
            this.$emit('num-blur', event);
          }
        } else {
          // 有值
          if ((this.numValue + '') !== undefined && (this.numValue + '').length > 0 && this.isNumber(this.numValue)) {
            let num = this.numValue;
            if (num) {
              num = this.getFixedData(num);
              this.numValue = num + this.sign;
            }
            this.$emit('num-blur', event);
            this.$emit('input', this.formatPrecis(math.divide(num, this.multiple)));
          } else {
            this.$emit('num-blur', event);
            this.$emit('input', this.numValue);
          }
        }
      },
      changeFn: function(val) {
        this.inputFlag = true;
        if (!this.numberFormatter) {
          if (val && this.isNumber(val)) {
            this.$emit('change-num', this.formatPrecis(math.divide(val, this.multiple)));
          } else {
            this.$emit('change-num', val);
          }
        } else {
          if (val && this.isNumber(val)) {
            this.$emit('change-num', this.getFixedData(this.value));
          } else {
            this.$emit('change-num', val);
          }
        }
      },
      focusFn: function(event) {
        if (this.numberFormatter) {
          this.numValue = this.value;
        } else {
          this.inputFlag = true;
          this.numValue = this.setSplitSign(this.numValue);
        }
        this.$emit('focus', event);
      },
      setValueFormat: function(val) {
        let formatVal = '';
        if (val) {
          val = this.setSplitSign(val);
          if (this.isNumber(val)) {
            formatVal = this.formatPrecis(math.multiply(val, this.multiple));
          } else {
            formatVal = val + '';
          }
        } else {
          formatVal = '';
        }
        return formatVal;
      },
      setSplitSign(val) {
        // 数据为字符串
        val = val + '';
        let idx = val.indexOf(this.sign);
        if (idx > -1) {
          return val.substr(0, idx);
        }
        return val;
      },
      isNumber: function(val) {
        var regPos = /^\d+(\.\d+)?$/; // 非负浮点数
        var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; // 负浮点数
        if (regPos.test(val) || regNeg.test(val)) {
          return true;
        } else {
          return false;
        }
      },
      format: function(val) {
        this.numValue = numberFormatter(Number(val), this.numberFormatter);
      },
      formatPrecis: function(val) {
        return Number(math.format(val, {precision: 14}));
      },
      // 获取格式化时，小数点后的长度，（通过逗号判断）
      getDot: function() {
        let dot = this.numberFormatter.split('.');
        if (dot[1]) {
          this.varPrecision = dot[1].length;
        } else {
          this.varPrecision = 0;
        }
      },
      getFixedData: function(val) {
        return Number(val).toFixed(this.precision);
      }
    },
    created: function() {
      if (!this.varlimitChar) {
        this.varlimitChar = /[^\u4e00-\u9fa5A-Za-z0-9\s\x21-\x2f\u2030\x3a-\x40\x5b-\x60\x7B-\x7F\（\）\《\》\、\——\；\，\。\·\“\”\<\>\！\？\：\【\】]{1,}/g;
      }
      if (!this.numberFormatter) {
        if (this.multiple <= 0 || this.multiple % 10 !== 0) {
          throw new Error('请输入正确的倍数值!');
        }
        // 单独初始化数据的时候需要格式处理
        if (this.value) {
          this.numValue = this.setValueFormat(this.value) + this.sign;
        }
      } else {
        this.getDot();
        this.format(this.value);
      }
      if (this.precision < 0 || this.precision % 1 !== 0 || this.precision >= 8) {
        throw new Error('请输入正确的小数位数!');
      }
    }
  };
</script>
