export default {
  computed: {
    indexPath() {
      var path = [this.index];
      var parent = this.$parent;
      while (parent.$options.componentName !== 'ElMenu') {
        if (parent.index) {
          path.unshift(parent.index);
        }
        parent = parent.$parent;
      }
      return path;
    },
    rootMenu() {
      var parent = this.$parent;
      while (
        parent &&
        parent.$options.componentName !== 'ElMenu'
      ) {
        parent = parent.$parent;
      }
      return parent;
    },
    parentMenu() {
      let parent = this.$parent;
      while (
        parent &&
        ['ElMenu', 'ElSubmenu'].indexOf(parent.$options.componentName) === -1
      ) {
        parent = parent.$parent;
      }
      return parent;
    },
    paddingStyle() {
      if (this.rootMenu.mode !== 'vertical') return {};

      let padding = 20;
      let parent = this.$parent;

      if (this.rootMenu.collapse) {
        padding = 20;
      } else {
        while (parent && parent.$options.componentName !== 'ElMenu') {
          if (parent.$options.componentName === 'ElSubmenu') {
            padding += 20;
          }
          parent = parent.$parent;
        }
      }
      return {paddingLeft: padding + 'px'};
    }
  },
  methods: {
    // 判断对象类型
    getType(obj) {
      var _type = typeof (obj);
      var tmpString;
      if (_type === 'object') {
        if (obj === null) {
          tmpString = 'null';
        } else {
          tmpString = Object.prototype.toString.call(obj).slice(8, -1);
        }
      }
      return tmpString.toLowerCase();
    },
    // 返回元素的宽高属性的数值形式，如 20
    getStyleNum(el, styleName) {
      return parseInt((this.getStyle(el, styleName)).replace(/px|pt|em/ig, ''), 10);
    },
    // 获取元素样式
    getStyle(el, styleName) {
      return el.style[styleName] ? el.style[styleName] : el.currentStyle ? el.currentStyle[styleName] : window.getComputedStyle(el, null)[styleName];
    },
    // 设置元素样式
    setStyle(el, obj) {
      if (this.getType(obj) === 'object') {
        for (var s in obj) {
          var cssArrt = s.split('-');
          for (var i = 1; i < cssArrt.length; i++) {
            cssArrt[i] = cssArrt[i].replace(cssArrt[i].charAt(0), cssArrt[i].charAt(0).toUpperCase());
          }
          var cssArrtnew = cssArrt.join('');
          el.style[cssArrtnew] = obj[s];
        }
      }
    },
    // 获取隐藏元素的宽高
    getSize(el) {
      var addCss = { display: '', position: 'absolute', visibility: 'hidden' };
      var oldCss = {};
      // 将当前样式存储起来
      for (var i in addCss) {
        oldCss[i] = this.getStyle(el, i);
      }
      this.setStyle(el, addCss);
      var _width = el.clientWidth || this.getStyleNum(el, 'width');
      var _height = el.clientHeight || this.getStyleNum(el, 'height');
      for (i in oldCss) {
        this.setStyle(el, oldCss);
      }
      return { width: _width, height: _height };
    },
    // 获得元素到浏览器顶部距离
    getOffsetTop(obj) {
      var tmp = obj.offsetTop;
      var val = obj.offsetParent;
      while (val != null) {
        tmp += val.offsetTop;
        val = val.offsetParent;
      }
      return tmp;
    },
    // 获得元素到浏览器左侧距离
    getOffsetLeft(obj) {
      var tmp = obj.offsetLeft;
      var val = obj.offsetParent;
      while (val != null) {
        tmp += val.offsetLeft;
        val = val.offsetParent;
      }
      return tmp;
    }
  }
};
