<script>
import YuEcharts from '../../echarts/src/main.vue';
import ElTableX from '../../xtable-x/src/xtable-x.js';
import ElInput from '../../input/src/input.vue';

/**
 * 分屏面板
 */
export default {
  functional: true,
  name: 'YuXsplitScreenItem',
  xtype: 'YuXsplitScreenItem',

  component: {
    YuEcharts,
    ElTableX,
    ElInput
  },
  render(createElement, context) {
    var renderComponent = function() {
      var type = context.data.props.graphType;
      if (type === 'pie' || type === 'line' || type === 'bar') {
        return 'yufp-echart';
      } else if (type === 'table') {
        return ElTableX;
      } else if (type === 'router') {
        return 'div';
      } else if (type === 'input') {
        return ElInput;
      } else {
        return type;
      }
    };
    return createElement(renderComponent(), context.data, context.children);
  }
};
</script>
