<template>
  <li ref="subMenu"
    :class="{
      'el-submenu': true,
      'is-active': active,
      'is-opened': opened
    }"
    @mouseenter="handleMouseenter"
    @mouseleave="handleMouseleave"
  >
    <div class="el-submenu__title" ref="submenu-title" @click="handleClick" :style="paddingStyle">
      <slot name="title"></slot>
      <i :class="{
        'el-submenu__icon-arrow': true,
        'el-icon-caret-bottom': rootMenu.mode === 'horizontal',
        'el-icon-arrow-down': rootMenu.mode === 'vertical' && !rootMenu.collapse,
        'el-icon-caret-right': rootMenu.mode === 'vertical' && rootMenu.collapse
      }">
      </i>
    </div>
    <template v-if="rootMenu.mode === 'horizontal' || (rootMenu.mode === 'vertical' && rootMenu.collapse)">
      <transition :name="menuTransitionName">
        <ul ref="menuList" class="el-menu" v-show="opened" :style="{top: menuUlPosition.top + 'px', left: menuUlPosition.left + 'px'}"><slot></slot></ul>
      </transition>
    </template>
    <el-collapse-transition v-else>
      <ul class="el-menu" v-show="opened"><slot></slot></ul>
    </el-collapse-transition>
  </li>
</template>
<script>
  import ElCollapseTransition from '@/src/transitions/collapse-transition';
  import menuMixin from './menu-mixin';
  import Emitter from '@/src/mixins/emitter';

  export default {
    name: 'ElSubmenu',
    xtype: 'YuSubmenu',

    componentName: 'ElSubmenu',

    mixins: [menuMixin, Emitter],

    components: { ElCollapseTransition },

    props: {
      index: {
        type: String,
        required: true
      }
    },

    data() {
      return {
        timeout: null,
        items: {},
        submenus: {},
        // 菜单的位置
        menuUlPosition: {
          top: '',
          left: ''
        },
        offset: {
          offsetTop: '',
          offsetLeft: ''
        }
      };
    },
    computed: {
      menuTransitionName() {
        return this.rootMenu.collapse ? 'el-zoom-in-left' : 'el-zoom-in-top';
      },
      opened() {
        return this.rootMenu.openedMenus.indexOf(this.index) > -1;
      },
      active: {
        cache: false,
        get() {
          let isActive = false;
          const submenus = this.submenus;
          const items = this.items;

          Object.keys(items).forEach(index => {
            if (items[index].active) {
              isActive = true;
            }
          });

          Object.keys(submenus).forEach(index => {
            if (submenus[index].active) {
              isActive = true;
            }
          });

          return isActive;
        }
      }
    },
    methods: {
      addItem(item) {
        this.$set(this.items, item.index, item);
      },
      removeItem(item) {
        delete this.items[item.index];
      },
      addSubmenu(item) {
        this.$set(this.submenus, item.index, item);
      },
      removeSubmenu(item) {
        delete this.submenus[item.index];
      },
      handleClick() {
        const {rootMenu} = this;
        if (
          (rootMenu.menuTrigger === 'hover' && rootMenu.mode === 'horizontal') ||
          (rootMenu.collapse && rootMenu.mode === 'vertical')
        ) {
          return;
        }
        this.dispatch('ElMenu', 'submenu-click', this);
      },
      handleMouseenter() {
        const {rootMenu} = this;
        if (rootMenu.mode === 'horizontal') {
          this.computePosition();
        }
        if (
          (rootMenu.menuTrigger === 'click' && rootMenu.mode === 'horizontal') ||
          (!rootMenu.collapse && rootMenu.mode === 'vertical')
        ) {
          return;
        }
        clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
          this.rootMenu.openMenu(this.index, this.indexPath);
        }, 300);
      },
      handleMouseleave() {
        const {rootMenu} = this;
        if (
          (rootMenu.menuTrigger === 'click' && rootMenu.mode === 'horizontal') ||
          (!rootMenu.collapse && rootMenu.mode === 'vertical')
        ) {
          return;
        }
        clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
          this.rootMenu.closeMenu(this.index);
        }, 300);
      },
      /**
       * created by taoting1 2018-9-26
       * 找到当前submenu到el-menu的层数
       * @param level 层数
       */
      getLevel() {
        var parent = this.$parent;
        var level = 0;
        while (
          parent &&
          parent.$options.componentName !== 'ElMenu'
        ) {
          if (parent.$options.componentName === 'ElSubmenu') { level++; }
          parent = parent.$parent;
        }
        return level;
      },
      /**
       * 计算ul的位置,使生成的菜单列表位置自适应页面
       */
      computePosition() {
        var distW;
        var subMenu = this.$refs.subMenu;
        // 当前subMenu到浏览器顶部的距离
        var offsetTop = this.getOffsetTop(subMenu);
        // 当前subMenu到浏览器左边的距离
        var offsetLeft = this.getOffsetLeft(subMenu);
        // 当前subMenu中的ul
        var menuList = this.$refs.menuList;
        // 获得当前subMenu中的ul的宽高属性
        var menuSize = this.getSize(menuList);
        // ul的宽度
        var menuWidth = menuSize.width;
        // ul的高度
        var menuHeight = menuSize.height;
        var distH = offsetTop + menuHeight - window.innerHeight;
        if (distH > 0) {
          this.menuUlPosition.top = 0 - distH;
        }
        if (this.getLevel() > 1) {
          var left = this.getStyle(this.parentMenu.$refs.menuList, 'left');
          var index = left.lastIndexOf('px');
          if (index !== -1) {
            left = left.slice(0, index);
          }
          this.menuUlPosition.left = left;
          return;
        }
        if (this.parentMenu.$options.componentName !== 'ElMenu') {
          distW = offsetLeft + menuWidth + this.$refs.subMenu.clientWidth - window.innerWidth;
        } else {
          distW = offsetLeft + menuWidth - window.innerWidth;
        }
        if (distW > 0) {
          if (this.parentMenu.$options.componentName !== 'ElMenu') {
            this.menuUlPosition.left = 0 - this.$refs.subMenu.clientWidth;
          } else {
            this.menuUlPosition.left = 0 - distW;
          }
        }
      }
    },
    created() {
      this.parentMenu.addSubmenu(this);
      this.rootMenu.addSubmenu(this);
    },
    beforeDestroy() {
      this.parentMenu.removeSubmenu(this);
      this.rootMenu.removeSubmenu(this);
    }
  };
</script>
