import { getCell, getColumnByCell, getRowIdentity } from './util';
import { getStyle, hasClass, addClass, removeClass } from '@/src/utils/dom';
import ElTooltip from '@/packages/tooltip';
import debounce from 'throttle-debounce/debounce';
import ElCheckbox from '@/packages/checkbox';
import AsyncValidator from 'async-validator';
import { validators } from '../../../src/utils/validator.js';
import { isDate } from '../../date-picker/src/util/index.js';
import LayoutObserver from './layout-observer';

export default {
  components: {
    ElCheckbox,
    ElTooltip,
    CustomContent: {
      props: {
        data: Object
      },
      render(h) {
        var parent = this.$parent.$parent;
        var index = this.data.index;
        var cellIndex = this.data.index;
        var row = this.data.row;
        var column = this.data.column;
        return (
          parent.renderCustomContent
            ? parent.renderCustomContent.call(parent._renderProxy, h, { column, row, index, cellIndex })
            : ''
        );
      }
    }
  },

  mixins: [LayoutObserver],

  props: {
    store: {
      required: true
    },
    stripe: Boolean,
    context: {},
    layout: {
      required: true
    },
    rowClassName: [String, Function],
    rowStyle: [Object, Function],
    fixed: String,
    rules: [Object, Array, String],
    highlight: Boolean
  },

  render(h) {
    const columnsHidden = this.columns.map((column, index) => this.isColumnHidden(index));
    return (
      <table
        class="el-table__body"
        cellspacing="0"
        cellpadding="0"
        border="0">
        <colgroup>
          {
            this._l(this.columns, column =>
              <col
                name={column.id}
                width={column.realWidth || column.width}
              />)
          }
        </colgroup>
        <tbody>
          {
            this._l(this.data, (row, $index) =>
              [<tr
                style={this.rowStyle ? this.getRowStyle(row, $index) : null}
                key={this.table.rowKey ? this.getKeyOfRow(row, $index) : $index}
                on-dblclick={($event) => this.handleDoubleClick($event, row)}
                on-click={($event) => this.handleClick($event, row, $index)}
                on-contextmenu={($event) => this.handleContextMenu($event, row, $index)}
                on-mouseenter={() => this.handleMouseEnter($index)}
                on-mouseleave={() => this.handleMouseLeave()}
                class={[this.getRowClass(row, $index)]}>
                {
                  this._l(this.columns, (column, cellIndex) => {
                    const { rowspan, colspan } = this.getSpan(row, column, $index, cellIndex);
                    if (!rowspan || !colspan) {
                      return '';
                    } else {
                      return (
                        <td
                          class={[column.id, column.align, column.className || '', columnsHidden[cellIndex] ? 'is-hidden' : '', { 'current-row-edit': column.ctype && this.type === 'edit' }, {'el-table__body__column__hidden': this.store.table.showHiddenMenu && column.hideColumn}]}
                          rowspan={rowspan}
                          colspan={colspan}
                          on-mouseenter={($event) => this.handleCellMouseEnter($event, row)}
                          on-click={($event) => this.cellClick($event, row, $index)}
                          on-contextmenu={($event) => this.cellHandleContextMenu($event, row, $index, cellIndex)}
                          on-mouseleave={this.handleCellMouseLeave}>
                          {
                            this.type === 'defalut'
                              ? column.renderCell.call(this._renderProxy, h, { row, column, $index, store: this.store, _self: this.context || this.table.$vnode.context }, columnsHidden[cellIndex])
                              : this.type === 'edit' && this.index === $index && column.ctype ? column.editRenderCell.renderCell.call(this._renderProxy, h, { row, column, $index, cellIndex, store: this.store, _self: this.context || this.table.$vnode.context }, columnsHidden[cellIndex])
                                : column.renderCell.call(this._renderProxy, h, { row, column, $index, store: this.store, _self: this.context || this.table.$vnode.context }, columnsHidden[cellIndex])
                          }
                          {
                            this.validateFieldsMessage && this.validateFieldsMessage[column.property] && this.index === $index ? <div style="color:red">{this.validateFieldsMessage[column.property][0].message}</div> : ''
                          }
                          {
                            this.show && this.$index === $index && this.cellIndex === cellIndex ? <custom-content data={{column: column, row: row, index: $index, cellIndex: cellIndex}}></custom-content> : ''
                          }
                        </td>
                      );
                    }
                  })
                }
              </tr>,
              this.store.states.expandRows.indexOf(row) > -1
                ? (<tr>
                  <td colspan={this.columns.length} class="el-table__expanded-cell">
                    {this.table.renderExpanded ? this.table.renderExpanded(h, { row, $index, store: this.store }) : ''}
                  </td>
                </tr>)
                : ''
              ]
            ).concat(
              this._self.$parent.$slots.append
            ).concat(
              <el-tooltip effect={this.table.tooltipEffect} placement="top" ref="tooltip" content={this.tooltipContent}></el-tooltip>
            )
          }
        </tbody>
      </table>
    );
  },

  watch: {
    'store.states.index'(newVal, oldVal) {
      this.type = 'edit';
      this.index = newVal;
    },
    'store.states.hoverRow'(newVal, oldVal) {
      if (!this.store.states.isComplex) return;
      const el = this.$el;
      if (!el) return;
      const rows = el.querySelectorAll('tbody > tr.el-table__row');
      const oldRow = rows[oldVal];
      const newRow = rows[newVal];
      if (oldRow) {
        removeClass(oldRow, 'hover-row');
      }
      if (newRow) {
        addClass(newRow, 'hover-row');
      }
    },
    'store.states.currentRow'(newVal, oldVal) {
      if (!this.highlight) return;
      const el = this.$el;
      if (!el) return;
      const data = this.store.states.data;
      const rows = el.querySelectorAll('tbody > tr.el-table__row');
      const oldRow = rows[data.indexOf(oldVal)];
      const newRow = rows[data.indexOf(newVal)];
      // 如果有新选择行，则删除所有的current-row（原情况如果是unshift 方式添加的会导致oldRow 索引计算错误，导致清除不了原选中的样式）
      if (newRow) {
        [].forEach.call(rows, row => removeClass(row, 'current-row'));
        addClass(newRow, 'current-row');
      } else {
        if (oldRow) {
          removeClass(oldRow, 'current-row');
        } else {
          [].forEach.call(rows, row => removeClass(row, 'current-row'));
        }
      }
    }
  },

  computed: {
    table() {
      return this.$parent;
    },

    data() {
      return this.store.states.data;
    },

    columnsCount() {
      return this.store.states.columns.length;
    },

    leftFixedCount() {
      return this.store.states.fixedColumns.length;
    },

    rightFixedCount() {
      return this.store.states.rightFixedColumns.length;
    },

    columns() {
      return this.store.states.columns;
    }
  },

  data() {
    return {
      tooltipContent: '',
      type: 'default',
      index: null,
      $index: null,
      cellIndex: null,
      validateState: 'success',
      validateMessage: null,
      validateFieldsMessage: null,
      show: false
    };
  },

  created() {
    this.activateTooltip = debounce(50, tooltip => tooltip.handleShowPopper());
  },

  methods: {
    getKeyOfRow(row, index) {
      const rowKey = this.table.rowKey;
      if (rowKey) {
        return getRowIdentity(row, rowKey);
      }
      return index;
    },

    isColumnHidden(index) {
      if (this.fixed === true || this.fixed === 'left') {
        return index >= this.leftFixedCount;
      } else if (this.fixed === 'right') {
        return index < this.columnsCount - this.rightFixedCount;
      } else {
        return (index < this.leftFixedCount) || (index >= this.columnsCount - this.rightFixedCount);
      }
    },

    getSpan(row, column, rowIndex, columnIndex) {
      let rowspan = 1;
      let colspan = 1;

      const fn = this.table.spanMethod;
      if (typeof fn === 'function') {
        const result = fn({
          row,
          column,
          rowIndex,
          columnIndex
        });

        if (Array.isArray(result)) {
          rowspan = result[0];
          colspan = result[1];
        } else if (typeof result === 'object') {
          rowspan = result.rowspan;
          colspan = result.colspan;
        }
      }

      return {
        rowspan,
        colspan
      };
    },

    getRowStyle(row, index) {
      const rowStyle = this.rowStyle;
      if (typeof rowStyle === 'function') {
        return rowStyle.call(null, row, index);
      }
      return rowStyle;
    },

    getRowClass(row, index) {
      const classes = ['el-table__row'];

      if (this.stripe && index % 2 === 1) {
        classes.push('el-table__row--striped');
      }
      const rowClassName = this.rowClassName;
      if (typeof rowClassName === 'string') {
        classes.push(rowClassName);
      } else if (typeof rowClassName === 'function') {
        classes.push(rowClassName.call(null, row, index) || '');
      }

      return classes.join(' ');
    },

    handleCellMouseEnter(event, row) {
      const table = this.table;
      const cell = getCell(event);
      if (cell) {
        const column = getColumnByCell(table, cell);
        const hoverState = table.hoverState = { cell, column, row };
        table.$emit('cell-mouse-enter', hoverState.row, hoverState.column, hoverState.cell, event);
      }

      // 判断是否text-overflow, 如果是就显示tooltip
      const cellChild = event.target.querySelector('.cell');
      if (!(hasClass(cellChild, 'el-tooltip') && cellChild.childNodes.length)) {
        return;
      }
      // use range width instead of scrollWidth to determine whether the text is overflowing
      // to address a potential FireFox bug: https://bugzilla.mozilla.org/show_bug.cgi?id=1074543#c3
      const range = document.createRange();
      range.setStart(cellChild, 0);
      range.setEnd(cellChild, cellChild.childNodes.length);
      const rangeWidth = range.getBoundingClientRect().width;
      const padding = (parseInt(getStyle(cellChild, 'paddingLeft'), 10) || 0) +
        (parseInt(getStyle(cellChild, 'paddingRight'), 10) || 0);
      if ((rangeWidth + padding > cellChild.offsetWidth || cellChild.scrollWidth > cellChild.offsetWidth) && this.$refs.tooltip) {
        const tooltip = this.$refs.tooltip;
        // TODO 会引起整个 Table 的重新渲染，需要优化
        this.tooltipContent = cell.innerText || cell.textContent;
        tooltip.referenceElm = cell;
        tooltip.$refs.popper && (tooltip.$refs.popper.style.display = 'none');
        tooltip.doDestroy();
        tooltip.setExpectedState(true);
        this.activateTooltip(tooltip);
      }
    },

    handleCellMouseLeave(event) {
      this.show = false;
      const tooltip = this.$refs.tooltip;
      if (tooltip) {
        tooltip.setExpectedState(false);
        tooltip.handleClosePopper();
      }
      const cell = getCell(event);
      if (!cell) return;

      const oldHoverState = this.table.hoverState;
      if (oldHoverState) {
        this.table.$emit('cell-mouse-leave', oldHoverState.row, oldHoverState.column, oldHoverState.cell, event);
      }
    },

    handleMouseEnter(index) {
      this.store.commit('setHoverRow', index);
    },

    handleMouseLeave() {
      // this.index = null;
      this.store.commit('setHoverRow', null);
    },
    cellHandleContextMenu(event, row, index, cellIndex) {
      this.show = false;
      this.cellIndex = cellIndex;
      this.$index = index;
      if (this.table.renderCustomContent) {
        event.stopPropagation();
        event.preventDefault();
        this.show = true;
      }
      this.handleEvent(event, row, 'cellcontextmenu');
    },
    handleContextMenu(event, row) {
      this.handleEvent(event, row, 'contextmenu');
    },

    handleDoubleClick(event, row) {
      this.handleEvent(event, row, 'dblclick');
    },

    handleClick(event, row, index) {
      var oldRow = this.store.states.currentEditRow;
      var oldIndex = this.store.states.index;
      if (oldRow && oldRow !== row) {
        if (!this.validate(oldRow)) {
          this.type = 'defalut';
          this.store.commit('setCurrentRow', oldRow, oldIndex);
          this.store.commit('setCheckIndex', oldRow, oldIndex);
          this.store.states.currentEditRow = oldRow;
          this.store.states.index = oldIndex;
          this.index = oldIndex;
          this.type = 'edit';
          return;
        }
      }
      this.type = 'edit';
      this.store.states.currentEditRow = row;
      this.store.states.index = index;
      this.store.commit('setCurrentRow', row, index);
      this.store.commit('setCheckIndex', row, index);
      this.handleEvent(event, row, 'click', index);
    },

    cellClick(event, row, index) {
      this.index = index;
    },

    handleEvent(event, row, name, index) {
      const table = this.table;
      const cell = getCell(event);
      let column;
      if (cell) {
        column = getColumnByCell(table, cell);
        if (column) {
          table.$emit(`cell-${name}`, row, column, cell, event);
        }
      }
      table.$emit(`row-${name}`, row, event, column, index);
    },

    handleExpandClick(row) {
      this.store.commit('toggleRowExpanded', row);
    },
    getRule: function(str) {
      for (const key in validators) {
        if (key === str) {
          return validators[key];
        }
      }
    },
    /**
     *
     * @param {*} row
     * @param {*} callback
     * @param {*} flag  是否需要在处理当前行的编辑状态
     */
    validate(row, callback, flag) {
      // 融合table 上的rules和column上的rules
      var tableRules = this.store.states.rules;
      var rules = {};
      for (var i = 0; i < this.columns.length; i++) {
        var element = this.columns[i];
        // 没有属性名退出
        if (!element.property) {
          continue;
        }
        var type = typeof (element.rules);
        var selfRules = type !== 'string' ? element.rules : this.getRule(element.rules);
        var tableRule = tableRules ? tableRules[element.property] : [];
        var tmpRule = [].concat(selfRules || tableRule || []);

        if (tmpRule.length > 0) {
          var tableValue = element.property;
          // 如果校验类型（type） 是date 且数据不是date 类型时，对数据进行转换校验
          if (row) {
            for (let i = 0; i < tmpRule.length; i++) {
              if (tmpRule[i].type === 'date' && isDate(row[tableValue]) && typeof (row[tableValue]) === 'string') {
                row[tableValue] = new Date(row[tableValue]);
                break;
              }
            }
          }
          rules[tableValue] = tmpRule;
        }
      }
      if (JSON.stringify(rules) === '{}' || rules.length === 0) {
        if (callback) {
          // 增加返回值 20191031
          callback(null, true);
        }
        return true;
      }
      if (!this.store.states.currentRow) {
        if (callback) {
          // 增加返回值 20191031
          callback(null, true);
        }
        return true;
      }
      var validator = new AsyncValidator(rules);
      var model = row;
      var result = true;
      validator.validate(model, { firstFields: true }, (errors, fields) => {
        this.validateState = !errors ? 'success' : 'error';
        this.validateMessage = errors ? errors[0].message : '';
        this.validateFieldsMessage = fields ? fields : '';
        if (this.validateState === 'success') {
          if (flag) {
            this.type = 'default';
          }
          result = true;
          if (callback) {
            callback(fields, result);
          }
        } else {
          result = false;
          if (callback) {
            callback(fields, result);
          }
        }
      });
      return result;
    },
    /**
     * 强制刷新表格的列元素
     * column 中包括columnId(单元格的td 的class 名称)，separator（分隔符）,dataCode（数据字典项）
     */
    forceUpdate: function(column) {
      var cell = this.$el.getElementsByClassName(column.columnId);
      for (var i = 0; i < cell.length; i++) {
        var element = cell[i];
        // 获取未转义的cell的值
        var value = element.innerText && element.innerText.trim();
        // 转义对应的编码
        if (value instanceof Array) {
          value = yufp.lookup.convertMultiKey(column.dataCode, value.join(','));
        } else if (typeof value === 'string' && value.indexOf(column.separator) > -1) {
          value = yufp.lookup.convertMultiKey(column.dataCode, value, column.separator);
        } else {
          value = yufp.lookup.convertKey(column.dataCode, value);
        }
        // 重新显示单元格中内容
        element.innerHTML = '<div class="cell">' + value + '</div>';
      }
    }
  }
};
