<template>
  <div class="el-form-item" :class="{
    'is-error': validateState === 'error',
    'is-validating': validateState === 'validating',
    'is-required': isRequired || required
  }">
    <label :for="name" class="el-form-item__label" v-bind:style="labelStyle" v-if="label || $slots.label">
      <slot name="label">{{label + form.labelSuffix}}</slot>
    </label>
    <div class="el-form-item__content" v-bind:style="contentStyle">
      <slot></slot>
      
      <transition name="el-zoom-in-top">
        <div class="el-form-item__error" v-if="validateState === 'error' && showMessage && form.showMessage">{{validateMessage}}</div>
      </transition>
      <yu-popover
        ref="popover1"
        placement="top-start"
        :title="noticeTitle"
        width="200"
        trigger="hover"
        :content="noticeStr">
      </yu-popover>
      <i v-show="notice && noticeStr" v-popover:popover1 :class="['el-icon-' + noticeIcon, 'xform-tooltip-icon']" style="font-size: 14px;position: absolute;z-index: 9;color: rgb(32, 160, 255);right: -4px;top:11px;"></i>
      <!-- <yu-tooltip v-if="notice && noticeStr" :content="noticeStr" placement="top-start">
        <i :class="['el-icon-' + noticeIcon, 'xform-tooltip-icon']" :style="noticeIconStyle"></i>
      </yu-tooltip> -->
      <i class="el-utrace" v-if="utrace && message.length>0" :title="form.showUtitleMessage ? message : ''" @click="btnUTraceListFn"></i>
    </div>
  </div>
</template>

<script>
import AsyncValidator from 'async-validator';
import emitter from '@/src/mixins/emitter';
import {validators} from '../../../src/utils/validator.js';
import { isDate } from '../../date-picker/src/util/index.js';

function noop() {}

function getPropByPath(obj, path) {
  let tempObj = obj;
  path = path.replace(/\[(\w+)\]/g, '.$1');
  path = path.replace(/^\./, '');

  let keyArr = path.split('.');
  let i = 0;

  for (let len = keyArr.length; i < len - 1; ++i) {
    let key = keyArr[i];
    if (key in tempObj) {
      tempObj = tempObj[key];
    } else {
      throw new Error('please transfer a valid name path to form item!');
    }
  }
  return {
    o: tempObj,
    k: keyArr[i],
    v: tempObj[keyArr[i]]
  };
}

/**
 * 业务表单-抽像表单子项（表单组子项）
 */
export default {
  name: 'YuXformAbstractItem',
  xtype: 'YuXformAbstractItem',

  componentName: 'YuXformAbstractItem',

  mixins: [emitter],

  props: {
    notice: [String, Boolean],
    noticeTitle: {
      type: String,
      default: ''
    },
    noticeIcon: {
      type: String,
      default: 'information'
    },
    // noticeIconStyle: {
    //   type: String,
    //   default: 'color: rgb(32, 160, 255);right: -4px;top:11px;'
    // },
    ctype: String,
    label: String,
    labelWidth: String,
    name: String,
    required: Boolean,
    rules: [Object, Array, String],
    error: String,
    validateStatus: String,
    showMessage: {
      type: Boolean,
      default: true
    },
    utrace: {
      type: Boolean,
      default: true
    },
    utraceMessage: {
      type: String,
      default: ''
    },
    // 添加模糊匹配模式
    fuzzyQuery: {
      type: String,
      default: ''
    }
  },
  watch: {
    error(value) {
      this.validateMessage = value;
      this.validateState = value ? 'error' : '';
    },
    validateStatus(value) {
      this.validateState = value;
    }
  },
  computed: {
    labelStyle() {
      var ret = {};
      if (this.form.labelPosition === 'top') return ret;
      var labelWidth = this.labelWidth || this.form.labelWidth;
      if (labelWidth) {
        ret.width = labelWidth;
      }
      return ret;
    },
    contentStyle() {
      var ret = {};
      const label = this.label;
      if (this.form.labelPosition === 'top' || this.form.inline) return ret;
      if (!label && !this.labelWidth && this.isNested) return ret;
      var labelWidth = this.labelWidth || this.form.labelWidth;
      if (labelWidth) {
        ret.marginLeft = labelWidth;
      }
      return ret;
    },
    form() {
      let parent = this.$parent;
      let parentName = parent.$options.componentName;
      while (parentName !== 'YuXform') {
        if (parentName === 'YuXformItem') {
          this.isNested = true;
        }
        parent = parent.$parent;
        try {
          parentName = parent.$options.componentName;
        } catch (error) {
          parent = this.$parent;
          break;
        }
      }
      return parent;
    },
    fieldValue: {
      cache: false,
      get() {
        var model = this.form.formdata;
        if (!model || !this.name) {
          return;
        }

        var path = this.name;
        if (path.indexOf(':') !== -1) {
          path = path.replace(/:/, '.');
        }

        return getPropByPath(model, path).v;
      }
    },
    isRequired() {
      let rules = this.getRules();
      let isRequired = false;

      if (rules && rules.length) {
        rules.every(rule => {
          if (rule.required) {
            isRequired = true;
            return false;
          }
          return true;
        });
      }
      return isRequired;
    }
  },
  data() {
    return {
      validateState: '',
      validateMessage: '',
      validateDisabled: false,
      validator: {},
      isNested: false,
      message: this.utraceMessage,
      noticeStr: Object.prototype.toString.call(this.notice) === '[object String]' ? this.notice : ''
    };
  },
  methods: {
    validate(trigger, callback = noop) {
      this.validateDisabled = false;
      var rules = this.getFilteredRule(trigger);
      if (
        (!rules || rules.length === 0) &&
        !this._props.hasOwnProperty('required')
      ) {
        callback();
        return true;
      }
      this.validateState = 'validating';
      var descriptor = {};
      descriptor[this.name] = rules;
      var validator = new AsyncValidator(descriptor);
      var model = {};
      model[this.name] = this.fieldValue;
      // 如果校验类型（type） 是date 且数据不是date 类型时，对数据进行转换校验
      for (let i = 0;i < rules.length; i++) {
        if (rules[i].type === 'date' && isDate(this.fieldValue) && typeof (this.fieldValue) === 'string') {
          model[this.name] = new Date(this.fieldValue);
          break;
        }
      }
      validator.validate(model, { firstFields: true }, (errors, fields) => {
        this.validateState = !errors ? 'success' : 'error';
        this.validateMessage = errors ? errors[0].message : '';

        callback(this.validateMessage);
      });
    },
    resetField() {
      this.validateState = '';
      this.validateMessage = '';

      let model = this.form.formdata;
      let value = this.fieldValue;
      let path = this.name;
      if (path.indexOf(':') !== -1) {
        path = path.replace(/:/, '.');
      }

      let prop = getPropByPath(model, path);

      if (Array.isArray(value)) {
        this.validateDisabled = true;
        prop.o[prop.k] = [];
        // prop.o[prop.k] = [].concat(this.initialValue);
      } else {
        this.validateDisabled = true;
        prop.o[prop.k] = this.initialValue;
      }
    },
    getRule: function(rule) {
      var _this = this;
      var selfrule = rule || _this.rules;
      for (const key in validators) {
        if (key === selfrule) {
          return validators[key];
        }
      }
    },
    getRules() {
      var formRules = this.form.rules;
      var type = typeof (this.rules);
      var rule = this.ctype === 'num'
        ? [].concat(this.getRule('number')).concat(this.getRule() ? this.getRule() : [])
        : this.getRule();
      var selfRules = type !== 'string' && this.rules ? this.rules : rule;
      var requiredRule = this._props.hasOwnProperty('required')
        ? { required: !!this.required || this.rules === 'required' }
        : [];

      formRules = formRules && formRules[this.name] || [];

      return [].concat(selfRules || []).concat(requiredRule).concat(formRules);
    },
    getFilteredRule(trigger) {
      var rules = this.getRules();

      return rules.filter(rule => {
        return !rule.trigger || rule.trigger.indexOf(trigger) !== -1;
      });
    },
    onFieldBlur() {
      this.validate('blur');
    },
    onFieldChange() {
      if (this.validateDisabled) {
        this.validateDisabled = false;
        return;
      }
      if (this.notice === true && this.ctype === 'select' && arguments.length >= 2) {
        this.noticeStr = arguments[1];
      }
      if (this.ctype === 'num' && arguments.length > 0) {
        let val = arguments[0];
        val && typeof val === 'string' && val !== '-' && this.dispatch('YuXform', 'el.form.item.blur', [this.name, Number(val.replace(/[^\.-\d]/g, ''))]);
      }
      this.validate('change');
    },
    onFieldChangeData() {
      if (this.notice === true && this.ctype === 'select' && arguments.length >= 2) {
        this.noticeStr = arguments[1];
      }
    },
    btnUTraceListFn(event) {
      this.$emit('utrace-click', this._props);
      this.dispatch('YuXform', 'utrace-click', this._props);
    }
  },
  mounted() {
    if (this.name) {
      this.dispatch('YuXform', 'el.form.addField', [this]);
      let initialValue = this.fieldValue;
      if (Array.isArray(initialValue)) {
        initialValue = [].concat(initialValue);
      }
      Object.defineProperty(this, 'initialValue', {
        value: initialValue
      });

      let rules = this.getRules();

      if (rules.length || this._props.hasOwnProperty('required')) {
        this.$on('el.form.blur', this.onFieldBlur);
        this.$on('el.form.change', this.onFieldChange);
      }
    } else {
      this.$on('el.form.change', this.onFieldChangeData);
    }
    // if (!this.notice && this.name) {
    //   this.noticeStr = (this.form.formdata || this.$parent.formdata)[this.name];
    // }
  },
  beforeDestroy() {
    this.form.ifInit = false;
    this.dispatch('YuXform', 'el.form.removeField', [this]);
  }
};
</script>
