import ElCheckbox from '@/packages/checkbox';
import ElRadio from '@/packages/radio';
import ElTag from '@/packages/tag';
import objectAssign from '@/src/utils/merge';
import YuXformItemPart from '@/packages/xform/src/form-item-part';
import { getValueByPath } from '@/src/utils/util';
import { formatters } from '../../../src/utils/formatter.js';
import deepmerge from 'deepmerge';
import Locale from '@/src/mixins/locale';

let columnIdSeed = 1;
const defaults = {
  default: {
    order: ''
  },
  edit: {},
  selection: {
    width: 48,
    minWidth: 48,
    realWidth: 48,
    order: '',
    className: 'el-table-column--selection'
  },
  single: {
    width: 48,
    minWidth: 48,
    realWidth: 48,
    order: '',
    className: 'el-table-column--single'
  },
  expand: {
    width: 48,
    minWidth: 48,
    realWidth: 48,
    order: ''
  },
  index: {
    width: 48,
    minWidth: 48,
    realWidth: 48,
    order: ''
  }
};

const forced = {
  selection: {
    renderHeader: function(h) {
      return <el-checkbox
        nativeOn-click={ this.toggleAllSelection }
        value={ this.isAllSelected } />;
    },
    renderCell: function(h, { row, column, store, $index }) {
      return <el-checkbox
        value={ store.isSelected(row) }
        disabled={ column.selectable ? !column.selectable.call(null, row, $index) : false }
        on-input={ () => { store.commit('rowSelectedChanged', row); } } />;
    },
    sortable: false,
    resizable: false
  },
  single: {
    renderHeader: function(h, { column }) {
      return column.label || '';
    },
    renderCell: function(h, { row, column, store, $index }) {
      return <el-radio
        class='hide-label'
        value={store.states.check}
        label={ $index }
        disabled={ column.selectable ? !column.selectable.call(null, row, $index) : false }
        on-input={ () => { store.commit('setCurrentRow', row); } } />;
    },
    sortable: false,
    resizable: false
  },
  edit: {
    renderHeader: function(h, { column }) {
      return column.label || '';
    },
    renderCell: function(h, { row, column, cellIndex, store, _self }) {
      var item = {};
      var _this = _self;
      // yu-num 的multiple 和column的冲突了，boolean的时候删除这个属性
      if (column.ctype === 'yu-num' && typeof (column.multiple) === 'boolean') {
        delete column.multiple;
      }
      // 绑定相关属性，事件
      item = deepmerge(item, this.$parent.tableColumnMap[cellIndex].data.attrs, { clone: true });
      const selfData = { attrs: item};
      var listeners = this.$parent.tableColumnMap[cellIndex].componentOptions.listeners;
      if (listeners && listeners.input) {
        delete listeners.input;
      }
      selfData.on = listeners;
      return <YuXformItemPart
        {... selfData}
        value={row[column.property]}
        multiple={column.multiple}
        disabled={column.disabled}
        options={column.options}
        props={column.props}
        data-code={column.dataCode}
        on-input={ ($$v) => { if (_this.tabledata.indexOf(row) === _this.tabledata.indexOf(_this.$refs.table.store.states.currentRow)) { _this.saveEditUTraceData(store, row, column, row[column.property], $$v);_this.$set(row, column.property, $$v);} }}
        ctype={column.ctype}>
      </YuXformItemPart>;
    }
  },
  index: {
    renderHeader: function(h, { column }) {
      return column.label || '#';
    },
    renderCell: function(h, { $index }) {
      return <div>{ $index + 1 }</div>;
    },
    sortable: false
  },
  expand: {
    renderHeader: function(h, { column }) {
      return column.label || '';
    },
    renderCell: function(h, { row, store }, proxy) {
      const expanded = store.states.expandRows.indexOf(row) > -1;
      return <div class={ 'el-table__expand-icon ' + (expanded ? 'el-table__expand-icon--expanded' : '') }
        on-click={ () => proxy.handleExpandClick(row) }>
        <i class='el-icon el-icon-arrow-right'></i>
      </div>;
    },
    sortable: false,
    resizable: false,
    className: 'el-table__expand-column'
  }
};

const getDefaultColumn = function(type, options) {
  const column = {};
  objectAssign(column, defaults[type || 'default']);

  for (let name in options) {
    if (options.hasOwnProperty(name)) {
      const value = options[name];
      if (typeof value !== 'undefined') {
        column[name] = value;
      }
    }
  }

  if (!column.minWidth) {
    column.minWidth = 80;
  }

  column.realWidth = column.width || column.minWidth;
  return column;
};

const DEFAULT_RENDER_CELL = function(h, {row, column, store}) {
  const property = column.property;
  const value = property && property.indexOf('.') === -1
    ? row[property]
    : getValueByPath(row, property);
  if (column && column.formatter) {
    let dt = column.formatter(row, column, value);
    // 当配置数据字典（dataCode），且翻译后的值和翻译前的值相等时，表示列数据翻译失败。然后将列信息添加到yufp.lookup.unRegArray json对象中，key为表格序列号tableSequence
    if (column.dataCode && arrayToString(dt) === arrayToString(value)) {
      if (yufp.lookup.unRegArray) {
        let tb = yufp.lookup.unRegArray[column.tableSequence];
        if (tb) {
          // 列信息是否已经存在于tb.lookupArray 中
          var inFlag = false;
          for (var i = 0; i < tb.lookupArray.length; i++) {
            var element = tb.lookupArray[i];
            if (element.columnId === column.id) {
              inFlag = true;
              break;
            }
          }
          // 列信息不存在则添加到lookupArray 中
          if (!inFlag) {
            tb.lookupArray.push({columnId: column.id, dataCode: column.dataCode});
          }
        } else {
          // 添加表格信息tableSequence 序列号，lookupArray 列信息对象， time 添加日期流水
          yufp.lookup.unRegArray[column.tableSequence] = {tableSequence: column.tableSequence, lookupArray: [{columnId: column.id, dataCode: column.dataCode, separator: column.separator}], time: new Date().getTime()};
          yufp.lookup.unRegArray[column.tableSequence]['fn'] = (function(table) {
            return function(columnObj) {
              table && table.$refs && table.$refs.refTableBody && table.$refs.refTableBody.forceUpdate(columnObj);
            };
          }(store.table));
        }
      }
    }
    return dt;
  }
  return value;
};
/**
 * 如果值数组就返回对应的字符串，以便后续比较
 * @param {} val
 */
const arrayToString = function(val) {
  if (val instanceof Array) {
    return val.toString();
  } else {
    return val;
  }
};

export default {
  name: 'YuXtableColumn',
  xtype: 'YuXtableColumn',
  mixins: [Locale],
  props: {
    multiple: Boolean,
    disabled: Boolean,
    // 可编辑变格表单控件类型
    ctype: String,
    // 可编辑表格下拉框，单选框，多选框静态数据
    options: Array,
    dataCode: String,
    type: {
      type: String,
      default: 'default'
    },
    label: String,
    className: String,
    labelClassName: String,
    property: String,
    prop: String,
    width: {},
    minWidth: {},
    renderHeader: Function,
    sortable: {
      type: [String, Boolean],
      default: false
    },
    sortMethod: Function,
    resizable: {
      type: Boolean,
      default: true
    },
    context: {},
    columnKey: String,
    align: String,
    headerAlign: String,
    showTooltipWhenOverflow: Boolean,
    showOverflowTooltip: Boolean,
    fixed: [Boolean, String],
    formatter: Function,
    selectable: Function,
    reserveSelection: Boolean,
    filterMethod: Function,
    filteredValue: Array,
    filters: Array,
    filterPlacement: String,
    filterMultiple: {
      type: Boolean,
      default: true
    },
    rules: [Object, Array, String],
    required: Boolean,
    utrace: {
      type: Boolean,
      default: true
    },
    separator: {
      type: String,
      default: ','
    },
    // 表格转码为options 并且配置props时
    props: Object,
    // 是否开启隐藏列
    hideColumn: Boolean
  },

  data() {
    return {
      isSubColumn: false,
      columns: [],
      utraceMessage: '',
      varHideColumn: this.hideColumn
    };
  },

  beforeCreate() {
    this.row = {};
    this.column = {};
    this.$index = 0;
  },

  components: {
    ElCheckbox,
    ElTag,
    ElRadio,
    YuXformItemPart
  },

  computed: {
    owner() {
      let parent = this.$parent;
      while (parent && !parent.tableId) {
        parent = parent.$parent;
      }
      return parent;
    }
  },

  created() {
    this.customRender = this.$options.render;
    this.$options.render = h => h('div', this.$slots.default);
    this.columnId = (this.$parent.tableId || (this.$parent.columnId + '_')) + 'column_' + columnIdSeed++;

    let parent = this.$parent;
    let owner = this.owner;
    this.isSubColumn = owner !== parent;

    let type = this.type;

    let width = this.width;
    if (width !== undefined) {
      width = parseInt(width, 10);
      if (isNaN(width)) {
        width = null;
      }
    }

    let minWidth = this.minWidth;
    if (minWidth !== undefined) {
      minWidth = parseInt(minWidth, 10);
      if (isNaN(minWidth)) {
        minWidth = 80;
      }
    }

    let isColumnGroup = false;
    var formatter;
    if (this.formatter) {
      formatter = this.formatter;
    } else if (this.ctype === 'timeselect' || this.ctype === 'timepicker' || this.ctype === 'datepicker') {
      formatter = formatters['dateFormatter'];
    } else if (this.options || this.dataCode) {
      formatter = formatters['keytoValue'];
    }
    // 对指定的主键不进行小U监听
    var isUtrace = parent.ukeyField === (this.prop || this.property) ? false : this.utrace;

    let column = getDefaultColumn(type, {
      tableSequence: owner.store.table.sequenceId,
      id: this.columnId,
      separator: this.separator,
      columnKey: this.columnKey,
      label: this.label,
      className: this.className,
      labelClassName: this.labelClassName,
      property: this.prop || this.property,
      type,
      multiple: this.multiple,
      disabled: this.disabled,
      options: this.options,
      props: this.props,
      ctype: this.ctype,
      dataCode: this.dataCode,
      defaultRender: DEFAULT_RENDER_CELL,
      renderCell: null,
      editRenderCell: forced['edit'],
      renderHeader: this.renderHeader,
      minWidth,
      width,
      isColumnGroup,
      context: this.context,
      align: this.align ? 'is-' + this.align : null,
      headerAlign: this.headerAlign ? 'is-' + this.headerAlign : (this.align ? 'is-' + this.align : null),
      sortable: this.sortable === '' ? true : this.sortable,
      sortMethod: this.sortMethod,
      resizable: this.resizable,
      showOverflowTooltip: this.showOverflowTooltip || this.showTooltipWhenOverflow,
      formatter: formatter,
      selectable: this.selectable,
      reserveSelection: this.reserveSelection,
      fixed: this.fixed === '' ? true : this.fixed,
      filterMethod: this.filterMethod,
      filters: this.filters,
      filterable: this.filters || this.filterMethod,
      filterMultiple: this.filterMultiple,
      filterOpened: false,
      filteredValue: this.filteredValue || [],
      filterPlacement: this.filterPlacement || '',
      rules: this.rules,
      utrace: isUtrace,
      required: this.required,
      hideColumn: this.varHideColumn
    });
    objectAssign(column, forced[type] || {});
    // 传入的fixed 值为 'left' 或者 'right' 以外的其他字符串时，设置 fixed 的值为 false
    if (typeof column.fixed === 'string' && !(column.fixed === 'left') && !(column.fixed === 'right')) {
      column.fixed = false;
      console.error(this.t('el.table.fixedError'));
    }
    this.columnConfig = column;
    let renderCell = column.renderCell;
    let _self = this;
    if (type === 'expand') {
      owner.renderExpanded = function(h, data) {
        return _self.$scopedSlots.default
          ? _self.$scopedSlots.default(data)
          : _self.$slots.default;
      };

      column.renderCell = function(h, data) {
        return <div class="cell">{ renderCell(h, data, this._renderProxy) }</div>;
      };

      return;
    }
    column.renderCell = function(h, data) {
      // 未来版本移除
      if (_self.$vnode.data.inlineTemplate) {
        renderCell = function() {
          data._self = _self.context || data._self;
          if (Object.prototype.toString.call(data._self) === '[object Object]') {
            for (let prop in data._self) {
              if (!data.hasOwnProperty(prop)) {
                data[prop] = data._self[prop];
              }
            }
          }
          // 静态内容会缓存到 _staticTrees 内，不改的话获取的静态数据就不是内部 context
          data._staticTrees = _self._staticTrees;
          data.$options.staticRenderFns = _self.$options.staticRenderFns;
          return _self.customRender.call(data);
        };
      } else if (_self.$scopedSlots.default) {
        renderCell = () => _self.$scopedSlots.default(data);
      }

      if (!renderCell) {
        renderCell = DEFAULT_RENDER_CELL;
      }

      return _self.showOverflowTooltip || _self.showTooltipWhenOverflow
        ? <div class="cell el-tooltip" style={'width:' + (data.column.realWidth || data.column.width) + 'px'}>{ renderCell(h, data) }</div>
        : <div class="cell">{ renderCell(h, data) }{_self.$parent.$parent.utrace ? _self._showUTraceInfo(h, data) : ''}</div>;
    };
  },

  destroyed() {
    // this.$parent.tableColumnMap = null;
    if (!this.$parent) return;
    const parent = this.$parent;
    // 表格设置了hideColumn 为true 才删除vnodeColumn 中数据
    if (this.owner.store.table.showHiddenMenu === true) {
      this.owner.store.states.vnodeColumn.splice(this.owner.store.states.vnodeColumn.indexOf(this), 1);
    }
    this.owner.store.commit('removeColumn', this.columnConfig, this.isSubColumn ? parent.columnConfig : null);
  },

  watch: {
    options(newVal) {
      if (this.columnConfig) {
        this.columnConfig.options = newVal;
      }
    },
    dataCode(newVal) {
      if (this.columnConfig) {
        this.columnConfig.dataCode = newVal;
      }
    },
    ctype(newVal) {
      if (this.columnConfig) {
        this.columnConfig.ctype = newVal;
      }
    },
    multiple(newVal) {
      if (this.columnConfig) {
        this.columnConfig.multiple = newVal;
      }
    },
    disabled(newVal) {
      if (this.columnConfig) {
        this.columnConfig.disabled = newVal;
      }
    },
    label(newVal) {
      if (this.columnConfig) {
        this.columnConfig.label = newVal;
      }
    },
    prop(newVal) {
      if (this.columnConfig) {
        this.columnConfig.property = newVal;
      }
    },
    property(newVal) {
      if (this.columnConfig) {
        this.columnConfig.property = newVal;
      }
    },
    filters(newVal) {
      if (this.columnConfig) {
        this.columnConfig.filters = newVal;
      }
    },
    filterMultiple(newVal) {
      if (this.columnConfig) {
        this.columnConfig.filterMultiple = newVal;
      }
    },
    align(newVal) {
      if (this.columnConfig) {
        this.columnConfig.align = newVal ? 'is-' + newVal : null;

        if (!this.headerAlign) {
          this.columnConfig.headerAlign = newVal ? 'is-' + newVal : null;
        }
      }
    },
    headerAlign(newVal) {
      if (this.columnConfig) {
        this.columnConfig.headerAlign = 'is-' + (newVal ? newVal : this.align);
      }
    },
    width(newVal) {
      if (this.columnConfig) {
        this.columnConfig.width = newVal;
        this.owner.store.scheduleLayout();
      }
    },
    minWidth(newVal) {
      if (this.columnConfig) {
        this.columnConfig.minWidth = newVal;
        this.owner.store.scheduleLayout();
      }
    },
    fixed(newVal) {
      if (this.columnConfig) {
        this.columnConfig.fixed = newVal;
        this.owner.store.scheduleLayout();
      }
    },
    sortable(newVal) {
      if (this.columnConfig) {
        this.columnConfig.sortable = newVal;
      }
    },
    utrace(newVal) {
      this.utrace = newVal;
    },
    rules(newVal) {
      if (this.columnConfig) {
        this.columnConfig.rules = newVal;
        this.owner.store.commit('updateColumnsRules', this.columnConfig);
      }
    },
    /**
     * watch 列隐藏变量状态，然后同步更新表格布局
     */
    varHideColumn: function(newVal) {
      if (this.owner.store.table.showHiddenMenu === true) {
        this.columnConfig.hideColumn = newVal;
        this.owner.store.commit('updateColumns', this.columnConfig);
        // 更新父节点状态
        this.setParentNodeStatus();
      }
    }
  },

  mounted() {
    // 针对多级表头，递归获取最后一级表头
    var getColumn = function(tableColumn) {
      var columnData = tableColumn.filter(function(item) {
        return item.tag && item.tag.indexOf('YuXtableColumn') > -1;
      });
      for (var i = 0, l = columnData.length; i < l; i++) {
        if (!columnData[i].componentOptions.children) {
          column.push(columnData[i]);
        } else {
          getColumn(columnData[i].componentOptions.children);
        }
      }
      return column;
    };
    var children = [];
    var column = [];
    const owner = this.owner;
    const parent = this.$parent;
    children = parent.$vnode.componentOptions.children;
    parent.tableColumnMap = getColumn(children);
    let columnIndex;
    if (!this.isSubColumn) {
      columnIndex = [].indexOf.call(parent.$refs.hiddenColumns.children, this.$el);
    } else {
      columnIndex = [].indexOf.call(parent.$el.children, this.$el);
    }
    // 表格配置了hideColumn 为true 才保存vnodeColumn
    if (owner.store.table.showHiddenMenu === true) {
      owner.store.states.vnodeColumn.push(this);
    }
    owner.store.commit('insertColumn', this.columnConfig, columnIndex, this.isSubColumn ? parent.columnConfig : null);
  },

  methods: {
    _showUTraceInfo(h, data) {
      let _this = this;
      let messages = data && data.row && data.row.uTraceMessages;
      let fields = data && data.row && data.row.uTraceFidlds;
      let kv = data && data.row && _this.$parent.$parent.ukeyField && data.row[_this.$parent.$parent.ukeyField];
      return _this.resetColUTraceInfo(messages, fields, kv);
    },
    resetColUTraceInfo(messages, fields, keyvalue) {
      let _this = this;
      if (_this.utrace && messages && messages.length > 0 && fields && fields.length > 0) {
        var idx = fields.indexOf(_this.prop || _this.property);
        if (idx > -1) {
          var message = _this.$parent.$parent.showUtitleMessage ? messages[idx] : '';
          var targetProps = {
            on: {
              click: _this.btnUTraceListFn
            }
          };
          return (<span class="el-utrace" {...targetProps} ukv={keyvalue || ''} title={message || ''}></span>);
        }
      }
      return '';
    },
    btnUTraceListFn(event) {
      this.$parent.$parent.showUTraceListDialog(this.prop || this.property, event.target.getAttribute('ukv'));
      event.stopPropagation();
    },
    // 如果当前节点的父节点下元素都是隐藏的则隐藏父节点
    setParentNodeStatus: function() {
      if (this.$parent.$vnode.tag.indexOf('ElTable') > -1) {
        return;
      } else if (this.$parent.$vnode.tag.indexOf('YuXtableColumn') > -1) {
        var hiddenFlag = true;
        for (var i = 0; i < this.$parent.$children.length; i++) {
          var element = this.$parent.$children[i];
          // 只要有一个子元素的hidden 为false 则不隐藏这个列
          if (element.varHideColumn === false) {
            hiddenFlag = false;
          }
        }
        // 全部隐藏时
        if (hiddenFlag === true) {
          this.$parent.varHideColumn = true;
        } else {
          this.$parent.varHideColumn = false;
        }
      }
    }
  }
};
