<template>
  <span class="el-breadcrumb__item">
    <span class="el-breadcrumb__item__inner" @click="handleClick"><slot></slot></span><span class="el-breadcrumb__separator">{{separator}}</span>
  </span>
</template>
<script>
import Locale from '@/src/mixins/locale';

export default {
  name: 'ElBreadcrumbItem',
  xtype: 'YuBreadcrumbItem',
  mixins: [Locale],
  props: {
    to: {},
    replace: Boolean
  },
  data() {
    return {
      separator: ''
    };
  },
  methods: {
    handleClick: function(e) {
      this.$emit('click', e);
      if (!yufp.router) {
        console.log(this.t('el.breadcrumb.noYufpRouter'));
        return;
      }
      if (this.to) {
        if (typeof this.to === 'string') {
          yufp.router.to(this.to);
        } else {
          let routerId = this.to.routerId;
          let data = this.to.data;
          let rootId = this.to.rootId;
          yufp.router.to(routerId, data, rootId);
        }
      }
    }
  },
  mounted() {
    this.separator = this.$parent.separator;
  }
};
</script>
